import {emitter} from "./emitter";
import {header} from "../header";
import {gsap} from "gsap";
import { ScrollToPlugin } from "gsap/ScrollToPlugin";
import {BP_S, COLOR_MODES, COLOR_MODES_BG} from "./constants";
import {
    EVENT_SCROLL,
    EVENT_SCROLL_TO,
    EVENT_HEADER_COLOR,
    EVENT_RESIZE,
    Window
} from "./window";
import {clamp} from "./utils";
//@ts-ignore
import {SplitText} from "../gsap-bonus/SplitText";

export class Page {
    public element: HTMLElement;
    public sectionsWrapper: HTMLElement;
    public imagesToLoad: Array<HTMLImageElement>;
    private home: boolean;
    private sections: NodeListOf<HTMLElement>;
    private sectionAnchors: NodeListOf<HTMLElement>;
    private parallaxMedias: NodeListOf<HTMLElement>;
    private parallaxSections: NodeListOf<HTMLElement>;
    private ctaButtons: NodeListOf<HTMLElement>;
    private requestId: number|null;
    private headerColorDelay: number;
    private transitionSpeed: number = .5;
    private parallaxDistance: number = 0;
    private parallaxBind: FrameRequestCallback;
    private videos: NodeListOf<HTMLVideoElement>;
    private observerVideos: IntersectionObserver;
    private observerSections: IntersectionObserver;
    private anchoredLinks: NodeListOf<HTMLElement>;

    constructor(element: HTMLElement, home: boolean = false) {
        this.element = element;
        this.home = home;

        this.sectionsWrapper = document.querySelector('.sections');
        this.parallaxMedias = this.element.querySelectorAll('.parallax--media');
        this.parallaxSections = this.element.querySelectorAll('.parallax-section');
        this.ctaButtons = this.element.querySelectorAll('button.cta');
        this.videos = this.element.querySelectorAll('video[playsinline]');
        this.anchoredLinks = this.element.querySelectorAll('a[href^="./#"]');

        this.parallaxBind = this.parallax.bind(this);
        this.init();
    }

    init() {
        if (!this.home) {
            this.sections = this.element.querySelectorAll('section');
            this.sectionAnchors = this.element.querySelectorAll('section[data-anchor]');
            this.imagesToLoad = Array.from(this.element.querySelectorAll('img[data-src]'));

            emitter.on(EVENT_SCROLL, this.onScrollHandler);
            emitter.on(EVENT_RESIZE, this.onScrollHandler);
            emitter.on(EVENT_RESIZE, this.onResizeHandler);
            emitter.on(EVENT_SCROLL_TO, this.onScrollToHandler);

            this.headerColorDelay = this.transitionSpeed;
            this.onScrollHandler();
            this.onResizeHandler();
            this.headerColorDelay = 0;

            header.animateIn();
            header.hideZoom();

            this.parallaxSections.forEach((e, i)=>{
                (e.querySelector('.section-wrapper') as HTMLElement).style.zIndex = `${i + 1}`;
            });

            for (const item of this.anchoredLinks) {
                item.addEventListener('click', this.onAnchoredLinkClick.bind(this));
            }

            this.checkHash();
            gsap.registerPlugin(ScrollToPlugin);
            this.addListeners();
        } else {
            this.sectionsWrapper.classList.remove(...COLOR_MODES_BG);
            emitter.emit(EVENT_HEADER_COLOR, {mode: 'mode-transparent-dark'});
            window.scroll(0, 0);
            header.resetToHome();
        }
    }

    onAnchoredLinkClick(e: any) {
        e.preventDefault();
        const target = e.target.getAttribute('href').split('#')[1];

        this.onScrollToHandler({anchor: target});
    }

    checkHash() {
        const anchor = window.location.hash ? window.location.hash.split('#')[1] : '';
        const scrollEl = anchor ? this.element.querySelector(`[data-anchor=${anchor}]`) : '';
        const offset = 5;
        let headerHeight: number;
        let top: number;

        if (anchor && scrollEl) {
            headerHeight = header.element.getBoundingClientRect().height;
            top = (scrollEl.getBoundingClientRect().top + window.scrollY - headerHeight) + offset;
            window.scroll(0, top);
        } else {
            window.scroll(0, 0);
        }
    }

    animateIn(speed = this.transitionSpeed) {
        return new Promise((resolve) => {
            gsap.to(this.element, {
                duration: this.transitionSpeed,
                opacity: 1,
                onComplete: () => {
                    this.introAnimateIn(this.element, resolve);
                    this.elementsAnimateIn();
                }
            });
        });
    }

    introAnimateIn(el: HTMLElement, resolve: any) {
        const title = el.querySelector('.intro .title') as HTMLElement;

        const tl = gsap.timeline(),
            mySplitText = new SplitText(title, {type:"words,chars"}),
            chars = mySplitText.chars; //an array of all the divs that wrap each character

        title.style.opacity = '1';

        gsap.set(title, {perspective: 400});

        tl.from(chars, {
            duration: .8,
            opacity: 0,
            y: 80,
            ease: "Power4.easeOut",
            stagger: 0.01, onComplete: () => {
                resolve();
            }}, "+=0");
    }

    elementsAnimateIn() {
        if (this.home) return false;

        const copy = this.element.querySelector('.intro .copy');
        const cta = this.element.querySelector('.intro .top-header .cta-container');
        const media = this.element.querySelector('.intro .media');

        gsap.to([copy, cta, media], {duration: .5, delay: 1, opacity: 1});
    }

    animateOut() {
        return new Promise(resolve => {
            header.resetBackground();
            gsap.to(this.element, {
                duration: this.transitionSpeed,
                opacity: 0,
                onComplete: () => {
                    this.destroy();
                    resolve();
                }
            });
        });
    }

    onScrollToHandler = ({anchor}: any, duration: number = 1) => {
        const scrollEl = this.element.querySelector(`[data-anchor=${anchor}]`);
        const headerHeight = header.element.getBoundingClientRect().height;
        const offset = 5;
        const top = (scrollEl.getBoundingClientRect().top + window.scrollY - headerHeight) + offset;

        gsap.to(window, {duration, scrollTo: { y: top }, ease: 'Power4.easeInOut'});
    }

    sectionColorCheck() {
        const headerHeight = header.element.getBoundingClientRect().height;
        let currColor = 'mode-light';

        // Toggle header color
        this.sections.forEach((el)=> {
            let rect = el.getBoundingClientRect();

            if (rect.top <= headerHeight && rect.bottom >= 0 && rect.height > 0) {
                COLOR_MODES.map((color) => {
                    currColor = el.classList.contains(color) ? color : currColor;
                });
                emitter.emit(EVENT_HEADER_COLOR, {mode: currColor, delay: this.headerColorDelay});
            }
        });

        if (!this.sectionsWrapper.classList.contains(`${currColor}--bg`)) {
            this.sectionsWrapper.classList.remove(...COLOR_MODES_BG);
            this.sectionsWrapper.classList.add(`${currColor}--bg`);
        }
    }

    onScrollHandler = (init: boolean = false) => {
        this.sectionColorCheck();

        if (this.requestId) return;
            this.requestId = window.requestAnimationFrame(this.parallaxBind);
    }

    parallax() {
        let rect, innerRect;
        let innerEl: HTMLElement;
        let top, newLimit, perc, diff: number;
        let limit: any;

        // Adjust to change height of scroll area
        limit = Window.height * 2;

        this.parallaxMedias.forEach((el)=> {
            rect = el.getBoundingClientRect();
            innerEl = el.querySelector('.ratio > *') as HTMLElement;
            innerRect = innerEl.getBoundingClientRect();

            top = rect.top - Window.height;
            newLimit = -Math.abs(limit);
            perc = clamp(top / newLimit, 0, 1);
            diff = innerRect.height - rect.height;

            innerEl.style.transform = `translateY(-${diff * perc}px)`
        });

        this.parallaxSections.forEach((el) => {
            rect = el.getBoundingClientRect();
            innerEl = el.querySelector('.section-wrapper') as HTMLElement;

            top = rect.top - Window.height;
            perc =  clamp(top / (Window.height), -1, 0);

            innerEl.style.transform = `translateY(${this.parallaxDistance * perc}px)`;
        });

        this.requestId = null;
    }

    addListeners = () => {
        // Reset check for section height to parallax.
        document.addEventListener('lazybeforeunveil', () => {
            this.onResizeHandler();
        });

        // Video playback.
        this.observerVideos = new IntersectionObserver(
            (entries) => {
                for (let entry of entries) {
                    this.onIntersectVideo(entry)
                }
            },
            {threshold: [0] }
        );

        this.observerSections = new IntersectionObserver(
            (entries) => {
                for (let entry of entries) {
                    const boundingRect = entry.boundingClientRect;
                    const section = entry.target as HTMLElement;

                    if (boundingRect.top <= 0 || !section.hasAttribute('data-anchor'))
                        return false

                    if (entry.isIntersecting) {
                        this.onIntersectSection(section);
                    } else if (entry.target.previousElementSibling) {
                        this.onIntersectSection(section.previousElementSibling);
                    }
                }
            },
            { rootMargin: '0px 0px -50% 0px' }
        );

        this.videos.forEach((el) => { this.observerVideos.observe(el) });
        this.sectionAnchors.forEach((el) => { this.observerSections.observe(el) });
    }

    onIntersectVideo( e: any ) {
        const video = e.target as HTMLVideoElement;
        if (e.isIntersecting && video.paused) {
            video.play();
        } else if (!e.isIntersecting && !video.paused) {
            video.pause();
        }
    }

    onIntersectSection( t: any ) {
        const section = t as HTMLElement;
        const anchor = section.dataset.anchor;

        if (anchor) {
            window.location.hash = anchor;
            header.setActiveSubNavItem(anchor)
        }
    }

    removeListeners = () => {
        document.removeEventListener('lazybeforeunveil', () => {
            this.onResizeHandler();
        });

        this.videos.forEach((el) => { this.observerVideos.unobserve(el) })
        this.sectionAnchors.forEach((el) => { this.observerSections.unobserve(el) })
    }

    onResizeHandler = () => {
        let innerRect;
        let innerEl: HTMLElement;

        this.parallaxDistance = Window.width < BP_S ? 50 : 100;

        this.parallaxSections.forEach((el) => {
            innerEl = el.querySelector('.section-wrapper') as HTMLElement;
            innerRect = innerEl.getBoundingClientRect();
            el.style.height = `${innerRect.height - this.parallaxDistance}px`
        })
    }

    destroy() {
        emitter.off(EVENT_SCROLL, this.onScrollHandler);
        emitter.off(EVENT_RESIZE, this.onScrollHandler);
        emitter.off(EVENT_RESIZE, this.onResizeHandler);
        emitter.off(EVENT_SCROLL_TO, this.onScrollToHandler);
        this.removeListeners();
    }
}
