import {gsap} from 'gsap';

export default class ecosystemAvatars {
  element: HTMLElement;
  avatars: HTMLElement[];
  cols: HTMLElement[];
  grid: [number, number];
  animating: boolean;

  constructor(parent: HTMLElement) {
    this.element = parent;
    this.grid = [8, 30];
    this.avatars = [...parent.querySelectorAll('.row img')] as HTMLElement[];
    this.cols = [...parent.querySelectorAll('.col')] as HTMLElement[];
    gsap.set(this.cols, {opacity: 0, y: 10});

    this.addListeners();
  }

  animateIn(delay = 0) {
    if (this.animating) return;
    this.animating = true;

    gsap.to(this.cols, {duration: 0.5, delay: delay, opacity: 1, y: 0, stagger: 0.1});
  }

  addListeners() {
    let observer = new IntersectionObserver((entries, observer) => {
      for (let entry of entries) {
        if (entry.isIntersecting) {
          const className = entry.target.className;
          if (className === 'stats-wrapper') {
            this.animateIn();
          }
        }
      }
    });

    observer.observe(this.element.querySelector('.stats-wrapper'));
  }

  destroy() {

  }
}
