/*
UI controls for 3d experience
 */

import {gsap} from "gsap";
import Emitter from "@hellomonday/emitter";
import {timer} from "./hero3d";
import {audioController, MUSIC_MUTE_CHANGE, SFX_MUTE_CHANGE} from "../global/audioController";

export const EVENT_PLAY = 'event_play';
export const EVENT_PAUSE = 'event_pause';
export const EVENT_CHAPTER_SWITCH = "event_chapter_switch";

export class Controls extends Emitter {

    public element: HTMLElement;
    private progressBar: HTMLElement;
    private timerRing: SVGElement;
    private timerRingCircle: SVGElement;
    private playButton: HTMLElement;
    private chapterMenu: HTMLElement;
    private chapterMenuItems: NodeList;
    private playIcon: HTMLElement;
    private pauseIcon: HTMLElement;
    private nextChapterButton: HTMLElement;
    private previousChapterButton: HTMLElement;
    private sfxToggle: HTMLElement;
    private musicToggle: HTMLElement;
    private timerTween: any;
    private progressBarTween: any;
    private paused: boolean = true;
    private strokeDashArrayLength: number;

    constructor(element: HTMLElement) {
        super();
        this.element = element;
        this.progressBar = this.element.querySelector('.progress-bar');
        this.timerRing = this.element.querySelector('.timer-ring');
        this.timerRingCircle = this.element.querySelector('.timer-ring circle');
        this.playButton = this.element.querySelector('.play-pause');
        this.playIcon = this.playButton.querySelector('.icon-play');
        this.pauseIcon = this.playButton.querySelector('.icon-pause');
        this.chapterMenu = this.element.querySelector('.chapter-menu');
        this.chapterMenuItems = this.element.querySelectorAll('.chapter-menu-item');
        this.previousChapterButton = this.element.querySelector('.chapter-arrows .previous');
        this.nextChapterButton = this.element.querySelector('.chapter-arrows .next');
        this.sfxToggle = this.element.querySelector('.sfx-toggle');
        this.musicToggle = this.element.querySelector('.music-toggle');

        this.chapterMenuItems.forEach((chapterItem) => {
            (chapterItem as HTMLElement).addEventListener('click', this.chapterMenuClick);
        });
        this.playButton.addEventListener('click', this.playButtonHandler);
        this.previousChapterButton.addEventListener('click', this.previousChapterButtonHandler);
        this.nextChapterButton.addEventListener('click', this.nextChapterButtonHandler);
        this.sfxToggle.addEventListener('click', this.sfxToggleHandler);
        this.musicToggle.addEventListener('click', this.musicToggleHandler);

        audioController.on(MUSIC_MUTE_CHANGE, this.audioControllerSoundToggleHandler);
        audioController.on(SFX_MUTE_CHANGE, this.audioControllerSoundToggleHandler);

        const r = parseInt(this.timerRingCircle.getAttribute('r'));
        this.strokeDashArrayLength = 2 * Math.PI * r;
        gsap.set(this.timerRing, {strokeDasharray: this.strokeDashArrayLength});
    }

    set progress(value: number) {
        gsap.set(this.progressBar, {width: (value * 100) + '%'});
    }

    public startTimer(duration: number) {
        gsap.killTweensOf(this.timerRing);
        gsap.set(this.timerRing, {strokeDashoffset: this.strokeDashArrayLength});
        this.timerTween = gsap.to(this.timerRing, duration, {strokeDashoffset: 0, ease: 'none'});
    }

    public animateProgressFromTo(from: number, to: number, speed: number) {
        this.progress = from;
        this.animateProgressTo(to, speed);
    }

    public animateProgressTo(to: number, speed: number) {
        gsap.killTweensOf(this.progressBar);
        this.progressBarTween = gsap.to(this.progressBar, speed, {width: (to * 100) + '%', ease: 'none'});
    }

    public show(delay: number = 0) {
        this.element.style.display = 'block';
        gsap.set(this.element, {opacity: 0});
        gsap.to(this.element, .5, {opacity: 1, delay: delay});
    }

    public hide() {
        this.element.style.display = 'none';
    }

    public resume() {
        if (!this.paused) {
            return;
        }
        this.playIcon.style.display = 'none';
        this.pauseIcon.style.display = 'block';
        this.paused = false;
        this.timerTween.play();
        this.progressBarTween.play();
    }

    public pause() {
        if (this.paused) {
            return;
        }
        this.playIcon.style.display = 'block';
        this.pauseIcon.style.display = 'none';
        this.paused = true;
        this.timerTween.pause();
        this.progressBarTween.pause();
    }

    private playButtonHandler = (event: MouseEvent) => {
        if (this.paused) {
            this.emit(EVENT_PLAY, {});
        } else {
            this.emit(EVENT_PAUSE, {});
        }
    }

    public updateChapterMenu(chapterIndex: number, actIndex: number) {
        this.chapterMenuItems.forEach((chapterItem, index) => {
            const chapterItemEl = chapterItem as HTMLElement;
            if (index + 1 != chapterIndex) {
                chapterItemEl.classList.remove('active');
            } else {
                chapterItemEl.classList.add('active');
            }
        });
        this.previousChapterButton.classList.remove('disabled');
        this.nextChapterButton.classList.remove('disabled');
        if (chapterIndex == 1) {
            this.previousChapterButton.classList.add('disabled');
        } else if (chapterIndex == 4) {
            this.nextChapterButton.classList.add('disabled');
        }
    }

    private chapterMenuClick = (event: MouseEvent) => {
        const el = event.currentTarget as HTMLElement;
        const index = Array.from(el.parentNode.children).indexOf(el) + 1;
        this.emit(EVENT_CHAPTER_SWITCH, {chapterIndex: index});
    }

    private previousChapterButtonHandler = (event: MouseEvent) => {
        this.emit(EVENT_CHAPTER_SWITCH, {chapterIndex: timer.currentChapterIndex - 1});
    }

    private nextChapterButtonHandler = (event: MouseEvent) => {
        this.emit(EVENT_CHAPTER_SWITCH, {chapterIndex: timer.currentChapterIndex + 1});
    }

    private sfxToggleHandler = () => {
        audioController.sfxMuted = !audioController.sfxMuted;
    }

    private musicToggleHandler = () => {
        audioController.musicMuted = !audioController.musicMuted;
    }

    private audioControllerSoundToggleHandler = () => {
        if (audioController.sfxMuted) {
            this.sfxToggle.classList.remove('active');
        } else {
            this.sfxToggle.classList.add('active');
        }
        if (audioController.musicMuted) {
            this.musicToggle.classList.remove('active');
        } else {
            this.musicToggle.classList.add('active');
        }
    }

}