let GLOBE_HOME_ROTATION: Vector3;
let GLOBE_LOCAL_TRANSFER_ROTATION: Vector3;
let GLOBE_PACKAGE_ROTATION: Vector3;

export function init() {
    if (LANG_CODE === LOCALE_CHINESE) {
        GLOBE_LANDING_ROTATION = new Vector3(.5, -3.05, 0);
        GLOBE_HOME_ROTATION = new Vector3(.5, -3.65, 0);
        GLOBE_LOCAL_TRANSFER_ROTATION = new Vector3(.5, -3.3, 0);
        GLOBE_PACKAGE_ROTATION = new Vector3(-.1, -3.65, 0);
    } else {
        GLOBE_LANDING_ROTATION = new Vector3(0,0, 0);
        GLOBE_HOME_ROTATION =  new Vector3(.71, -.28, 0);
        GLOBE_LOCAL_TRANSFER_ROTATION = new Vector3(.6, .03, 0);
        GLOBE_PACKAGE_ROTATION = new Vector3(.1, -.28,0);
    }
}

/*
Animations for Intro globe sequence paths
*/
export function showIntroPaths() {
    overlayFactory.introAnimations.forEach((introAnimation, index) => {
        animateIntroPath(introAnimation, Math.random() * 15);
    });
}

function animateIntroPath(introAnimation: any, delay: number) {
    const speedIn = 1.5;
    const solidSpeedIn = 1.5;
    const speedOut = 1.5;
    const staggerDelay = .5;
    const path = introAnimation.path;
    const delayIn = delay;
    const delayOut = delay + speedIn + solidSpeedIn;

    introAnimation.clientParticle.animateIn(1, delayIn);
    introAnimation.minerParticles.animateIn(1, delayIn);
    path.animateIn(speedIn, delayIn, staggerDelay);
    path.animateInSolid(solidSpeedIn, delayIn + speedIn, .2, (material: ShaderMaterial) => {
        material.uniforms.reverseDirection.value = 1;
    });

    path.pathFollowers.forEach((pathItems :PathItem[], index: number) => {
        pathItems.forEach((pathItem: PathItem) => {
            pathItem.animateIn(speedIn, delayIn + speedIn + (index * .2));
        });
    });

    path.animateOut(speedOut, delayOut, staggerDelay, (material: ShaderMaterial, index: number) => {
        material.uniforms.reverseDirection.value = 0;
        if (index == path.tubes.length - 1) {
            animateIntroPath(introAnimation, Math.random() * 15 + 5);
            introAnimation.clientParticle.animateOut(1);
            introAnimation.minerParticles.animateOut(1);
        }
    });
}

export function hideIntroPaths() {
    overlayFactory.introAnimations.forEach((introAnimation, index) => {
        introAnimation.path.animateOut(1.5);
        introAnimation.path.pathFollowers.forEach((pathItems :PathItem[]) => {
            pathItems.forEach((pathItem: PathItem) => {
                pathItem.reset();
            });
        });
        introAnimation.clientParticle.animateOut();
        introAnimation.minerParticles.animateOut();
    });
}

/*
Starts globe from the beginning upon entry
 */
export function goto_landing() {
    cityScene.reset();
    overlayFactory.hideAll();
    hideCity(CITY_ZOOM_OUT_POSITION);
    showGlobe(GLOBE_ZOOM_OUT_POSITION, LOOK_AT, 0, 0);
    gsap.set(hero3d.globe.rotation, {y: 0, x: 0, z: 0});
    hero3d.globe.resetRotation(GLOBE_LANDING_ROTATION);
    nightToDay(0);
}

/*
Chapter 1 - Act 1
Shows CN network
 */
export function play1_1(chapterData: any, actData: any) {
    hideCity(CITY_ZOOM_OUT_POSITION);
    showGlobe(GLOBE_ZOOM_OUT_POSITION, LOOK_AT, 0, 0);
    nightToDay(0);
    dayToNight(3);
    fadeInScene(0, 1.5);
    audioController.play(AUDIO_CH1_MUSIC);
    audioController.play(AUDIO_GVA1);
    audioController.play(AUDIO_CHA);
    const remoteUserParticle = overlayFactory.ch1_remoteUserCNParticle;

    gsap.to(hero3d.globe.rotation, 4, {y: -2.6, x: .2, ease: 'Power2.easeInOut'});
    audioController.play(AUDIO_GR1);

    gsap.to(camera.position, 4, {z: 575,  ease: 'Power1.easeInOut'});

    overlayFactory.ch1_cnNetworkParticles.animateIn(1.5, 3);
    audioController.play(AUDIO_BE3, 3.5);

    showHideText(3, 6, chapterData.titles[0].title);

    gsap.to(hero3d.globe.rotation, 3, {y: -3.16, x: .4, delay: 6, ease: 'Power1.easeInOut'});
    audioController.play(AUDIO_GR2, 6);

    remoteUserParticle.animateInSprites(.5, 7);

    remoteUserParticle.animateIn(0, 8);

    gsap.to(camera.position, 2, {z: 575, ease: 'Power1.easeInOut', delay: 10} );

    showHideText(7, 10, chapterData.titles[1].title);
}

/*
Chapter 1 - Act 2
Shows slow CN transfer
 */
export function play1_2(chapterData: any, actData: any, chapterIndex: number, actIndex: number) {
    dayToNight(0);

    const remoteUserParticle = overlayFactory.ch1_remoteUserCNParticle;
    overlayFactory.ch1_remoteUserCNParticle.animateIn(0);
    overlayFactory.ch1_cnNetworkParticles.animateIn(0);
    remoteUserParticle.animateInSprites(0);
    gsap.set(camera.position, {z: 575} );
    gsap.set(hero3d.globe.rotation, {y: -3.16, x: .4});
    const transferTubeIndex = 4;
    overlayFactory.ch1_remoteUserPaths.animateIn(1, 0, .1);
    overlayFactory.ch1_remoteUserPaths.animateOut(1.5, 2, 0, undefined, [transferTubeIndex]);
    overlayFactory.ch1_cnNetworkParticles.animateInSprites(.5, 3.5);

    gsap.to(hero3d.globe.rotation, 4, {y: -3.04, delay: 1, ease: "Power2.easeInOut"});
    audioController.play(AUDIO_GR3, 1);

    remoteUserParticle.getSprite().pulse(.5, 7);

    overlayFactory.ch1_remoteUserPaths.animateInSolid(3.5, 3.5, 0, (material: ShaderMaterial) => {
            material.uniforms.reverseDirection.value = 1;
        });
    overlayFactory.ch1_remoteUserPaths.getPathFollowers(transferTubeIndex)[0].animateIn(3.5, 3.5);
    overlayFactory.ch1_remoteUserPaths.animateOut(1.5, 7.5, null, () => {
        remoteUserParticle.animateOutSprites(.5);
    });
    overlayFactory.ch1_cnNetworkParticles.animateOutSprites(.5, 7.5);
    showHideText(0, 7.5, chapterData.titles[2].title);
}

