    clientSprite.animateOut(.5, 4);
    requestSprite.animateIn(1, 4);
    gsap.to(requestSprite, .7, {pinHeight: 200, ease: 'Power1.easeInOut', delay: 4});

    chosenMinerParticles.animateIn(1, 5);
    minerParticles.animateIn(1, 5);
