    cameraZoomToTarget.x -= 90;
    cameraZoomToTarget.y -= 30;
    gotoCityPoint(cameraZoomToSrc, cameraZoomToTarget, 0, 20);
    minerParticles.animateOut(.5, 8);

    // Reverse Path Animation
