    showHideText(12, 18, chapterData.titles[2].title);

    // Coin In
    coinSprite.animateIn(.5, 12);
    coinSheet.play(12, 1, 0);
    coinSprite.animateOut(.5, 13);
    paymentPathR.animateOut(1, 13);

    // Animate Path Shaders for transfer
    paymentPath.animateIn(2, 13.5);
    paymentPath.animateInSolid(2, 15.5, 0, (material: ShaderMaterial) => {
        material.uniforms.reverseDirection.value = 1;
        blackDocSprite.animateIn(.5);
    })
    paymentPath.getPathFollowers(0)[0].animateIn(2, 15.5);
    paymentPath.animateOut(2, 17.5);
    chosenMinerParticles.animateOut(.5, 17.5);
    blackDocSprite.animateOut(.5, 19);
    clientParticles.animateOut(.5, 19.5)
    clientSprite.animateOut(.5, 19.5);
    minerSprite.animateOut(.5, 19.5);

    // Animate out scene
    hero3d.setHoverDistance(new Vector3(0,0,0), 20);
