// Helper Animations
function showHideText(showDelay: number, hideDelay: number, text: string) {
    const home = state.getValue(CURRENT_SECTION);
    gsap.to(home.titleExperienceEl, .6, {delay: showDelay, opacity: 1,
        onStart: () => {home.titleExperienceEl.innerText = text;}});
    gsap.to(home.titleExperienceEl, .6, {delay: hideDelay, opacity: 0});
}

function updateLighting(speed: number = 2, delay: number = 0, ease: string = 'Power1.easeInOut') {
    const timeOfDay = state.getValue(TIME_OF_DAY);
    const globeOrCity = state.getValue(GLOBE_OR_CITY);
    let col;
    // DAY
    if (timeOfDay == DAY) {
        col = new Color(LIGHT_SCENE_BG);
        gsap.to([scene.background, scene.fog.color], speed, {r: col.r, g: col.g, b: col.b, delay: delay, ease: ease});
        col = new Color( globeOrCity == GLOBE ? LIGHT_AMBIENT_LIGHT_GLOBE : LIGHT_AMBIENT_LIGHT_CITY);
        gsap.to(ambientLight.color, speed, {r: col.r, g: col.g, b: col.b, delay: delay, ease: ease});
        col = new Color(LIGHT_POINT_LIGHT);
        const intensity = globeOrCity == GLOBE ? LIGHT_POINT_LIGHT_INT_GLOBE : LIGHT_POINT_LIGHT_INT_CITY;
        gsap.to(pointLight, speed, {intensity: intensity, delay: delay, ease: ease});
        gsap.to(pointLight.color, speed, {r: col.r, g: col.g, b: col.b, delay: delay, ease: ease});
        gsap.to(pointLight.position, speed, {x: -1000, y: 1000, z: 1000, delay: delay, ease: ease});
        gsap.to(hero3d.globe.atmosphere.material, speed, {opacity: LIGHT_ATMOSPHERE_OPACITY});
    }
    // NIGHT
    else {
        col = new Color(DARK_SCENE_BG);
        gsap.to([scene.background, scene.fog.color], speed, {r: col.r, g: col.g, b: col.b, delay: delay, ease: ease});
        col = new Color( globeOrCity == GLOBE ? DARK_AMBIENT_LIGHT_GLOBE : DARK_AMBIENT_LIGHT_CITY);
        gsap.to(ambientLight.color, speed, {r: col.r, g: col.g, b: col.b, delay: delay, ease: ease});
        col = new Color( globeOrCity == GLOBE ? DARK_POINT_LIGHT_GLOBE : DARK_POINT_LIGHT_CITY);
        const pointLightPos = GLOBE ? new Vector3(1000, 1000 , 1000) : new Vector3(1000, 2000, 1000); //z=500
        const intensity = globeOrCity == GLOBE ? DARK_POINT_LIGHT_INT_GLOBE : DARK_POINT_LIGHT_INT_CITY;
        gsap.to(pointLight, speed, {intensity: intensity, delay: delay, ease: ease});
        gsap.to(pointLight.color, speed, {r: col.r, g: col.g, b: col.b, delay: delay, ease: ease});
        gsap.to(pointLight.position, speed, {x: pointLightPos.x, y: pointLightPos.y, z: pointLightPos.z, delay: delay, ease: ease});
        gsap.to(hero3d.globe.atmosphere.material, speed, {opacity: DARK_ATMOSPHERE_OPACITY});
    }
}

function nightToDay(speed = 2, delay = 0) {
    const home = state.getValue(CURRENT_SECTION);
    state.setValue(TIME_OF_DAY, DAY);
    const ease = 'Power1.easeInOut';
    gsap.delayedCall(delay, () => {
        home.updateDOMColors();
    });
    let col = new Color(LIGHT_SPHERE_COLOR);
    gsap.to([hero3d.globe.sphereMaterial.color], speed, {r: col.r, g: col.g, b: col.b, delay: delay, ease: ease});
    hero3d.globe.animateOutBackGlowDark(speed / 2, delay, ease);
    //hero3d.globe.animateInBackGlowLight(speed / 2, delay, ease);
    updateLighting(speed, delay, ease);
    cityScene.animateMaterialColor(speed, delay, DAY);
}

function dayToNight(speed = 2, delay = 0) {
    const home = state.getValue(CURRENT_SECTION);
    state.setValue(TIME_OF_DAY, NIGHT);
    const ease = 'Power1.easeInOut';
    gsap.delayedCall(delay, () => {
        home.updateDOMColors();
    });
    let col = new Color(DARK_SPHERE_COLOR);
    gsap.to([hero3d.globe.sphereMaterial.color], speed, {r: col.r, g: col.g, b: col.b, delay: delay, ease: ease});
    const glowDelay = speed == 0 ? 0 : delay + 1;
    //hero3d.globe.animateOutBackGlowLight(speed / 2, glowDelay, ease);
    hero3d.globe.animateInBackGlowDark(speed / 2, glowDelay, ease);
    updateLighting(speed, delay);
    cityScene.animateMaterialColor(speed, delay, NIGHT);
}

function fadeScene(delay: number = 0, speed: number = 1.5) {
    fadeOutScene(delay, speed)
    fadeInScene(delay + speed, speed)
}

function fadeOutScene(delay: number = 0, speed: number = 1.5) {
    hero3d.fadeToColor(speed, delay, hexNumToStr(state.getValue(TIME_OF_DAY) == DAY ? LIGHT_SCENE_BG : DARK_SCENE_BG));
}

function fadeInScene(delay: number = 0, speed: number = 1.5) {
    hero3d.fadeFromColor(speed, delay);
}

function showGlobe(targetPosition: Vector3, lookAtPosition: Vector3 = new Vector3(0,0,0), delay: number = 0, speed:number = 0) {
    let isSceneSwitch = state.getValue(GLOBE_OR_CITY) == GLOBE ? false : true;
    state.setValue(GLOBE_OR_CITY, GLOBE);
    gsap.to(camera.position, speed, {
        delay: delay,
        x: targetPosition.x,
        y: targetPosition.y,
        z: targetPosition.z,
        ease: 'Power1.easeInOut',
        onStart: () => {
            if (isSceneSwitch) {
                updateLighting(0);
            }
            (scene.fog as Fog).far = -100;
            hero3d.globe.add();
        }
    });
    gsap.to(hero3d.lookAtPosition, speed, {
        x: lookAtPosition.x,
        y: lookAtPosition.y,
        z: lookAtPosition.z,
        delay: delay,
        ease: 'Power1.easeInOut'
    });
}

function hideGlobe(targetPosition: Vector3, delay: number = 0, speed: number = 0) {
    gsap.to(camera.position, speed, {
        delay: delay,
        z: targetPosition.z,
        ease: 'Power1.easeInOut',
        onComplete: () => {
            hero3d.globe.remove();
        }
    });
}

function gotoCityPoint(targetCityPosition: Vector3, lookAtPosition: Vector3, delay: number = 0, speed:number = 0, ease: string = 'Power1.easeInOut') {
    let isSceneSwitch = state.getValue(GLOBE_OR_CITY) == CITY ? false : true;
    state.setValue(GLOBE_OR_CITY, CITY);
    let srcAnim = gsap.to(camera.position, speed, {
        delay: delay,
        x: targetCityPosition.x,
        y: targetCityPosition.y,
        z: targetCityPosition.z,
        ease: ease,
        onStart: () => {
            if (isSceneSwitch) {
                updateLighting(0);
            }
            (scene.fog as Fog).far = 600;
            cityScene.add()
        }
    });
    let targetAnim = gsap.to(hero3d.lookAtPosition, speed, {
        x: lookAtPosition.x,
        y: lookAtPosition.y,
        z: lookAtPosition.z,
        delay: delay,
        ease: ease
    });
    return [srcAnim, targetAnim];
}

function hideCity(targetCityPosition: Vector3, lookAtPosition: Vector3 = new Vector3(0,0,0), delay: number = 0, speed: number = 0) {
    gsap.to(camera.position, speed, {
        delay: delay,
        x: targetCityPosition.x,
        y: targetCityPosition.y,
        z: targetCityPosition.z,
        ease: 'Power1.easeInOut',
        onComplete: () => {
            cityScene.remove();
        }
    });
    gsap.to(hero3d.lookAtPosition, speed, {
        x: lookAtPosition.x,
        y: lookAtPosition.y,
        z: lookAtPosition.z,
        delay: delay,
        ease: 'Power1.easeInOut'
    });
}