import {emitter} from '../global/emitter';
import {EVENT_RESIZE, EVENT_SCROLL, Window} from '../global/window';
import {gsap} from 'gsap';

export class MineGraph {
  element: HTMLElement;
  wrapper: HTMLElement;
  parent: HTMLElement;
  child: HTMLElement;

  line: SVGPathElement;
  lineBg: SVGPathElement;
  lineLength: number;
  animating = false;
  reset = true;

  width: number;
  height: number;
  scale: number;

  coins1: HTMLElement[];
  coins2: HTMLElement[];
  coins3: HTMLElement[];
  dots: SVGCircleElement[];

  tls: Array<any> = [];

  constructor(element: HTMLElement) {
    this.element = element;
    this.wrapper = element.querySelector('.graph-wrapper');
    this.parent = element.querySelector('.parent');
    this.child = element.querySelector('.child');

    this.line = element.querySelector('.line') as SVGPathElement;
    this.lineBg = element.querySelector('.line--bg') as SVGPathElement;
    this.lineLength = this.line.getTotalLength() + 0.5;
    this.dots = [...element.querySelectorAll('circle.dot')] as SVGCircleElement[];

    this.coins1 = [...element.querySelectorAll('.coin--1')] as HTMLElement[];
    this.coins2 = [...element.querySelectorAll('.coin--2')] as HTMLElement[];
    this.coins3 = [...element.querySelectorAll('.coin--3')] as HTMLElement[];

    this.resetProps();
    this.animateIn(1);
  }

  tweenCoins(coins: HTMLElement[], duration: number, delay: number, stop: number) {
    this.tls[stop] = gsap.timeline();
    this.tls[stop].to(coins, {
      opacity: 1,
      duration: 0.1,
      delay: delay + 0.1,
      stagger: duration / coins.length + 0.01,
      ease: 'expo.out'
    }).to(coins, {opacity: 0, duration: 0.45, stagger: duration / coins.length});
    return gsap.to(coins, {duration: duration, stagger: duration / coins.length, y: -70, delay, ease: 'back.out(2.5)'});
  }

  async animateIn(delay = 0) {
    let stop1 = 902;
    let stop2 = 482;
    let stop3 = 0;
    let duration = 1;

    gsap.to(this.dots, 0.2, {scale: 1, opacity: 1, stagger: 0.1, delay});
    gsap.to(this.lineBg, 1.2, {strokeDashoffset: stop3, delay});

    // Step 1
    this.tweenCoins(this.coins1, duration, 1, 1);
    gsap.to(this.line, {duration, strokeDashoffset: stop1, delay: 1});

    // Step 1
    this.tweenCoins(this.coins2, duration, 3, 2);
    gsap.to(this.line, {duration, strokeDashoffset: stop2, delay: 3});

    // Step 3
    this.tweenCoins(this.coins3, duration,  5, 3);
    gsap.to(this.line, {duration, strokeDashoffset: stop3, delay: 5, onComplete: ()=> {
          gsap.delayedCall(2, () =>( this.resetAnimation() ));
        }});

    this.reset = false;
  }

  resetProps() {

      gsap.set(this.line, {strokeDasharray: this.lineLength, strokeDashoffset: this.lineLength});
      gsap.set(this.lineBg, {strokeDasharray: this.lineLength, strokeDashoffset: this.lineLength});
      gsap.set(this.dots, {scale: 0, opacity: 0});
      gsap.set(this.coins1, {opacity: 0, y: 0});
      gsap.set(this.coins2, {opacity: 0, y: 0});
      gsap.set(this.coins3, {opacity: 0, y: 0});
  }

  resetAnimation() {
    this.tls.forEach((tl)=> {
      tl.kill();
    })

    gsap.killTweensOf(this.line);
    gsap.killTweensOf(this.lineBg);
    gsap.killTweensOf(this.dots);
    gsap.killTweensOf(this.coins1);
    gsap.killTweensOf(this.coins2);
    gsap.killTweensOf(this.coins3);

    this.resetProps();

    this.animateIn(2);
  }

  destroy() {

  }
}
