import {emitter} from "./emitter";

export const EVENT_RESIZE = 'events_resize';
export const EVENT_SCROLL = 'events_scroll';
export const EVENT_SCROLL_TO = 'events_scroll_to';
export const EVENT_HEADER_COLOR = 'events_header_color';

export class Window {
    public static width: number;
    public static height: number;
    public static halfWidth: number;
    public static halfHeight: number;
    public static lastScrollY: number = 0;

    constructor() {
        window.addEventListener('resize', (event: Event) => this.resizeHandler(event));
        window.addEventListener('scroll', (event: Event) => this.scrollHandler(event));
        this.resizeHandler();
    }

    private resizeHandler(event?: Event): void {
        Window.width = window.innerWidth;
        Window.height = window.innerHeight;
        Window.halfWidth = Math.round(Window.width * 0.5);
        Window.halfHeight = Math.round(Window.height * 0.5);
        emitter.emit(EVENT_RESIZE, {
            width: Window.width,
            height: Window.height
        });
    }

    private scrollHandler(event: Event): void {
        emitter.emit(EVENT_SCROLL, {
            y: window.scrollY,
            deltaY: window.scrollY - Window.lastScrollY
        });
        Window.lastScrollY = window.scrollY;
    }
}

declare global {
    interface Window {
        ga: any;
        lazySizesConfig: any;
    }
}
