const vertex = `
    uniform float isGlobeParticles;
    uniform float dpr;
    uniform float globalAlpha;
    attribute float alpha;
    attribute float random;
    varying float vAlpha;
    
    float map(float value, float min1, float max1, float min2, float max2) {
      return min2 + (value - min1) * (max2 - min2) / (max1 - min1);
    }
    
    void main() {
        vec4 mvPosition = modelViewMatrix * vec4(position, 1.0);
        vec4 worldPosition = modelMatrix * vec4(position, 1.0);
           
        // Adjust particle alpha for Z depth
        float dAlpha = step(0., worldPosition.z);
        vAlpha = mix(1., dAlpha, isGlobeParticles);
        
        // Adjust particles to fade in staggered
        float minAlphaIn = random * .5;
        float maxAlphaIn = minAlphaIn + .5;
        vAlpha = map(globalAlpha, minAlphaIn, maxAlphaIn, 0., 1.) * vAlpha;
        vAlpha = clamp(vAlpha, 0., 1.);
                   
        // Size of particle
        float dSize = worldPosition.z / 30. + .01;
        float maxScale = 1.3;
        float startScale = .7;
        float scaleUp = clamp(map(vAlpha, 0., startScale, 0., maxScale), 0., maxScale);
        float scaleDown = clamp(map(vAlpha, startScale, 1., maxScale, 1.), maxScale, 1.);
        float scale = mix(scaleUp, scaleDown, step(startScale, vAlpha));
        
        gl_PointSize = (mix(5., dSize, isGlobeParticles) * scale) * dpr;
        gl_Position = projectionMatrix * mvPosition;
    }
`;
const fragment = `
    uniform sampler2D texture;
    uniform vec3 color;
    varying float vGlobalAlpha;
    varying float vAlpha;
    
    void main() {
        gl_FragColor = vec4( color, vAlpha );
        gl_FragColor = gl_FragColor * texture2D( texture, gl_PointCoord );
    }
`;


export default {vertex, fragment};
