import Emitter from "@hellomonday/emitter";

const STATE_CHANGE_EVENT = 'stateChangeEvent';

export class State {

    readonly stateItems: any = {};

    constructor() {
        this.stateItems = {};
    }

    setValue(key: string, value: any) {
        if (!this.stateItems[key]) {
            this.stateItems[key] = new StateItem(value);
        } else {
            this.stateItems[key].value = value;
        }
    }

    getValue(key: string) {
        return this.stateItems[key] ? this.stateItems[key].value : null;
    }

    on(key:string, callback: Function) {
        if (!this.stateItems[key]) {
            this.stateItems[key] = new StateItem();
        }
        this.stateItems[key].on(STATE_CHANGE_EVENT, callback);
    }

    off(key: string) {
        if (this.stateItems[key]) {
            this.stateItems[key].off(STATE_CHANGE_EVENT);
        }
    }
}

class StateItem extends Emitter {

    private _value: string = '';

    constructor(value = '') {
        super();
        this.value = value;
    }

    get value() {
        return this._value;
    }

    set value(newValue) {
        const lastValue = this.value;
        this._value = newValue;
        this.emit(STATE_CHANGE_EVENT, {current: this.value, previous: lastValue});
    }
}

export const state = new State();