/*
Used for creating particle clouds and single particles that you can attach sprites to.
 */

import {
    BufferAttribute,
    BufferGeometry,
    Color,
    Object3D, Points,
    ShaderMaterial,
    Texture,
    Vector3
} from "three";
import {gsap} from "gsap";
import ParticleShader from './shaders/ParticleShader';
import {loadTexture, projectVector} from "../global/utils";
import {camera, hero3d, scene} from "./hero3d";
import {Window} from "../global/window";
import Sprite2d from "./sprite2d";
import coordPos from "@hellomonday/coord-pos";
import {City} from "./city";
import {ASSETS_3D, DAY, GLOBE_RADIUS_FAR, TIME_OF_DAY} from "../global/constants";
import * as THREE from "three";
import {state} from "../global/state";

export class ParticleItem {
    public id: string;
    public position: Vector3;
    constructor(id: string, position: Vector3 = null) {
        this.id = id;
        this.position = position;
    }
}

export class ParticleCityItem extends ParticleItem {
    public city: City;
    constructor(city: City, zPosition: number = GLOBE_RADIUS_FAR) {
        super(city.name, new Vector3(...coordPos(city.latitude, city.longitude, zPosition)));
        this.city = city;
    }
}

export default class Particles {
    public material: ShaderMaterial;
    private indicesByName: any = {};
    private attachedSprites: any = {};
    private cloud: Points;
    private positionAttributes: BufferAttribute;
    private geometry: BufferGeometry;

    constructor(particleItems: Array<ParticleItem>, particleColor: number, container: Object3D,
                isGlobeParticles: boolean = true, particleType: string = state.getValue(ASSETS_3D).spot_texture_url) {

        this.geometry = new BufferGeometry();
        let positions = new Float32Array(particleItems.length * 3);
        this.positionAttributes = new BufferAttribute(positions, 3);
        this.geometry.setAttribute('position', this.positionAttributes);

        let alpha = new Float32Array(particleItems.length);
        this.geometry.setAttribute('alpha', new BufferAttribute(alpha, 1));

        let random = new Float32Array(particleItems.length);
        this.geometry.setAttribute('random', new BufferAttribute(random, 1));

        let uniforms = {
            color: { value: new Color( particleColor ) },
            texture: {type: "t", value: new Texture()},
            globalAlpha: {type: "f", value: 0},
            dpr: {type: "f", value: hero3d.dpr},
            isGlobeParticles: {type: "f", value: isGlobeParticles}
        };

        // point cloud material
        this.material = new ShaderMaterial( {
            uniforms: uniforms,
            vertexShader: ParticleShader.vertex,
            fragmentShader: ParticleShader.fragment,
            transparent: true,
            fog: false,
            depthWrite: false,
            depthTest: false
        });

        this.cloud = new Points( this.geometry, this.material );
        this.cloud.visible = false;
        container.add(this.cloud);

        for (let i = 0; i < particleItems.length; i++) {
            const particleItem = particleItems[i];
            this.geometry.attributes.position.setXYZ(i, particleItem.position.x, particleItem.position.y, particleItem.position.z);
            this.geometry.attributes.random.setX(i, Math.random());
            this.geometry.attributes.alpha.setX(i, 1);
            this.indicesByName[particleItem.id] = i;
        }

        loadTexture(particleType).then(value => {
           this.material.uniforms.texture.value = value;
        });
    }

    public attachSprite2d(sprite2d: Sprite2d, particleItemId: string = null, spriteId: string = null) {
        const index = particleItemId ? this.indicesByName[particleItemId] : 0;
        const id = spriteId ? spriteId : 'sprite' + Object.keys(this.attachedSprites).length;
        this.attachedSprites[id] = {sprite: sprite2d, particleIndex: index};
    }

    public getSprite(spriteId: string = null): Sprite2d {
        if (spriteId) {
            return this.attachedSprites[spriteId].sprite;
        } else {
            return this.attachedSprites[Object.keys(this.attachedSprites)[0]].sprite;
        }
    }

    public animateIn(speed: number = 1, delay: number = 0, withSprites: boolean = false) {
        gsap.to(this.material.uniforms.globalAlpha, speed, {value: 1, delay: delay, ease: 'none', onStart: () => {
                this.cloud.visible = true;
            }});
        if (withSprites) {
            this.animateInSprites(speed, delay);
        }
        this.blending = state.getValue(TIME_OF_DAY) == DAY ? false : true;
    }

    public animateInSprites(speed: number = 1, delay: number = 0) {
        Object.keys(this.attachedSprites).forEach( (id) => {
            this.attachedSprites[id].sprite.animateIn(speed, delay);
        });
    }

    public animateOut(speed: number = 1, delay: number = 0, withSprites: boolean = false) {
        gsap.to(this.material.uniforms.globalAlpha, speed, {value: 0, delay: delay, ease: 'none', onComplete: () => {
            this.cloud.visible = false;
            }});
        if (withSprites) {
            this.animateOutSprites(speed, delay);
        }
    }

    public animateOutSprites(speed: number = 1, delay: number = 0) {
        Object.keys(this.attachedSprites).forEach( (id) => {
            this.attachedSprites[id].sprite.animateOut(speed, delay);
        });
    }

    public set blending(value: boolean) {
        if (value) {
            this.material.blending = THREE.CustomBlending;
            this.material.blendSrc = THREE.SrcAlphaFactor;
            this.material.blendDst = THREE.DstAlphaFactor;
            this.material.blendEquation = THREE.AddEquation;
            this.material.premultipliedAlpha = true;
        } else {
            this.material.blending = THREE.NormalBlending;
            this.material.premultipliedAlpha = false;
        }
        this.material.needsUpdate = true;
    }

    public render() {
        Object.keys(this.attachedSprites).forEach( (id) => {
            let attachedSprite = this.attachedSprites[id];
            let sprite2d = attachedSprite.sprite;
            if (sprite2d.element.style.display == 'block' || sprite2d.needsUpdate) {
                const index = attachedSprite.particleIndex;
                const position = this.geometry.attributes.position;
                const localPos = new Vector3(position.getX(index), position.getY(index), position.getZ(index));
                const worldPosition = this.cloud.localToWorld(localPos);
                const coord2d = projectVector(worldPosition, camera, Window.width, Window.height);
                gsap.set(sprite2d.element, {x: coord2d.x - sprite2d.xOffset, y: coord2d.y - sprite2d.yOffset});
                sprite2d.needsUpdate = false;
            }
        });
        this.material.uniforms.dpr.value = hero3d.dpr;
    }

    public reset(delay: number = 0) {
        gsap.set(this.material.uniforms.globalAlpha, {value: 0, delay: delay});
    }
}