import {Page} from "../global/page";
import {SLUG_STORE} from "../global/constants";
import {StoreCarousel} from "../components/storeCarousel";
import {Carousel} from "../components/carousel";
import {Slideshow} from "../components/slideshow";
import {Accordion} from "../components/accordion";

export class Store extends Page {
    storeCarousel: StoreCarousel;
    slideshow: Slideshow;
    carousels: Array<Carousel> = [];
    accordion: Accordion;

    constructor() {
        super(document.querySelector('main'));

        this.storeCarousel = new StoreCarousel(this.element.querySelector('.StoreCarousel'));
        this.slideshow = new Slideshow(this.element.querySelector('.Slideshow'));

        for (const el of this.element.querySelectorAll('.Carousel')) {
            this.carousels.push(new Carousel(el as HTMLElement));
        }

        this.accordion = new Accordion(this.element.querySelector('.Accordion'));
    }

    get slug() {
        return SLUG_STORE;
    }

    destroy() {
        this.storeCarousel.destroy();
        this.slideshow.destroy();
        this.accordion.destroy()
        this.carousels.forEach((c) => { c.destroy() });
        super.destroy();
    }
};
