import {
    EVENT_HEADER_COLOR,
    EVENT_RESIZE, EVENT_SCROLL,
    EVENT_SCROLL_TO, Window
} from "./global/window";
import {emitter} from "./global/emitter";
import {gsap} from "gsap";
import {BP_M, COLOR_MODES, LANG_COOKIE_KEY, LANG_COOKIE_VALUE, LOCALE_CHINESE} from "./global/constants";
import {Accordion} from "./components/accordion";
import {setCookie} from "./global/utils";

export enum Action {
    show = 'show',
    hide = 'hide'
}

export interface StatePayload {
    state: Action;
}

class Header {
    public element: HTMLElement;
    public backgroundWrapper: HTMLElement;
    private navigation: HTMLElement;
    private backgrounds: NodeList;
    private languageAnchors: NodeList;
    private communityButton: HTMLElement;
    private communityDropdown: HTMLElement;
    private communityAnchors: NodeList;
    private navItems: NodeListOf<HTMLElement>;
    private subNavItems: NodeListOf<HTMLElement>;
    private navHandleButton: HTMLElement;
    private hamburger: HTMLElement;
    private menuAccordion: Accordion;
    private hidden: boolean = false;
    private lastY: number = 0;
    private hoverState: boolean = false;

    constructor() {
        this.element = document.querySelector('.nav-header');
        this.navigation = this.element.querySelector('.navigation-container');
        this.backgroundWrapper = this.element.querySelector('.background');
        this.navItems = this.element.querySelectorAll('nav > ul > li[data-slug]');
        this.subNavItems = this.element.querySelectorAll('.submenu li');
        this.languageAnchors = this.element.querySelectorAll('.languages a');
        this.communityButton = this.element.querySelector('.community-nav button');
        this.communityDropdown = this.element.querySelector('.community-nav .dropdown');
        this.communityAnchors = this.element.querySelectorAll('.community-nav a');
        this.navHandleButton = this.element.querySelector('.nav-handle');
        this.hamburger = this.element.querySelector('.hamburger');
        this.backgrounds = this.element.querySelectorAll('.background .div');

        this.subNavItems.forEach((el) => {
            el.addEventListener('click',
                (e) => this.onSubNavItemClick(e));
        }, this);

        this.languageAnchors.forEach((el) => {
            el.addEventListener('click',
                (e) => this.onLanguageClick(e));
        }, this);

        this.communityAnchors.forEach((el) => {
            el.addEventListener('click',
                (e) => this.onLanguageClick(e));
        }, this);

        this.element.addEventListener('mouseenter' , (e) => this.onMouseEnter());
        this.element.addEventListener('mouseleave' , (e) => this.onMouseLeave());
        this.communityButton.addEventListener('click', (e) => this.toggleCommunityDropdown(e));
        this.navHandleButton.addEventListener('click', (e) => this.toggleMobileNav(e));
        this.backgroundWrapper.addEventListener('click', (e) => this.hideMobileNav());

        emitter.on(EVENT_HEADER_COLOR, this.setColorHandler.bind(this));
        emitter.on(EVENT_SCROLL, this.onScrollHandler.bind(this));
        emitter.on(EVENT_RESIZE, this.onResizeHandler.bind(this));

        this.menuAccordion = new Accordion(this.element.querySelector('.Accordion'), [], false, false);
    }

    resetToHome(speed: number = .5) {
        gsap.to([this.navigation], {
            duration: speed,
            opacity: 0,
            onComplete: () => {
                this.element.classList.add('zoom');
            }});
    }

    animateIn(delay: number = 0, speed: number = .5) {
        gsap.to([this.element, this.navigation], {
            duration: speed,
            opacity: 1,
            delay: delay
        });
    }

    onMouseLeave() {
        this.hoverState = false;
    }

    onMouseEnter() {
        this.hoverState = true;
        this.show();
    }

    onLanguageClick(e: any) {
        setCookie(LANG_COOKIE_KEY + '=' + LANG_COOKIE_VALUE + ';path=/;')
        window.location.href = e.target.href;
        return false;
    }

    onResizeHandler() {
        if (Window.width > BP_M) {
            this.navHandleButton.classList.remove('active');
            this.hamburger.classList.remove('is-active');
            gsap.set(this.navigation, {clearProps: 'all'});
            gsap.set(this.backgroundWrapper, {clearProps: 'all'});
        }
    }

    onScrollHandler() {

        // Community explorer selector
        if (this.communityButton.classList.contains('active')) {
            this.hideCommunityDropdown();
        }

        if (this.element.classList.contains('zoom'))
            return;

        // Toggle header display
        if (Window.lastScrollY <= window.scrollY && Window.lastScrollY > 0 && Window.width > BP_M) {
            if (this.lastY > 20) {
                this.hide();
                this.lastY = 0;
            } else {
                this.lastY += 1;
            }
        } else {
            this.show();
            this.lastY = 0;
        }
    }

    toggleCommunityDropdown(e: any) {
        if (e.target.classList.contains('active')) {
            this.hideCommunityDropdown();
        } else {
            this.showCommunityDropdown();
        }
    }

    toggleMobileNav(e: any) {
        if (e.target.classList.contains('active')) {
            this.hideMobileNav();
        } else {
            this.showMobileNav();
        }
    }

    hideMobileNav() {
        if (Window.width < BP_M) {
            this.navHandleButton.classList.remove('active');
            this.hamburger.classList.remove('is-active');
            gsap.to(this.navigation, {duration: .8, x: '0%',  ease: 'Power3.easeOut' });
            gsap.to(this.backgroundWrapper, {duration: .8, autoAlpha: 0});
        }
    }

    showMobileNav() {
        this.navHandleButton.classList.add('active');
        this.hamburger.classList.add('is-active');
        gsap.to(this.navigation, {duration: .8, x: '-100%',  ease: 'Power3.easeOut' });
        gsap.to(this.backgroundWrapper, {duration: .8, autoAlpha: 1});
    }

    hideCommunityDropdown() {
        this.communityButton.classList.remove('active');
        this.communityDropdown.classList.remove('active');
    }

    showCommunityDropdown() {
        this.communityButton.classList.add('active');
        this.communityDropdown.classList.add('active');
    }

    onSubNavItemClick(e: any) {
        const el = e.target;
        const anchor = el.dataset.anchor;
        const parentNav = e.target.closest('[data-slug]')
        const parentAnchor = parentNav.querySelector('a');

        if (parentNav.classList.contains('active')) {
            // Scroll down
            emitter.emit(EVENT_SCROLL_TO, { anchor });
        } else {
            // Deeplink
            history.pushState({},'', `${parentAnchor.getAttribute('href')}#${anchor}`);
        }
    }

    setActiveNavItem(slug: string) {
        this.navItems.forEach((el) => {
            const navSlug = el.getAttribute('data-slug');
            const navSlugFiltered = navSlug.replace('/'+LOCALE_CHINESE,'').substr(1);
            el.classList.remove('active');

            if (navSlugFiltered == slug || slug.search(navSlugFiltered) === 0) {
                el.classList.add('active');
            }
        });

        this.subNavItems.forEach((el) => el.classList.remove('active'));
    }

    setActiveSubNavItem(anchor: string) {
        let activeItem;
        let subItems;

        activeItem = [...this.navItems].filter(el => {
            if (el.classList.contains('active')) {
                return el;
            }
        })[0];

        if (activeItem) {
            subItems = activeItem.querySelectorAll('[data-anchor]');

            if (subItems) {
                subItems.forEach((el) => {
                    const element = el as HTMLElement;

                    if (element.dataset.anchor === anchor)
                        element.parentElement.classList.add('active');
                    else
                        element.parentElement.classList.remove('active');
                })
            }
        }
    }

    resetBackground() {
        if (Window.width < BP_M) {
            this.navHandleButton.classList.remove(...COLOR_MODES);
            this.element.classList.remove(...COLOR_MODES);
        }

        this.backgroundWrapper.style.removeProperty('opacity');
    }

    hide() {
        if (this.hidden || this.hoverState) return;
        this.hidden = true;

        gsap.to([this.navigation, this.backgroundWrapper], {duration: .5, y: '-100%'});
    }

    show() {
        if (!this.hidden) return;
        this.hidden = false;

        gsap.to([this.navigation, this.backgroundWrapper], {duration: .5, y: '0%'});
    }

    hideZoom() {
        if (!this.element.classList.contains('zoom')) return false;

        const duration = .8;
        const ease = 'Power4.easeOut';

        gsap.to(this.navigation, {
            duration,
            opacity: 0,
            y: -30,
            ease,
            onComplete: ()=> {
                this.element.classList.remove('zoom');
                gsap.to(this.navigation, {
                    duration,
                    opacity: 1,
                    y: 0,
                    ease
                });
            }});
    }

    private setColorHandler({mode, delay = 0}: any) {
        if (Window.width < BP_M) {
            this.element.classList.remove(...COLOR_MODES);
            this.element.classList.add('mode-light');

            if (!this.navHandleButton.classList.contains(mode)) {
                this.navHandleButton.classList.remove(...COLOR_MODES);
                this.navHandleButton.classList.add(mode);
            }
        } else if (!this.element.classList.contains(mode)) {

            this.element.classList.remove(...COLOR_MODES);

            // Delay background for page change
            if (delay) {
                gsap.set(this.backgrounds, {transitionDelay: delay});
                gsap.delayedCall(delay, ()=> {
                    gsap.set(this.backgrounds, {clearProps: 'transition-delay'});
                })
            }

            this.element.classList.add(mode);
        }
    }
}

export const header = new Header();
