import {gsap} from "gsap";
import {EVENT_RESIZE, Window} from "../global/window";
import {emitter} from "../global/emitter";

export class Slideshow {
    public element: HTMLElement;
    private navItems: NodeListOf<HTMLElement>;
    private imageItems: NodeListOf<HTMLElement>;
    private copyItems: NodeListOf<HTMLElement>;
    private btnPrev: HTMLElement;
    private btnNext: HTMLElement;

    private speed: number;
    private currIndex: number = 0;

    constructor(element: HTMLElement) {
        this.element = element;
        this.navItems = this.element.querySelectorAll('.nav li');
        this.imageItems = this.element.querySelectorAll('.images-block .item');
        this.copyItems = this.element.querySelectorAll('.copy-block .item');
        this.btnPrev = this.element.querySelector('.prev');
        this.btnNext = this.element.querySelector('.next');
        this.speed = .3;
        this.init();
    }

    private init() {
        this.layout();
        this.addListeners();
        this.showActive();
    }

    private addListeners() {
        this.btnPrev.addEventListener('click', this.onPrevClick.bind(this));
        this.btnNext.addEventListener('click', this.onNextClick.bind(this));

        for (const item of this.navItems) {
            item.addEventListener('click', this.onNavClick.bind(this));
        }
        emitter.on(EVENT_RESIZE, this.layout);
    }

    private removeListeners() {
        this.btnPrev.removeEventListener('click', this.onPrevClick.bind(this));
        this.btnNext.removeEventListener('click', this.onNextClick.bind(this));

        for (const item of this.navItems) {
            item.removeEventListener('click', this.onNavClick.bind(this));
        }
        emitter.off(EVENT_RESIZE, this.layout);
    }

    private onPrevClick(e: any) {
        this.hideCurrent();
        this.currIndex = this.currIndex === 0 ? this.navItems.length - 1 : this.currIndex -= 1;
        this.showActive();
    }

    private onNextClick(e: any) {;
        this.hideCurrent();
        this.currIndex = this.currIndex === this.navItems.length - 1 ? 0 : this.currIndex += 1;
        this.showActive();
    }

    private onNavClick(e: any) {
        const item = e.target.parentElement;

        this.hideCurrent();
        this.currIndex = [...this.navItems].indexOf(item);
        this.showActive();
    }

    private hideCurrent() {
        const activeItem = [this.imageItems[this.currIndex], this.copyItems[this.currIndex]];

        // Deselect old active
        this.navItems[this.currIndex].classList.remove('active');
        gsap.to(activeItem, {duration: this.speed, autoAlpha: 0});
    }

    private showActive() {
        const activeItem = [this.imageItems[this.currIndex], this.copyItems[this.currIndex]];

        this.navItems[this.currIndex].classList.add('active');
        //gsap.set(activeItem, {display: 'block', opacity: 0});
        gsap.to(activeItem, {duration: this.speed, autoAlpha: 1});
    }

    private layout = () => {
    }

    public destroy() {
        this.removeListeners();
        for (const item of this.navItems) {
            item.removeEventListener('click', this.onNavClick.bind(this));
        }
    }
}
