import {state} from "./state";
import {LANG_CODE, LANG_COOKIE_KEY, LANG_COOKIE_VALUE, LOCALE, LOCALE_CHINESE, LOCALE_CHINESE_CODE} from "./constants";
import {getCookie} from "./utils";

export const redirectsObj = [
    {
      catch: '/#community',
      destination: '/build/#community'
    },
    /*{
        catch: '/grants/',
        destination: 'https://vintage.filecoin.io/grants/'
    },
    {
        catch: '/blog/*',
        destination: 'https://vintage.filecoin.io/blog/*'
    }*/
];

export class Redirects {
    public check() {
        const path = window.location.href.replace(window.location.origin, '');

        redirectsObj.forEach((r) => {
            const catchSplit = r.catch.split('*');
            const pathSplit = path.split('/');
            //
            // if (catchSplit.length > 1 && path.search(catchSplit[0]) > -1) {
            //     // wildcard found
            //     if (pathSplit.length === 3) {
            //         // base
            //         window.location.href = r.destination.split('*')[0];
            //     } else {
            //         // detail
            //         window.location.href = r.destination.split('*')[0] + pathSplit[2];
            //     }
            // } else if (path === r.catch) {
            //     window.location.href = r.destination;
            // }
        })
    }

    public redirectLang() {
        if (getCookie(LANG_COOKIE_KEY) == LANG_COOKIE_VALUE) {
            return;
        }

        if (LANG_CODE != LOCALE_CHINESE &&
            window.navigator.language.slice(0, 2) == LOCALE_CHINESE_CODE) {
            switch(window.location.pathname){
                case "/":
                    window.location.href = "/" + LOCALE_CHINESE;
            }
        }
    }
}

export const redirects = new Redirects();
