import Emitter from "@hellomonday/emitter";
import {state} from "./state";
import {ASSETS_3D} from "./constants";
import {gsap} from "gsap";
import {preload} from "./preload";
import {isTouch} from "./utils";

export const AUDIO_BE3 = 'be3';
export const AUDIO_BE5 = 'be5';
export const AUDIO_CA1 = 'ca1';
export const AUDIO_CA2 = 'ca2';
export const AUDIO_CA3 = 'ca3';
export const AUDIO_CHA = 'cha';
export const AUDIO_CH1_MUSIC = 'ch1_music';
export const AUDIO_CH2_MUSIC = 'ch2_music';
export const AUDIO_CH3_MUSIC = 'ch3_music';
export const AUDIO_CH4_MUSIC = 'ch4_music';
export const AUDIO_CITY = 'city';
export const AUDIO_CW = 'cw';
export const AUDIO_DLT1 = 'dlt1';
export const AUDIO_DLT2 = 'dlt2';
export const AUDIO_DLT3 = 'dlt3';
export const AUDIO_QIN = 'qin';
export const AUDIO_Q2C = 'q2c';
export const AUDIO_C2B = 'c2b';
export const AUDIO_BCIN = 'bcin';
export const AUDIO_REDX = 'redx';
export const AUDIO_COIN = 'coin';
export const AUDIO_FS1 = 'fs1';
export const AUDIO_FS2 = 'fs2';
export const AUDIO_FW1 = 'fw1';
export const AUDIO_FW2 = 'fw2';
export const AUDIO_FW3 = 'fw3';
export const AUDIO_GR1 = 'gr1';
export const AUDIO_GR2 = 'gr2';
export const AUDIO_GR3 = 'gr3';
export const AUDIO_GR4 = 'gr4';
export const AUDIO_GR5 = 'gr5';
export const AUDIO_GVA1 = 'gva1';
export const AUDIO_IAMB = 'iamb';
export const AUDIO_LW1 = 'lw1';
export const AUDIO_LW2 = 'lw2';
export const AUDIO_MA = 'ma';
export const AUDIO_MW = 'mw';
export const AUDIO_PW1 = 'pw1';
export const AUDIO_PW2 = 'pw2';
export const AUDIO_SL1 = 'sl1';
export const AUDIO_SL2 = 'sl2';
export const AUDIO_SL3 = 'sl3';
export const AUDIO_TA = 'ta';
export const AUDIO_TW1 = 'tw1';
export const AUDIO_TW2 = 'tw2';
export const AUDIO_TW3 = 'tw3';
export const AUDIO_CLK = 'clk';

export const MUSIC_MUTE_CHANGE = 'music_mute_change';
export const SFX_MUTE_CHANGE = 'sfx_mute_change';

export class AudioController extends Emitter{

    public audioCtx: AudioContext;
    private audioItems: {[key: string]: AudioItem} = {};
    private musicAudioItems: {[key: string]: AudioItem} = {};
    private sfxAudioItems: {[key: string]: AudioItem} = {};
    private _sfxMuted: boolean;
    private _musicMuted: boolean;

    constructor() {
        super();
    }

    private init() {
        // @ts-ignore
        this.audioCtx = new(window.AudioContext || window.webkitAudioContext);
        const assets = state.getValue(ASSETS_3D);
        for (const assetId in assets) {
            const url = assets[assetId];
            if (url.indexOf('.ogg') > -1 || url.indexOf('.mp3') > -1) {
                this.audioItems[assetId] = new AudioItem(assetId, url)
                if (assetId == AUDIO_CH1_MUSIC || assetId == AUDIO_CH2_MUSIC ||
                    assetId == AUDIO_CH3_MUSIC || assetId == AUDIO_CH4_MUSIC) {
                    this.musicAudioItems[assetId] = this.audioItems[assetId];
                } else {
                    this.sfxAudioItems[assetId] = this.audioItems[assetId];
                }
            }
        };

        this.audioItems[AUDIO_GVA1].useBuffer = false;
        this.audioItems[AUDIO_CITY].useBuffer = false;
        this.audioItems[AUDIO_IAMB].useBuffer = false;
        this.audioItems[AUDIO_CH1_MUSIC].useBuffer = false;
        this.audioItems[AUDIO_CH2_MUSIC].useBuffer = false;
        this.audioItems[AUDIO_CH3_MUSIC].useBuffer = false;
        this.audioItems[AUDIO_CH4_MUSIC].useBuffer = false;

        this.audioItems[AUDIO_CLK].maxGain = -1;
        this.audioItems[AUDIO_CH1_MUSIC].maxGain = -.6;
        this.audioItems[AUDIO_CH2_MUSIC].maxGain = -.6;
        this.audioItems[AUDIO_CH3_MUSIC].maxGain = -.6;
        this.audioItems[AUDIO_CH4_MUSIC].maxGain = -.6;

        this.musicMuted = true;
    }

    public async load() {
        if (isTouch()) {
            return;
        }
        this.init();
        await Promise.all(Object.keys(this.audioItems).map(async (assetId) => {
            return new Promise(resolve => {
                const audioItem = this.audioItems[assetId];
                if (audioItem.useBuffer) {
                    const request = new XMLHttpRequest();
                    request.open("GET", audioItem.url);
                    request.responseType = "arraybuffer";
                    const self = this;
                    request.onload = function() {
                        let undecodedAudio = request.response;
                        self.audioCtx.decodeAudioData(undecodedAudio, (data: AudioBuffer) => audioItem.buffer = data);
                        resolve();
                    };
                    request.send();
                } else {
                    resolve();
                }
            });
        }));
    }

    public play(id: string, delay: number = 0) {
        const audioItem = this.get(id);
        if (!audioItem) return;
        if (delay == 0) {
            audioItem.play();
        } else {
            gsap.delayedCall(delay, () => {
                audioItem.play();
            });
        }
    }

    public pause(id: string, delay: number = 0) {
        const audioItem = this.get(id);
        if (!audioItem) return;
        gsap.delayedCall(delay, () => {
            audioItem.pause();
        });
    }

    public fadeIn(id: string, delay: number = 0, duration: number = .5) {
        const audioItem = this.get(id);
        if (!audioItem) return;
        gsap.delayedCall(delay, () => {
            audioItem.fadeIn(duration);
        });
    }

    public fadeOut(id: string, delay: number = 0, duration: number = .5, pauseOnComplete: boolean = true) {
        const audioItem = this.get(id);
        if (!audioItem) return;
        gsap.delayedCall(delay, () => {
            audioItem.fadeOut(duration, pauseOnComplete);
        });
    }

    public get(id: string) {
        return this.audioItems[id];
    }

    public stopAll() {
        Object.keys(this.audioItems).forEach((id) => {
            this.audioItems[id].pause();
        });
    };

    public set musicMuted(isMuted: boolean) {
        this._musicMuted = isMuted;
        Object.keys(this.musicAudioItems).forEach((id) => {
            this.musicAudioItems[id].mute(isMuted);
        });
        this.emit(MUSIC_MUTE_CHANGE, {isMuted: isMuted});
    }

    public get musicMuted() {
        return this._musicMuted;
    }

    public set sfxMuted(isMuted: boolean) {
        this._sfxMuted = isMuted
        Object.keys(this.sfxAudioItems).forEach((id) => {
            this.sfxAudioItems[id].mute(isMuted);
        });
        this.emit(SFX_MUTE_CHANGE, {isMuted: isMuted});
    }

    public get sfxMuted() {
        return this._sfxMuted;
    }

    public pauseAll() {
        if (this.audioCtx) {
            this.audioCtx.suspend();
        }
    }

    public resumeAll() {
        if (this.audioCtx) {
            this.audioCtx.resume();
        }
    }
}

export class AudioItem {

    public id: string;
    public url: string;
    public audioElement: HTMLAudioElement;
    public buffer: AudioBuffer;
    public sourceNode: AudioBufferSourceNode;
    public gainNode: GainNode;
    public useBuffer: boolean = true;
    public isPlaying: boolean = false;
    public muted: boolean = false;
    private _maxGain: number = 0;

    constructor(id: string, url: string) {
        this.id = id;
        this.url = url;

        this.gainNode = audioController.audioCtx.createGain();
        this.gainNode.connect(audioController.audioCtx.destination);
    }

    public set maxGain(value: number) {
        this._maxGain = value;
        if (!this.muted) {
            this.gainNode.gain.value = value;
        }
    }

    public get maxGain() {
        return this._maxGain;
    }

    play() {
        if (this.buffer) {
            this.sourceNode = audioController.audioCtx.createBufferSource();
            this.sourceNode.buffer = this.buffer;
            this.sourceNode.connect(audioController.audioCtx.destination);
            this.sourceNode.connect(this.gainNode);
            this.sourceNode.start(0);
        } else {
            if (!this.audioElement) {
                this.audioElement = new Audio(this.url);
                this.audioElement.preload = 'auto';
                const source = audioController.audioCtx.createMediaElementSource(this.audioElement);
                source.connect(this.gainNode);
                source.connect(audioController.audioCtx.destination);
            }
            this.audioElement.play();
        }
        this.isPlaying = true;
        if (!this.muted) {
            this.gainNode.gain.value = this.maxGain;
        }
    }

    pause() {
        if (this.sourceNode) {
            this.sourceNode.disconnect();
            this.sourceNode.stop(0);
            this.sourceNode = null;
        } else {
            if (this.audioElement) {
                this.audioElement.pause();
            }
        }
        this.isPlaying = false;
    }

    fadeIn(duration: number = .5) {
        if (this.muted) {
            return;
        }
        gsap.to(this.gainNode.gain, duration, {value: this.maxGain});
    }

    fadeOut(duration: number = .5, pauseOnComplete: boolean = true) {
        gsap.to(this.gainNode.gain, duration, {
            value: -1,
            onComplete: () => {
                if (pauseOnComplete) {
                    this.pause();
                }
            }
        });
    }

    mute(isMuted: boolean) {
        this.muted = isMuted;
        this.gainNode.gain.value = isMuted ? -1 : this.maxGain;
    }
}

export const audioController = new AudioController();