import {gsap} from 'gsap';

export class Newsletter {
    element: HTMLElement;
    error: HTMLElement;
    form: HTMLElement;
    inputEmail: HTMLInputElement;
    url: string;
    apiKey: string;
    listId: string;

    constructor(element: HTMLElement) {
        this.element = element;
        this.error = element.querySelector('.error-message');
        this.form = element.querySelector('form');
        this.inputEmail = element.querySelector('.email');
        this.init();
    }

    init() {
        this.form.addEventListener('submit', this.onSubmit.bind(this));
        this.inputEmail.addEventListener('focus', this.onFocus.bind(this));

        this.apiKey = 'a1dfb670c4f1fb042e82a1f1d';
        this.listId = 'ff0bee3cae';
        this.url = `//protocol.us16.list-manage.com/subscribe/post-json?u=${this.apiKey}&id=${this.listId}`;

        (<any>window).MC_callback = (e: any) => {
            console.log(e);

            if (e.result === 'error') {
                if (e.msg.includes('already subscribed')) {
                    const linkHref = e.msg.match(/href="([^"]*)/)[1];
                    this.error.innerHTML = `Your email, ${this.inputEmail.value} is already subscribed to Filecoin News
                    <a href="${linkHref}">Update your preferences</a>`;
                } else {
                    this.error.innerHTML = e.msg;
                }
                this.showError();
            } else {
                this.showSuccess();
            }
        };
    }

    validateEmail(value: string) {
        var re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
        return re.test(value);
    }

    showError() {
        this.element.classList.remove('success');
        this.element.classList.add('error');
    }

    showSuccess() {
        const delayMessage = 3;

        this.element.classList.remove('error');
        this.element.classList.add('success');

        gsap.delayedCall(delayMessage, () => {
            this.element.classList.remove('success');
            this.inputEmail.value = '';
        })
    }

    onFocus(e: any) {
        this.element.classList.remove('success', 'error');
    }

    onSubmit(e: any) {
        e.preventDefault();

        const email = this.inputEmail.value;

        if (!this.validateEmail(email)) {
            this.showError();
            return false;
        }

        //generate JSONP script
        const script = document.createElement("script");
        script.type = "text/javascript";
        script.src = this.url + "&c=window.MC_callback&EMAIL=" + email;
        document.getElementsByTagName("head")[0].appendChild(script);
    }
}
