import {emitter} from '../global/emitter';
import {EVENT_RESIZE} from '../global/window';
import {gsap} from 'gsap';
import map from '@hellomonday/map';

export class EcoGraph {
  element: HTMLElement;
  animating = false;
  data: number[];
  max: number;
  weekLabelsWrapper: HTMLElement;
  commitLabelsWrapper: HTMLElement;
  count: number;
  bars: SVGRectElement[];
  labelsY: HTMLElement[] = [];
  labelsX: HTMLElement[] = [];
  lines: SVGLineElement[];
  chartWrapper: HTMLElement;
  observer: IntersectionObserver;

  constructor(element: HTMLElement, data: number[]) {
    this.element = element;
    this.max = Math.max(...data);
    this.data = data.map(value => map(value, 0, this.max, 0, 1));
    this.chartWrapper = element.querySelector('.chart');

    this.bars = [...element.querySelectorAll('rect')] as SVGRectElement[];
    this.lines = [...element.querySelectorAll('line')] as SVGLineElement[];
    this.count = this.bars.length;

    this.weekLabelsWrapper = this.element.querySelector('.labels__x');
    this.commitLabelsWrapper = this.element.querySelector('.labels__y');

    this.initWeekLabels();
    this.initCommitLabels();

    this.presetAnimation();
    this.addListeners();
  }

  addListeners() {
    this.observer = new IntersectionObserver((entries, observer) => {
      for (let entry of entries) {
        if (entry.isIntersecting) {
          const className = entry.target.className;
          if (className === 'chart') {
            this.animateIn(0);
          }
        }
      }
    });

    this.observer.observe(this.chartWrapper);
  }

  initWeekLabels() {
    for (let i = 0; i < this.count; i++) {
      let el = document.createElement('span');
      el.classList.add('span');
      el.innerText = `${i+1}`;
      this.weekLabelsWrapper.appendChild(el);
      this.labelsY.push(el);
    }
  }

  initCommitLabels() {
    let inc = this.max / 4;
    for (let i = 1; i < 5; i++) {
      let el = document.createElement('span');
      el.classList.add('span');
      el.innerText = `${Math.floor(i*inc)}`;
      this.commitLabelsWrapper.appendChild(el);
      this.labelsX.push(el);
    }
  }

  async animateIn(delay = 0) {
    if (this.animating) return;
    this.animating = true;

    gsap.to(this.lines, {duration: 1, scaleX: 1, delay});

    delay += 0.2;

    gsap.to(this.labelsY, {y: -0, opacity: 1, stagger: 0.03, delay});

    this.bars.forEach((bar, index) => {
        gsap.to(bar, {duration: 0.6, scaleY: this.data[index], delay: delay + (index * 0.03)});
    });

    delay += 2;

    gsap.to(this.labelsX, {duration: 0.6, y: 0, opacity: 1, stagger: 0.03, delay});
  }

  presetAnimation() {
    gsap.set(this.bars, {scaleY: 0, transformOrigin: 'bottom'});
    gsap.set(this.labelsY, {y: -10, opacity: 0});
    gsap.set(this.labelsX, {y: -10, opacity: 0});
    gsap.set(this.lines, {scaleX: 0});
    this.animating = false;
  }

  destroy() {
      this.observer.unobserve(this.chartWrapper);
  }
}
