    public async load() {
        const sprites = Array.from(this.sprites2dElement.querySelectorAll('[data-src]'));
        await Promise.all(sprites.map(async (sprite) => {
            await preload.loadImage(sprite as HTMLImageElement);
        }));
    }

    public init() {
        this.iconClient = this.sprites2dElement.querySelector(ICON_CLIENT);
        this.iconDoc = this.sprites2dElement.querySelector(ICON_DOC);
        this.iconDocBlack = this.sprites2dElement.querySelector(ICON_DOC_BLACK);
        this.iconMiner = this.sprites2dElement.querySelector(ICON_MINER);
        this.iconMinerOrange = this.sprites2dElement.querySelector(ICON_MINER_ORANGE);
        this.iconPackage = this.sprites2dElement.querySelector(ICON_PACKAGE);
        this.iconRequest = this.sprites2dElement.querySelector(ICON_REQUEST);
        this.iconCoinSpin = this.sprites2dElement.querySelector(ICON_COIN_SPIN);
        this.iconTextField = this.sprites2dElement.querySelector(TEXT_FIELD);
        this.iconQuestionIn = this.sprites2dElement.querySelector(ICON_QUESTION_IN);
        this.questionToCheck = this.sprites2dElement.querySelector(ICON_QUESTION_TO_CHECK);
        this.checkToBlock = this.sprites2dElement.querySelector(ICON_CHECK_TO_BLOCK);
        this.blockOut = this.sprites2dElement.querySelector(ICON_BLOCK_OUT);
        this.blockApproved = this.sprites2dElement.querySelector(ICON_BLOCK_APPROVED);
        this.blockDeclined = this.sprites2dElement.querySelector(ICON_BLOCK_DECLINED);

        this.initGlobeIntro();
        this.initGlobeChapter1();
        this.initCityChapter1();
        this.initGlobeChapter2();
        this.initCityChapter2();
        this.initCityChapter3();
        this.initCityChapter4();
        this.initGlobeChapter4();
        this.initialized = true;
    }

    public render() {
        if (this.initialized) {
            // Chapter 1
            this.ch1_remoteUserCNParticle.render();
            this.ch1_remoteUserFCAfricaParticle.render();
            this.ch1_remoteUserLocalTransferParticle.render();
            this.ch1_cityMinerLongTransferParticles.render();
            this.ch1_cityMinerShortTransferParticles.render();
            this.ch1_coinSpinParticle.render();
            this.ch1_cnNetworkParticles.render();

            // Chapter 2
            this.ch2_cityPackageParticles.render();
            this.ch2_packageUserParticles.render();
            this.ch2_packageMinerParticles.render();
            this.ch2_packageChosenMinerParticles.render();

            // Chapter 3
            this.ch3_cityMinerParticles.render();
            this.ch3_spritesheetParticles.render();

            // Chapter 4
            this.ch4_clientParticles.render();
            this.ch4_chosenMinerParticles.render();
        }
    }

    public hideAll() {
        for (let property in this) {
            const instance = this[property] as any;
            if (instance.animateOut) {
                if(instance.animateOutSprites) {
                    instance.animateOut(0, 0, true);
                } else {
                    instance.animateOut(0, 0);
                }
            }
            if (instance.reset) {
                instance.reset();
            }
        }
    }

