    private initGlobeChapter2() {
        const data_rates = ['0.02 FIL / 300 TB', '0.1 FIL / 800 TB', '0.06 FIL / 300 TB', '0.08 FIL / 500 TB', '0.07 FIL / 300 TB'];
        const non_chosen_miners = LANG_CODE === LOCALE_CHINESE ? [CITY_XIAN, CITY_HONG_KONG, CITY_CHONGQING] : [CITY_CHICAGO, CITY_QUEBEC, CITY_OUTER_QUEBEC];
        const chosen_miners = LANG_CODE === LOCALE_CHINESE ? [CITY_ZHENGZHOU, CITY_HENGYANG] : [CITY_CHARLOTTE, CITY_TORONTO];
        const all_miners = non_chosen_miners.concat(chosen_miners);
        const client_city = LANG_CODE === LOCALE_CHINESE ? CITY_SHANGHAI : CITY_NYC;

        // Source City (NYC)
        this.ch2_packageUserParticles = new Particles([
            new ParticleCityItem(citiesKey[client_city], GLOBE_RADIUS_CLOSE)], COLOR_BLUE, hero3d.globe.globeContainer, false);
        this.ch2_globePackageDoc2d = new Sprite2d(this.iconPackage);
        this.ch2_packageUserParticles.attachSprite2d(this.ch2_globePackageDoc2d);
        this.ch2_globeClient2d = new Sprite2d(this.iconClient, null, null, AUDIO_CW);
        this.ch2_packageUserParticles.attachSprite2d(this.ch2_globeClient2d);

        // Miner Cities
        const minerCityItems = non_chosen_miners.map(city =>  new ParticleCityItem(citiesKey[city], GLOBE_RADIUS_CLOSE));
        this.ch2_packageMinerParticles = new Particles(minerCityItems, COLOR_BLUE, hero3d.globe.globeContainer, true);
        non_chosen_miners.forEach((city, i) => {
            this.ch2_packageMinerParticles.attachSprite2d(new Sprite2d(this.iconTextField).setText(data_rates[i]), city);
        });

        // Chosen Miner Cities
        const chosenMinerCityItems = chosen_miners.map(city => new ParticleCityItem(citiesKey[city], GLOBE_RADIUS_CLOSE));
        this.ch2_packageChosenMinerParticles = new Particles( chosenMinerCityItems, COLOR_BLUE, hero3d.globe.globeContainer, true);
        chosen_miners.forEach((city, i) => {
            this.ch2_packageChosenMinerParticles.attachSprite2d(new Sprite2d(this.iconTextField).setText(data_rates[i + 3]), city, 'text_'+i);
            this.ch2_packageChosenMinerParticles.attachSprite2d(new Sprite2d(this.iconTextField), city, 'miner_'+i);
        });

        // Paths to all miners
        const minerCoords = all_miners.map(city => [citiesKey[city].latitude, citiesKey[city].longitude,
            citiesKey[client_city].latitude, citiesKey[client_city].longitude])
        this.ch2_packageMinerPaths = new Paths(minerCoords, hero3d.globe.globeContainer, 50, .05, undefined, false);
        this.ch2_packageMinerPaths.setSounds(AUDIO_DLT1);
        this.ch2_packageMinerPaths.hashSoundDelay = 1;
        this.ch2_packageMinerPaths.setAllUniforms('color', new Vector4(...COLOR_BLUE_RGBA));
        this.ch2_packageMinerPaths.setAllUniforms('textureColor', new Vector4(...COLOR_GRAY_BLUE_RGBA));

        // Path to chosen miner
        const chosenMinerCoords = chosen_miners.map(city =>  [citiesKey[client_city].latitude,
            citiesKey[client_city].longitude, citiesKey[city].latitude, citiesKey[city].longitude]);
        this.ch2_packageChosenMinerPaths = new Paths(chosenMinerCoords, hero3d.globe.globeContainer, 50, .05);
        this.ch2_packageChosenMinerPaths.setSounds(AUDIO_DLT2, AUDIO_SL2);
        this.ch2_packageMinerPaths.hashSoundDelay = 1;
        this.ch2_packageChosenMinerPaths.setAllUniforms('color', new Vector4(...COLOR_BLUE_RGBA));
        this.ch2_packageChosenMinerPaths.setAllUniforms('textureColor', new Vector4(...COLOR_GRAY_BLUE_RGBA));
        this.ch2_packageChosenMinerPaths.addPathFollower(this.makeFollowSphere(.4, COLOR_BLUE), 0);
        this.ch2_packageChosenMinerPaths.addPathFollower(this.makeFollowSphere(.4, COLOR_BLUE), 1);
    }

