import {Page} from '../global/page';
import {SLUG_PROVIDE} from '../global/constants';
import {Slideshow} from '../components/slideshow';
import {MineGraph} from '../components/minegraph';
import {Accordion} from '../components/accordion';
//import {Calculate} from '../components/calculate';

export class Provide extends Page {
  slideshow: Slideshow;
  graph: MineGraph;
  accordion: Accordion;
  //calculate: Calculate;

  constructor() {
    super(document.querySelector('main.provide'));
    this.slideshow = new Slideshow(this.element.querySelector('.Slideshow'));
    this.graph = new MineGraph(this.element.querySelector('.rewards .graph'));
    this.accordion = new Accordion(this.element.querySelector('.Accordion'));
    //this.calculate = new Calculate(this.element.querySelector('.calculate'));
  }

  get slug() {
    return SLUG_PROVIDE;
  }

  destroy() {
    this.slideshow.destroy();
    this.graph.destroy();
    this.accordion.destroy();
    //this.calculate.destroy();
    super.destroy();
  }
}
