import {Page} from "../global/page";
import {SLUG_BUILD} from "../global/constants";
import {Accordion} from "../components/accordion";
import {Carousel} from "../components/carousel";
import {VideoOverlay} from "../components/videoOverlay";
import {Build3d} from "./build3d";
import {Window} from "../global/window";
import {clamp} from "../global/utils";
import {header} from "../header";

export class Build extends Page {
    accordionElement: HTMLElement;
    toolsAccordion: Accordion;
    carouselElements: NodeListOf<HTMLElement>;
    videoOverlayElements: NodeListOf<HTMLElement>;
    carousels: Array<Carousel> = [];
    videoOverlays: Array<VideoOverlay> = [];
    globe: HTMLElement;
    build3d: Build3d;
    overlay: HTMLElement;

    constructor() {
        super(document.querySelector('main'));
        this.element = document.querySelector('main.build');
        this.overlay = document.querySelector('.overlay');
        this.accordionElement = this.element.querySelector('.Accordion');
        this.toolsAccordion = new Accordion(this.accordionElement, [0, 1, 2], true);
        this.carouselElements = this.element.querySelectorAll('.Carousel');
        this.videoOverlayElements = this.element.querySelectorAll('.VideoOverlay');
        this.globe = this.element.querySelector('.build-globe');
        console.log(this.globe);
        this.init();
    }

    init = () => {
        let el;
        let settingMobile;

        for (el of this.carouselElements) {
            el = el as HTMLElement;
            settingMobile = el.dataset.settingMobile == "true";
            this.carousels.push(new Carousel(el, settingMobile));
        }

        for (el of this.videoOverlayElements) {
            el = el as HTMLElement;
            this.videoOverlays.push(new VideoOverlay(el, this.element));
        }
    }

    async animateIn() {
        await super.animateIn();
        //this.initBuild3d();
    }

    // initBuild3d() {
    //     this.build3d = new Build3d()
    //     this.build3d.init();
    // }

    parallax() {
        super.parallax();

        const perc = clamp(Window.lastScrollY / Window.height, 0, 1);
        const opacity = clamp(Window.lastScrollY / (Window.height * 2.5), 0, 1);

        this.overlay.style.opacity = `${opacity}`;
        this.globe.style.display = opacity > .7 ? 'none' : 'block';

        const percHeader = clamp(Window.lastScrollY / (Window.height/4), 0, 1);
        header.backgroundWrapper.style.opacity = `${percHeader}`;
    }

    get slug() {
        return SLUG_BUILD;
    }

    destroy() {
        this.toolsAccordion.destroy();
        //this.build3d.destroy();
        this.carousels.forEach((item) => { item.destroy() });
        this.videoOverlays.forEach((item) => { item.destroy() });
        super.destroy();
    }
}
