import {Page} from "../global/page";
import {SLUG_BLOG_SINGLE} from "../global/constants";
import {gsap} from "gsap";
import {Sharing} from "../global/sharing";
import {Carousel} from "../components/carousel";

export class BlogSingle extends Page {
    private speed: number = .5;
    networks: HTMLElement;
    toggle: HTMLElement;
    isSocialVisible: boolean;
    shareElement: HTMLElement;
    sharing: Sharing;
    relatedArticlesCarousel: HTMLElement;
    carousel: Carousel;

    constructor() {
      super(document.querySelector('main'));
      this.element = document.querySelector('main.blog-single');
      this.networks = this.element.querySelector('.metadata .networks');
      this.toggle = this.element.querySelector('.metadata .fold-out');
      this.shareElement = this.element.querySelector('.share');
      this.sharing = new Sharing(this.shareElement, 'hello');
      this.relatedArticlesCarousel = this.element.querySelector('.Carousel');

      if (this.relatedArticlesCarousel) {
          this.carousel = new Carousel(this.relatedArticlesCarousel, true);
      }

      this.init();
    }

    init = () => {
      this.toggle.addEventListener(`click`, this.onToggleClick);
      this.isSocialVisible = this.networks.classList.contains(`visible`);
    }

    onToggleClick = (event: Event) => {
      event.preventDefault();
      if (this.networks.classList.contains(`visible`)) {
        this.isSocialVisible = false;
        this.networks.classList.remove(`visible`);
      } else {
        this.networks.classList.add(`visible`);
        this.isSocialVisible = true;
      }
    }

    animateIn(speed = this.speed) {
        return new Promise((resolve) => {
            gsap.to(this.element, {
                duration: this.speed,
                opacity: 1,
                onComplete: () => {
                    resolve();
                }
            });
        });
    }

    get slug() {
        return SLUG_BLOG_SINGLE;
    }

    destroy() {
        this.toggle.removeEventListener(`click`, this.onToggleClick);
        this.sharing.destroy()

        if (this.relatedArticlesCarousel) {
            this.carousel.destroy();
        }

        super.destroy();
    }
};
