/*
A 2d dom element (icon). These are always attached to a 3d point in space.
 */

import {gsap} from "gsap";
import {preload} from "../global/preload";
import {audioController} from "../global/audioController";

export default class Sprite2d {
    public element: HTMLElement;
    public verticalLine: HTMLElement;
    public textField: HTMLElement;
    public width: number;
    public height: number;
    public xOffset: number = 0;
    public yOffset: number = 0;
    public needsUpdate: boolean;
    private animatedIn: boolean;
    private soundIn: string;
    private soundOut: string;
    private initHeight: number;
    private _scale: number = 1;
    private _pinHeight = 0;
    private id: string;

    constructor(element: HTMLElement, id: string = null, soundIn: string = null, soundOut: string = null) {
        this.id = id;
        this.soundIn = soundIn;
        this.soundOut = soundOut;
        this.element = element.parentElement.appendChild(element.cloneNode(true)) as HTMLElement;
        this.verticalLine = this.element.querySelector('.vertical-line');
        this.textField = this.element.querySelector('.text-field .text');
    }

    layout() {
        const style = getComputedStyle(this.element);
        this.width =  parseInt(style.width, 10);
        this.height = parseInt(style.height, 10);
        if (!this.initHeight) {
            this.initHeight = this.height;
        }
        this.xOffset = this.width / 2;
        this.yOffset = this.verticalLine ? 0 : this.height / 2;
        this._pinHeight = this.height;
    }

    set scale(scale: number) {
        this._scale = scale;
    }

    get scale() {
        return this._scale;
    }

    animateIn(speed: number = 1, delay: number = 0) {
        this.layout();
        this.needsUpdate = true;

        const onStart = (speed: number) => {
            if (this.soundIn && speed > 0 && !this.animatedIn) {
                audioController.play(this.soundIn);
            }
            this.animatedIn = true;
        }

        if (this.verticalLine || this.textField) {
            gsap.to(this.element, speed, {delay: delay, opacity: 1, display: 'block',
                onStart: onStart, onStartParams: [speed]});
        } else {
            gsap.set(this.element, {scale: .2, delay: delay})
            gsap.to(this.element, speed, {delay: delay, opacity: 1, scale: this.scale, display: 'block', ease: 'Back.easeOut',
                onStart: onStart, onStartParams: [speed]});
        }
    }

    animateOut(speed: number = 1, delay: number = 0) {

        const onStart = (speed: number) => {
            if (this.soundOut && speed > 0 && this.animatedIn) {
                audioController.play(this.soundOut);
            }
            this.animatedIn = false;
        }

        if (this.verticalLine || this.textField) {
            gsap.to(this.element, speed, {delay: delay, opacity: 0, display: 'none',
                onStart: onStart, onStartParams: [speed]} );
        } else {
            gsap.to(this.element, speed, {delay: delay, opacity: 0, scale: .2, display: 'none', ease: 'Back.easeOut',
                onStart: onStart, onStartParams: [speed]});
        }
    }

    pulse(speed: number = 1, delay: number = 0) {
        gsap.set(this.element, {scale: .5, delay: delay})
        gsap.to(this.element, speed, {delay: delay, opacity: 1, scale: this.scale, display: 'block', ease: 'Back.easeOut'});
    }

    set pinHeight(height: number) {
        this._pinHeight = height;
        this.element.style.height = height + 'px';
        this.element.style.marginTop = (-height) + 'px';
    }

    get pinHeight() {
        return this._pinHeight;
    }

    reset(delay: number = 0) {
        if (this.verticalLine) {
            gsap.set(this.verticalLine, {clearProps:"all", delay: delay});
            this.pinHeight = this.initHeight;
        }
    }

    setText(label: string) {
        if (this.textField) {
            this.textField.innerText = label;
        }
        return this;
    }
}