import Emitter from "@hellomonday/emitter";
import {gsap} from "gsap";

export const ANIMATION_DONE = 'animation_done';

export class Spritesheet extends Emitter{

    public element: HTMLElement;
    public duration: number;
    private url: string;
    private spriteWidth: number;
    private spriteHeight: number;
    private totalFrames: number;
    private totalWidth: number;

    constructor(element: HTMLElement) {
        super();
        this.element = element;
        this.url = this.element.getAttribute('data-src');
        this.spriteWidth = parseInt(this.element.getAttribute('data-spritesheet-width'));
        this.spriteHeight = parseInt(this.element.getAttribute('data-spritesheet-height'));
        this.totalFrames = parseInt(this.element.getAttribute('data-spritesheet-frames'));
        this.element.style.backgroundImage = "url('"+ this.url +"')";
        this.element.style.backgroundSize = (this.spriteWidth * this.totalFrames) + "px " + this.spriteHeight + "px";
        this.element.style.width = this.spriteWidth + 'px';
        this.element.style.height = this.spriteHeight + 'px';
        this.totalWidth = (this.totalFrames - 1) * this.spriteWidth;
        this.duration = this.totalFrames / 30;
    }

    play(delay: number = 0, speed: number = null, repeat: number = 0, onComplete: Function = null) {
        if (speed) {
            this.duration = speed;
        }
        gsap.set(this.element, {delay: delay, backgroundPosition: '0'});
        gsap.to(this.element, this.duration, {
            repeat: repeat,
            backgroundPosition: ("-"+ this.totalWidth+ "px"),
            ease: 'steps('+ (this.totalFrames - 1) +')',
            delay: delay,
            onComplete: () => {
                if (onComplete) {
                    onComplete();
                }
            }
        });
    }

    reset() {
        gsap.killTweensOf(this.element);
        gsap.set(this.element, {backgroundPosition: '0'});
    }
}