const vertex = `
    varying vec2 vUv;
    varying float vDistanceToCamera;
    void main()
    {
        vUv = uv;
        vec4 mvPosition = modelViewMatrix * vec4(position, 1.0);
        vec4 worldPosition = modelMatrix * vec4(position, 1.0);
        vDistanceToCamera = abs(distance(cameraPosition, vec3(worldPosition)));
        gl_Position = projectionMatrix * mvPosition;
    }
`;
const fragment = `
    #ifdef GL_ES
    precision highp float;
    #endif

    varying vec2 vUv;
    varying float vDistanceToCamera;

    uniform vec2 uvOffset;
    uniform float uTubeLength;
    uniform vec4 color;
    uniform vec4 textureColor;
    uniform sampler2D texture;
    uniform float offset;
    uniform float globalAlpha;
    uniform float textureVisibleAmount;
    uniform float textureAlphaAmount;
    uniform float featherAmount;
    uniform float reverseDirection;

    void main(void)
    {
        if (offset == 0.) 
            discard;
        float uv = float(vUv);
        const float hashDensity = 120.;
        float hashRepeat = uTubeLength * (hashDensity / vDistanceToCamera);
        vec4 t1 = texture2D(texture, ((uv + uvOffset) * hashRepeat));
        float uvDir = mix(1. - uv, uv, reverseDirection); 
        float alpha = mix(uv, 1., featherAmount) * step(1. - offset, uvDir);
        float textureAlpha = float(t1.a * alpha) * textureAlphaAmount;
        alpha = mix(textureAlpha, alpha, step(uv, 1. - textureVisibleAmount));
        vec3 finalColor = mix(t1.rgb * textureColor.rgb, color.rgb, step(uv, 1. - textureVisibleAmount));
        gl_FragColor = vec4(finalColor, alpha * globalAlpha); //t1
    }
`;

export default {vertex, fragment};
