import {Page} from "../global/page"
import {SLUG_ECOSYSTEM} from "../global/constants";
import Avatars from './ecosystemAvatars';
import {EcoGraph} from "../components/ecograph";
import {Carousel} from "../components/carousel";

export class Ecosystem extends Page {
  data: number[];

  avatars: Avatars;
  graph: EcoGraph;
  carousels: Array<Carousel> = [];

  constructor() {
    super(document.querySelector('main'));
    this.element = document.querySelector('main.ecosystem');

    this.avatars = new Avatars(this.element.querySelector('section.community'));
    this.fetchData().then(data => {
      this.graph = new EcoGraph(this.element.querySelector('section.graph'), data);
    });

    let el;
    let settingMobile;
    for (el of this.element.querySelectorAll('.Carousel')) {
        el = el as HTMLElement;
        settingMobile = el.dataset.settingMobile == "true";
        this.carousels.push(new Carousel(el, settingMobile));
    }
  }

  fetchData() {
    return fetch('/data/commits.json').then(response => response.json());
  }

  get slug() {
    return SLUG_ECOSYSTEM;
  }

  destroy() {
      this.avatars.destroy();
      this.graph.destroy();
      super.destroy();
  }
};
