import * as THREE from 'three';
// @ts-ignore
import { geoInterpolate } from 'd3-geo';
import {
    GLOBE_RADIUS,
    CURVE_MIN_ALTITUDE,
    CURVE_MAX_ALTITUDE,
    LOCALE,
    LOCALE_CHINESE,
    ALL_SLUGS,
    BASE_PATH, LOCALE_ENGLISH
} from './constants';
import {Vector2, Vector3} from "three";
import {PerspectiveCamera} from "three";
import coordPos from "@hellomonday/coord-pos";
import {Object3D} from "three";
import {Texture} from "three";
import {state} from "./state";

const DEGREE_TO_RADIAN = Math.PI / 180;

export function clamp(num:number, min:number, max:number) {
    return num <= min ? min : (num >= max ? max : num);
}

export function getSplineFromCoords(coords: Array<any>,
                                    curveMinAltitude: number = CURVE_MIN_ALTITUDE,
                                    curveMaxAltitude: number = CURVE_MAX_ALTITUDE) {
    const startLat = coords[0];
    const startLng = coords[1];
    const endLat = coords[2];
    const endLng = coords[3];

    // spline vertices
    const start = new Vector3(...coordPos(startLat, startLng, GLOBE_RADIUS));
    const end =  new Vector3(...coordPos(endLat, endLng, GLOBE_RADIUS));
    const altitude = clamp(start.distanceTo(end) * .75, curveMinAltitude, curveMaxAltitude);
    const interpolate = geoInterpolate([startLng, startLat], [endLng, endLat]);
    const midCoord1 = interpolate(0.25);
    const midCoord2 = interpolate(0.75);
    const mid1 = new Vector3(...coordPos(midCoord1[1], midCoord1[0], GLOBE_RADIUS + altitude));
    const mid2 = new Vector3(...coordPos(midCoord2[1], midCoord2[0], GLOBE_RADIUS + altitude));

    const spline = new THREE.CubicBezierCurve3(start, mid1, mid2, end);
    return {
        start,
        end,
        spline: spline
    };
}

export function projectVector(position: Vector3, camera: PerspectiveCamera, width: number, height: number) {
    let vector = new THREE.Vector3();
    vector.set( position.x, position.y, position.z );
    vector.project( camera );
    vector.x = (   vector.x + 1 ) * width  / 2 ;
    vector.y = ( - vector.y + 1 ) * height / 2 ;
    vector.z = 0;
    return vector;
}

export function loadJSON(url: string) : Promise<Array<any>> {
    return new Promise(resolve => {
        const req = new XMLHttpRequest();
        req.onreadystatechange = function() {
            if (this.readyState == 4 && this.status == 200) {
                const json = JSON.parse(this.responseText);
                resolve(json);
            }
        };
        req.open("GET", url, true);
        req.send();
    });
}

export function hexNumToStr(dec: number) {
    return '#' + dec.toString(16);
}

export function translatePoint(v: Vector3, object: Object3D) {
    let point: Vector3 = v.clone();
    let obj = object.clone();
    obj.updateMatrixWorld();
    obj.localToWorld(point);
    return point;
}

export function reversePath(array: Array<number>) {
    let reversePath = [];
    for (let i = 0; i < array.length; i+=3) {
        reversePath.unshift(array[i+2]);
        reversePath.unshift(array[i+1]);
        reversePath.unshift(array[i]);
    }
    return reversePath;
}

export function contains(a: Array<any>, obj: any) {
    for (var i = 0; i < a.length; i++) {
        if (a[i] === obj) {
            return true;
        }
    }
    return false;
}

export function loadTexture(url: string)  {
    return new Promise<Texture>((resolve, reject) => {
        let texture = new Texture();
        let img = new Image();
        img.onload = (data) => {
            texture.image = img;
            texture.needsUpdate = true;
            resolve(texture);
        }
        // on fail call reject
        img.src = url;
    })
}

export function setCookie(cookie: string) {
    document.cookie = cookie;
}

export function getCookie(name: string) {
    var cookieArr = document.cookie.split(";");
    for(var i = 0; i < cookieArr.length; i++) {
        var cookiePair = cookieArr[i].split("=");
        if(name == cookiePair[0].trim()) {
            return decodeURIComponent(cookiePair[1]);
        }
    }
    return null;
}

export function cubicOut(t: number) {
    var f = t - 1.0
    return f * f * f + 1.0
}

export function quadOut(t: number) {
    return -t * (t - 2.0)
}

export function getDynamicBasePath(url: string, slugs: Array<string>) {
    if (url.substr(-1) != '/') url += '/';
    let fullUrl = '';
    const urlParts = url.split('/');
    for (let i = 0; i < urlParts.length; i++) {
        const urlPart = urlParts[i];
        for (let j = 0; j < slugs.length; j++) {
            const slug = slugs[j].toLowerCase().replace('/', '').trim();
            const dir = urlPart.toLowerCase().trim();
            if (slug.length && slug == dir) {
                return fullUrl;
            }
        }
        fullUrl += urlPart;
        if (i < urlParts.length - 1) {
            fullUrl += '/';
        }
    }
    return fullUrl;
}

export function getLanguageCode(basePath: string) {
    // remove last slash of base path
    const base = basePath.replace(/\/$/, '');
    // get the string after last slash of base path
    const localeSlug = base.substring(base.lastIndexOf('/') + 1);
    return localeSlug === LOCALE_CHINESE ? LOCALE_CHINESE : LOCALE_ENGLISH;
}

export function rewriteRelLinks(links: NodeListOf<Element>) {
    const path = window.location.pathname.replace(BASE_PATH, '');
    const pathArray = path.split('/');
    let locale = BASE_PATH.split('/')[BASE_PATH.split('/').length-2] + '/';
    if (locale === '/') locale = '';

    // Build up relative path
    let relPath = '';
    for (let i=1; i< pathArray.length; i++) {
        relPath = relPath + '../';
    }

    // Rewrite urls
    let slug;
    let newPath;
    [...links].forEach((el) => {
        slug = el.parentElement.dataset.slug;

        // When no slug is found
        if (!slug) slug = '/';

        // Remove first slash
        if (slug.charAt(0) === '/') slug = slug.slice(1);

        if (slug === 'en/')
            newPath = relPath + "../" + slug;
        else
            newPath = relPath + slug.replace(locale, '');

        el.setAttribute('href', newPath);
    })
}

export function isTouch() {
    return ('ontouchstart' in window) ||
        (navigator.maxTouchPoints > 0) ||
        (navigator.msMaxTouchPoints > 0);
}
