export class Cities{

    public cn: any;
    public filecoin: any;
    public loaded: boolean;

    constructor() {
        this.addCity(CITY_KERIYA, 39.192981, 90.798783);
        this.addCity(CITY_SONGEA, -10.649220, 35.641817);
        this.addCity(CITY_WISNER, 40.919835, -91.823537);
        this.addCity(CITY_OUTER_QUEBEC, 49.0827035,-79.8104387);
        this.addCity(CITY_XIAOJIN, 31.216362, 101.155618);
        this.addCity(CITY_TAMENTIT, 27.877672, 0.646661);
    }

    public addCities(data: any) {
        for (let i = 0; i < data.length; i++) {
            this.addCity(data[i].name, data[i].latitude, data[i].longitude);
        }
    }

    public async load() {
        const assets3d = state.getValue(ASSETS_3D);
        this.cn = await loadJSON(assets3d.city_data_cn);
        this.filecoin = await loadJSON(assets3d.city_data_fc);
        return new Promise(resolve => {
            if (this.loaded) {
                resolve();
                return;
            }
            this.addCities(this.cn);
            this.addCities(this.filecoin);
            resolve();
        });
    }

    public addCity(name: string, lat: number, long: number) {
        const city: City = new City(name, lat, long);
        citiesKey[city.name] = city;
        return city;
    }
}
export const cities = new Cities();