/*
An item that can be attached to follow a Line or Curve path
 */

import {Curve, Object3D, Vector3} from "three";
import {gsap} from "gsap";
import {LinePath3} from "./LinePath3";

export class PathItem {
    public mesh: Object3D;
    private _progress: number;

    constructor(mesh: Object3D) {
        this.mesh = mesh;
        this.mesh.visible = false;
    }

    set progress(value: number) {
        this._progress = value;
        this.mesh.position.copy(this.position);
    }

    get progress() {
        return this._progress;
    }

    get position(): Vector3 {
        return new Vector3();
    }

    animateIn(speed: number = 1, delay: number = 0, repeat: number = 0, onComplete: Function = null) {
        gsap.set(this, {progress: 0, delay: delay});
        gsap.to(this, speed, {
            progress: 1,
            delay: delay,
            ease: 'Power1.easeInOut',
            repeat: repeat,
            onStart: () => {
                this.mesh.visible = true;
            },
            onComplete: () => {
                this.mesh.visible = false;
                if (onComplete) {
                    onComplete();
                }
            }
        });
    }

    animateOut(speed: number = 1, delay: number = 0) {
        gsap.to(this, speed, {
            progress: 0,
            delay: delay,
            ease: 'Power1.easeInOut'
        });
    }

    reset() {
        this.progress = 0;
        this.mesh.visible = false;
    }
}

export class SplinePathItem extends PathItem {
    private path: Curve<any>;

    constructor(mesh: Object3D, path: Curve<any>) {
        super(mesh);
        this.path = path;
        this.progress = 0;
    }

    get position(): Vector3 {
        return this.path.getPointAt(this.progress);
    }
}

export class LinePathItem extends PathItem {
    private path: LinePath3;

    constructor(mesh: Object3D, path: LinePath3) {
        super(mesh);
        this.path = path;
        this.progress = 0;
    }

    get position(): Vector3 {
        return this.path.getLinearPoint(this.progress);
    }
}
