/*
Parses the city model and exposes city-specific animations
 */

import {GLTFLoader} from "three/examples/jsm/loaders/GLTFLoader";
import {ambientLight, camera, rootMesh, scene} from "./hero3d";
import * as THREE from "three";
import {
    AdditiveBlending, CanvasTexture,
    DoubleSide, FrontSide,
    Mesh,
    MeshBasicMaterial,
    MeshStandardMaterial,
    Object3D
} from "three";
import {gsap} from "gsap";
import {Color} from "three";
import {ASSETS_3D, COLOR_BLUE, DAY, LANG_CODE, LOCALE_CHINESE, NIGHT} from "../global/constants";
import {overlayFactory} from "./overlayFactory";
import {translatePoint} from "../global/utils";
import Particles, {ParticleItem} from "./Particles";
import {state} from "../global/state";
import {AUDIO_FW3, audioController} from "../global/audioController";

// City-specific colors
export const DARK_CITY_ROAD_COLOR: number = 0x0F1318;
export const DARK_CITY_LAND_COLOR: number = 0x161C22;
export const LIGHT_CITY_ROAD_COLOR: number = 0xABABAB;
export const LIGHT_CITY_LAND_COLOR: number = 0xC5C5C5;
export const DARK_CITY_BUILDING_COLOR_TOP: string = '#56677c';
export const DARK_CITY_BUILDING_COLOR_BOTTOM: string = '#06080a';
export const LIGHT_CITY_BUILDING_COLOR_TOP: string = '#ffffff';
export const LIGHT_CITY_BUILDING_COLOR_BOTTOM: string = '#999999';

// City container
export const cityContainer: Object3D = new Object3D();

// 3d city model ID's
const ID_BRIDGE = 'BRIDGE';
const ID_ROADS = 'ROADS';
const ID_LANDMASS = 'LANDMASS';
const ID_BUILDINGS = 'CITY_UNTRIANGULATED';
const ID_CAMERA_TARGETS = 'CAMERA_TARGETS';
const ID_CAMERA_SOURCES = 'CAMERA_SOURCE';
const ID_NODES = 'NODES';
const ID_COIN_TARGET = 'coin_target';

// miner building
const ID_MINER_BUILDING_OUTSIDE = 'BUILDING_1_OUTSIDE';
const ID_MINER_BUILDING_INSIDE_BOT = 'BUILDING_1_INSIDE_BOT';
const ID_MINER_BUILDING_INSIDE_MID = 'BUILDING_1_INSIDE_MID';
const ID_MINER_BUILDING_INSIDE_TOP = 'BUILDING_1_INSIDE_TOP';
const ID_MINER_DESK = 'BUILDING_1_MINER';
const ID_BOX_FRONT = 'box_front';
const ID_BOX_BACK = 'box_back';
const ID_DRIVE = 'drive';
const ID_DISK_INNER = 'disk_inner';

// server building
const ID_SERVER_BUILDING_OUTSIDE = 'BUILDING_2_OUTSIDE';
const ID_SERVER_BUILDING_INSIDE = 'BUILDING_2_INSIDE';
const ID_SERVER_BUILDING_STAIRCASE = 'staircase';
const ID_SERVER_BUILDING_BOX = 'box';
const ID_SERVER_BUILDING_DRIVE_NULLS = 'drive-nulls';
const ID_SERVER_BUILDING_CABINET_FRONTS = 'cabinets_fronts';
const ID_SERVER_BUILDING_BACK_TECH = 'back_tech';
const ID_SERVER_BUILDING_RACK_SETS1 = 'RACKS_SET1';
const ID_SERVER_BUILDING_RACK_SETS2 = 'RACKS_SET2';
const ID_SERVER_BUILDING_RACK_SETS3 = 'RACKS_SET3';
const ID_SERVER_BUILDING_RACK_SETS4 = 'RACKS_SET4';

const SCALE = 10;
const CANVAS_TEXTURE_SIZE = 128;

export class CityScene {

    public meshScene: Object3D;
    public serverIconNull: Object3D;
    public loaded: boolean;

    private roads: Mesh;
    private landMass: Mesh;
    private bridge: Mesh;
    private buildings: Mesh;
    private spriteNulls: Object3D;
    private cameraSourceNulls: Object3D;
    private cameraTargetNulls: Object3D;

    private serverDriveMaterial: MeshBasicMaterial;
    private serverRackMaterial: MeshBasicMaterial;
    private serverBuildingInside: Object3D;
    private serverBuildingOutside: Mesh;
    private serverBuildingOutsideMaterial: MeshStandardMaterial;
    private cabinetFronts: Mesh;
    private cabinetFrontsMaterial: MeshBasicMaterial;

    private minerBuildingOutside: Mesh;
    private minerBuildingOutsideMaterial: MeshStandardMaterial;
    private minerBuildingTopFloor: Mesh;
    private minerBuildingTopFloorMaterial: MeshBasicMaterial;
    private minerBuildingDesk: Object3D;
    private minerBuildingDeskMaterials: Array<MeshBasicMaterial> = [];
    private minerBuildingDriveMaterials: Array<MeshBasicMaterial> = [];
    private minerBuildingNasFront: Mesh;
    private minerBuildingNasFrontMaterial: MeshBasicMaterial;
    private minerBuildingNasBack: Mesh;
    private minerBuildingNasBackMaterial: MeshBasicMaterial;
    private backTechMaterial: MeshBasicMaterial;

    private canvasTexture: CanvasTexture;
    private canvasTextureContext: CanvasRenderingContext2D;
    private cityBuildingColorTop: string = LIGHT_CITY_BUILDING_COLOR_TOP;
    private cityBuildingColorBottom: string = LIGHT_CITY_BUILDING_COLOR_BOTTOM;
    private cityBuildingColorStop: number;

    add() {
        if (!cityContainer.parent) {
            rootMesh.add(cityContainer)
        }
    }

    remove() {
        if (cityContainer.parent) {
            rootMesh.remove(cityContainer);
        }
    }

    async load() {
        const assets3d = state.getValue(ASSETS_3D);
        return new Promise(resolve => {
            if (this.loaded) {
                resolve();
                return;
            }
            const gltf_loader = new GLTFLoader();
            gltf_loader.load(
                assets3d.city_model_url,
                (gltf: any) => {
                    this.meshScene = gltf.scene;
                    this.canvasTextureContext = this.createCanvasTexture();
                    this.bridge = this.meshScene.getObjectByName(ID_BRIDGE) as Mesh;
                    if (this.bridge) {
                        this.bridge.material = new MeshStandardMaterial({
                            map: this.canvasTexture,
                            flatShading: true
                        });
                    }

                    this.roads = this.meshScene.getObjectByName(ID_ROADS) as Mesh;
                    if (this.roads) {
                        this.roads.material = new MeshBasicMaterial({
                            flatShading: true
                        });
                    }

                    this.buildings = this.meshScene.getObjectByName(ID_BUILDINGS) as Mesh;
                    this.buildings.material = new MeshStandardMaterial({
                        map: this.canvasTexture,
                        flatShading: true,
                        side: DoubleSide
                    });

                    this.landMass = this.meshScene.getObjectByName(ID_LANDMASS) as Mesh;
                    this.landMass.material = new MeshBasicMaterial();
                    this.cityBuildingColorStop = LANG_CODE === LOCALE_CHINESE ? .15 : .3;
                    this.parseNulls();
                    this.parseServerBuilding();
                    this.parseMinerBuilding();
                    this.animateMaterialColor(0, 0, NIGHT);
                    cityContainer.add(gltf.scene);
                    cityContainer.scale.set(SCALE, SCALE, SCALE);
                    this.loaded = true;
                    resolve();
                }
            );
        });
    }

    createCanvasTexture() {
        const canvas = document.createElement('canvas');
        canvas.classList.add('canvasTexture');
        canvas.width = CANVAS_TEXTURE_SIZE;
        canvas.height = CANVAS_TEXTURE_SIZE;
        this.canvasTexture = new THREE.CanvasTexture(canvas);
        this.canvasTexture.flipY = true;
        //this.canvasTexture.encoding = THREE.sRGBEncoding;
        //document.body.appendChild(canvas);
        return canvas.getContext('2d');
    }

    parseNulls() {
        this.cameraSourceNulls = this.meshScene.getObjectByName(ID_CAMERA_SOURCES);
        this.cameraTargetNulls = this.meshScene.getObjectByName(ID_CAMERA_TARGETS);
        this.serverIconNull = this.meshScene.getObjectByName(ID_COIN_TARGET);
        this.spriteNulls = this.meshScene.getObjectByName(ID_NODES);
        const particleItems = this.spriteNulls.children.map((spriteNull: Object3D, index) =>
            new ParticleItem('citySpot' + index, translatePoint(spriteNull.position, spriteNull.parent)));
        overlayFactory.ch1_citySpotsParticles = new Particles(particleItems, COLOR_BLUE, cityContainer, false );
    }

    parseMinerBuilding() {
        // Desk Building
        this.minerBuildingOutside = this.meshScene.getObjectByName(ID_MINER_BUILDING_OUTSIDE).children[0] as Mesh;
        this.minerBuildingOutside.material = this.minerBuildingOutsideMaterial = new MeshStandardMaterial({
            map: this.canvasTexture,
            flatShading: true,
            side: DoubleSide,
            transparent: true
        });
        this.minerBuildingOutside.renderOrder = 2;

        // Desk Building Floors Material
        const minerBuildingFloorsBot: Mesh = this.meshScene.getObjectByName(ID_MINER_BUILDING_INSIDE_BOT) as Mesh;
        const srcMaterial: MeshStandardMaterial = minerBuildingFloorsBot.material as MeshStandardMaterial;
        srcMaterial.map.encoding = THREE.LinearEncoding;
        minerBuildingFloorsBot.material = new MeshBasicMaterial({
            map: srcMaterial.map,
        });

        const minerBuildingFloorsMid: Mesh = this.meshScene.getObjectByName(ID_MINER_BUILDING_INSIDE_MID) as Mesh;
        const srcMaterial1: MeshStandardMaterial = minerBuildingFloorsMid.material as MeshStandardMaterial;
        srcMaterial1.map.encoding = THREE.LinearEncoding;
        minerBuildingFloorsMid.material = new MeshBasicMaterial({
            map: srcMaterial1.map,
        });

        this.minerBuildingTopFloor = this.meshScene.getObjectByName(ID_MINER_BUILDING_INSIDE_TOP) as Mesh;
        const srcMaterial2: MeshStandardMaterial = this.minerBuildingTopFloor.material as MeshStandardMaterial;
        srcMaterial2.map.encoding = THREE.LinearEncoding;
        this.minerBuildingTopFloor.material = this.minerBuildingTopFloorMaterial = new MeshBasicMaterial({
            transparent: true,
            depthTest: false,
            opacity: 0,
            map: srcMaterial2.map,
        });

        // Miner Building miner desk
        this.minerBuildingDesk = this.meshScene.getObjectByName(ID_MINER_DESK);
        this.minerBuildingDesk.visible = false;
        this.meshScene.remove(this.minerBuildingDesk);

        this.minerBuildingDesk.traverse((child) => {
            if (child instanceof THREE.Mesh) {
                let srcMaterial: MeshStandardMaterial = child.material as MeshStandardMaterial;
                let minerMaterial: MeshBasicMaterial = new MeshBasicMaterial({
                    map: srcMaterial.map,
                    flatShading: true,
                    transparent: true,
                    opacity: 0
                });
                if (child.name == ID_BOX_FRONT) {
                    this.minerBuildingNasFront = child;
                    this.minerBuildingNasFrontMaterial = minerMaterial;
                }
                else if (child.name == ID_BOX_BACK) {
                    this.minerBuildingNasBack = child;
                    this.minerBuildingNasBackMaterial = minerMaterial;
                    this.minerBuildingNasBackMaterial.side = THREE.DoubleSide;
                }
                else if (child.name == ID_DRIVE || child.name == ID_DISK_INNER) {
                    minerMaterial.blending = AdditiveBlending;
                    minerMaterial.depthTest = false;
                    minerMaterial.side = DoubleSide;
                    child.renderOrder = 1;
                    this.minerBuildingDriveMaterials.push(minerMaterial);
                } else {
                    this.minerBuildingDeskMaterials.push(minerMaterial);
                }
                child.material = minerMaterial;
                //@ts-ignore
                if (child.material.map) { child.material.map.encoding = THREE.LinearEncoding;};
            }
        });
    }

    parseServerBuilding() {
        // Server building outside
        this.serverBuildingOutside = this.meshScene.getObjectByName(ID_SERVER_BUILDING_OUTSIDE).children[0] as Mesh;
        this.serverBuildingOutsideMaterial = new MeshStandardMaterial({
            map: this.canvasTexture,
            transparent: true,
            flatShading: true
        });
        this.serverBuildingOutside.material = this.serverBuildingOutsideMaterial;

        // Server building inside - convert to basic materials
        this.serverBuildingInside = this.meshScene.getObjectByName(ID_SERVER_BUILDING_INSIDE);
        this.serverBuildingInside.visible = false;
        this.meshScene.remove(this.serverBuildingInside);

        const drive = this.meshScene.getObjectByName(ID_DRIVE) as Mesh;
        const srcDriveMaterial = drive.material as MeshStandardMaterial;
        srcDriveMaterial.map.encoding = THREE.LinearEncoding;

        this.serverDriveMaterial = new MeshBasicMaterial({
            map: srcDriveMaterial.map,
            flatShading: true
        });
        drive.material = this.serverDriveMaterial;

        this.serverBuildingInside.traverse((child) => {
            if (child instanceof THREE.Mesh) {
                if (child.name == ID_SERVER_BUILDING_STAIRCASE) {
                    let srcMaterial: MeshStandardMaterial = child.material as MeshStandardMaterial;
                    child.material = new MeshBasicMaterial({map: srcMaterial.map});
                }
                // Rack
                if (child.name == ID_SERVER_BUILDING_BOX) {
                    if (!this.serverRackMaterial) {
                        let srcMaterial: MeshStandardMaterial = child.material as MeshStandardMaterial;
                        this.serverRackMaterial = new MeshBasicMaterial({map: srcMaterial.map});
                        this.serverRackMaterial.side = FrontSide;
                        this.serverRackMaterial.transparent = true;
                        this.serverRackMaterial.blending = AdditiveBlending;
                        this.serverRackMaterial.opacity = .2;
                    }
                    child.material = this.serverRackMaterial;
                    child.renderOrder = 5;
                }
                else if (child.name == ID_SERVER_BUILDING_CABINET_FRONTS) {
                    let srcMaterial: MeshStandardMaterial = child.material as MeshStandardMaterial;
                    this.cabinetFronts = child;
                    this.cabinetFrontsMaterial = new MeshBasicMaterial({map: srcMaterial.map});
                    child.material = this.cabinetFrontsMaterial;
                    child.renderOrder = 10;
                }
                else if (child.name == ID_SERVER_BUILDING_BACK_TECH) {
                    let srcMaterial: MeshStandardMaterial = child.material as MeshStandardMaterial;
                    this.backTechMaterial = new MeshBasicMaterial({map: srcMaterial.map});
                    child.material = this.backTechMaterial;
                }
                else if (child.name != ID_DRIVE) {
                    let srcMaterial: MeshStandardMaterial = child.material as MeshStandardMaterial;
                    child.material = new MeshBasicMaterial({map: srcMaterial.map});
                }
                //@ts-ignore
                if (child.material.map) { child.material.map.encoding = THREE.LinearEncoding;};
            } else if (child instanceof Object3D) {
                if (child.name == ID_SERVER_BUILDING_DRIVE_NULLS) {
                    child.children.forEach((driveNull) => {
                        driveNull.add(drive.clone(true));
                    });
                }
            }
        });
    }

    fadeMinerBuildingWall(delay: number, speed: number = 1) {
        gsap.to(this.minerBuildingOutsideMaterial, speed, {opacity: 0, delay: delay, onComplete:() => {
                this.minerBuildingOutside.visible = false;
            }});
        gsap.to(this.minerBuildingTopFloorMaterial, speed, {opacity: 1, delay: delay});
    }

    hideMinerBuildingFloors(delay: number, speed: number = 1) {
        gsap.to(this.minerBuildingTopFloorMaterial, speed, {opacity: 0, delay: delay});
    }

    showMiner(delay: number) {
        this.meshScene.add(this.minerBuildingDesk);
        this.minerBuildingDesk.visible = true;
        gsap.to([this.minerBuildingDeskMaterials,
            this.minerBuildingNasFrontMaterial,
            this.minerBuildingNasBackMaterial],
            .5, {opacity: 1, delay: delay});

        audioController.play(AUDIO_FW3, delay + 2);
        gsap.to([this.minerBuildingNasFrontMaterial], .5, {opacity: 0, delay: delay + 2, onComplete: () => {
            this.minerBuildingNasFront.visible = false;
            }});
        gsap.to([this.minerBuildingNasBackMaterial], .5, {opacity: .1, delay: delay + 2});
        gsap.to([this.minerBuildingDriveMaterials], .5, {opacity: .1, delay: delay + 2});
    }

    hideMiner() {
        this.minerBuildingDesk.visible = false;
        this.meshScene.remove(this.minerBuildingDesk);

        gsap.set([this.minerBuildingDeskMaterials,
                this.minerBuildingNasFrontMaterial,
                this.minerBuildingNasBackMaterial], {opacity: 0});

        gsap.set([this.minerBuildingNasFrontMaterial,
            this.minerBuildingNasBackMaterial], {opacity: 0});
        gsap.set([this.minerBuildingDriveMaterials], {opacity: 0});
    }

    hideDrives(delay: number) {
        gsap.to([this.minerBuildingNasFrontMaterial, this.minerBuildingNasBackMaterial], .25, {opacity: 1, delay: delay, onStart: () => {
                this.minerBuildingNasFront.visible = true;
            }});
        gsap.to([this.minerBuildingDriveMaterials], .25, {opacity: 0, delay: delay});
    }

    fadeServerBuildingWall(delay: number, speed: number = 1) {
        this.serverBuildingInside.visible = true;
        this.meshScene.add(this.serverBuildingInside);
        gsap.to( this.serverBuildingOutsideMaterial, speed, {opacity: .25, delay: delay}); //.1
    }

    hideServerBuildingWall(delay: number, speed: number) {
        this.serverBuildingInside.visible = true;
        this.meshScene.add(this.serverBuildingInside);
        gsap.to( this.serverBuildingOutsideMaterial, speed, {opacity: 0, delay: delay, onComplete:() => {
                this.serverBuildingOutside.visible = false;
            }});
    }

    hideRacksSets() {
        this.serverBuildingInside.getObjectByName(ID_SERVER_BUILDING_RACK_SETS1).visible = false;
        this.serverBuildingInside.getObjectByName(ID_SERVER_BUILDING_RACK_SETS2).visible = false;
        this.serverBuildingInside.getObjectByName(ID_SERVER_BUILDING_RACK_SETS3).visible = false;
        this.serverBuildingInside.getObjectByName(ID_SERVER_BUILDING_RACK_SETS4).visible = false;
    }

    showRacksSets() {
        this.serverBuildingInside.getObjectByName(ID_SERVER_BUILDING_RACK_SETS1).visible = true;
        this.serverBuildingInside.getObjectByName(ID_SERVER_BUILDING_RACK_SETS2).visible = true;
        this.serverBuildingInside.getObjectByName(ID_SERVER_BUILDING_RACK_SETS3).visible = true;
        this.serverBuildingInside.getObjectByName(ID_SERVER_BUILDING_RACK_SETS4).visible = true;
    }

    showServerBuildingWall(delay: number, speed: number) {
        gsap.to( this.serverBuildingOutsideMaterial, speed, {opacity: 1, delay: delay,
            onStart: () => {
                this.serverBuildingOutside.visible = true;
            },
            onComplete: () => {
                this.serverBuildingInside.visible = false;
            }
        });
    }

    showServerRacks(delay: number, speed: number) {
        gsap.to(this.cabinetFrontsMaterial, speed, {delay: delay, opacity: 0, onStart: () => {
                this.cabinetFronts.visible = true;
                this.cabinetFrontsMaterial.transparent = true;
                this.cabinetFrontsMaterial.depthTest = false;
                this.showRacksSets();
                audioController.play(AUDIO_FW3);
            }, onComplete: () => {
                this.cabinetFronts.visible = false;
                this.cabinetFrontsMaterial.transparent = false;
                this.cabinetFrontsMaterial.depthTest = true;
            }});
    }

    hideServerRacks(delay: number, speed: number) {
        gsap.to(this.cabinetFrontsMaterial, speed, {delay: delay, opacity: 1, onStart: () => {
                this.cabinetFronts.visible = true;
                this.cabinetFrontsMaterial.transparent = true;
                this.cabinetFrontsMaterial.depthTest = false;
            }, onComplete: () => {
                this.cabinetFrontsMaterial.transparent = false;
                this.cabinetFrontsMaterial.depthTest = true;
            }});
        gsap.to(this.serverDriveMaterial, speed, {delay: delay, opacity: 0});
    }

    activateDrives(speed: number = 1, delay: number = 0) {
        gsap.to( [this.serverDriveMaterial.color, this.backTechMaterial.color], speed, {
            r: .22, g: .56, b: .96,
            delay: delay
        });
    }

    deactivateDrives() {
        gsap.set( [this.serverDriveMaterial.color, this.backTechMaterial.color], {r: .098, g: .12, b: .16});
    }
    
    reset() {
        this.serverBuildingOutside.visible = true;
        gsap.set(cityScene.serverBuildingOutsideMaterial, {opacity: 1});
        gsap.set(cityScene.minerBuildingOutsideMaterial, {opacity: 1});
        this.minerBuildingOutside.visible = true;
        gsap.set([this.minerBuildingTopFloorMaterial, this.minerBuildingOutsideMaterial], {opacity: 1});
        this.hideMiner();
        this.hideRacksSets();
        this.deactivateDrives();
        this.hideServerRacks(0,0);
    }

    animateMaterialColor(speed: number, delay: number, dayOrNight: String) {
        let buildingColorTop;
        let buildingColorBottom;
        let landColor;
        let roadColor;
        if (dayOrNight == DAY) {
            buildingColorTop = LIGHT_CITY_BUILDING_COLOR_TOP;
            buildingColorBottom = LIGHT_CITY_BUILDING_COLOR_BOTTOM;
            roadColor = new Color(LIGHT_CITY_ROAD_COLOR);
            landColor = new Color(LIGHT_CITY_LAND_COLOR);
        } else {
            buildingColorTop = DARK_CITY_BUILDING_COLOR_TOP;
            buildingColorBottom = DARK_CITY_BUILDING_COLOR_BOTTOM;
            roadColor = new Color(DARK_CITY_ROAD_COLOR);
            landColor = new Color(DARK_CITY_LAND_COLOR);
        }

        // roadColor.convertSRGBToLinear();
        // landColor.convertSRGBToLinear();

        //gsap.to( [this.serverBuildingOutsideMaterial.color, this.minerBuildingOutsideMaterial.color], speed,
            //{r: buildingColor.r, g: buildingColor.g, b: buildingColor.b, delay: delay});

        gsap.to(this, speed, {
            cityBuildingColorTop: buildingColorTop,
            cityBuildingColorBottom: buildingColorBottom,
            delay: delay,
            onUpdate: () => {
                this.updateCanvasTextureGradient();
            }
        });

        //@ts-ignore
        gsap.to( this.landMass.material.color, speed, {r: landColor.r, g: landColor.g, b: landColor.b, delay: delay});

        // if (this.bridge) {
        //     //@ts-ignore
        //     //gsap.to(this.bridge.material.color, speed, {r: buildingColor.r, g: buildingColor.g, b: buildingColor.b, delay: delay});
        // }

        if (this.roads) {
            //@ts-ignore
            gsap.to( this.roads.material.color, speed, {r: roadColor.r, g: roadColor.g, b: roadColor.b, delay: delay});
        }
    }

    updateCanvasTextureGradient() {
        this.canvasTexture.needsUpdate = true;
        const ctx = this.canvasTextureContext;
        ctx.clearRect(0,0, CANVAS_TEXTURE_SIZE, CANVAS_TEXTURE_SIZE)
        const gradient = ctx.createLinearGradient(0,0, 0, CANVAS_TEXTURE_SIZE);
        gradient.addColorStop(0, this.cityBuildingColorBottom);
        gradient.addColorStop(this.cityBuildingColorStop, this.cityBuildingColorTop);
        gradient.addColorStop(1, this.cityBuildingColorTop);
        ctx.fillStyle = gradient;
        ctx.fillRect(0,0, CANVAS_TEXTURE_SIZE, CANVAS_TEXTURE_SIZE);

    }

    getSpriteNulls() {
        return this.spriteNulls.children;
    }

    getCamera(index: number, scale: number = SCALE) {
        const targetObj = this.cameraTargetNulls.getObjectByName('cam'+(index + 1)+'_target');
        const sourceObj = this.cameraSourceNulls.getObjectByName('cam'+(index + 1));
        const target = targetObj ? translatePoint( targetObj.position, targetObj.parent).multiplyScalar(scale) : null;
        const src = sourceObj ? translatePoint( sourceObj.position, sourceObj.parent).multiplyScalar(scale) : null;
        return {
            src: src,
            target: target
        };
    }
}

export const cityScene = new CityScene();
