import {gsap} from "gsap";
import {EVENT_RESIZE, Window} from "../global/window";
import {emitter} from "../global/emitter";

export class StoreCarousel {
    public element: HTMLElement;
    private list: HTMLElement;
    private dataItemWrapper: HTMLElement;
    private icon: HTMLElement;
    private dataItems: NodeListOf<HTMLElement>;
    private mediaItems: NodeListOf<HTMLElement>;
    private currIndex: number;
    private listHeight: number;
    private itemHeight: number;
    private speed: number;
    private showDuration: number;
    private delayedCall: any;

    constructor(element: HTMLElement) {
        this.element = element;
        this.list = this.element.querySelector('.list');
        this.dataItemWrapper = this.element.querySelector('.data-items');
        this.icon = this.element.querySelector('.icon');
        this.dataItems = this.element.querySelectorAll('.data-items li');
        this.mediaItems = this.element.querySelectorAll('.media-items li');
        this.speed = .3;
        this.showDuration = 2;

        emitter.on(EVENT_RESIZE, this.layout);
        this.init();
    }

    private init() {
        this.currIndex = Math.round(this.dataItems.length/4) - 1;

        this.dataItems[this.currIndex].classList.add('active');
        this.mediaItems[this.currIndex].style.opacity = '1';

        this.layout();

        for (const item of this.dataItems) {
            item.addEventListener('click', this.onItemClick.bind(this));
        }

        this.autoNext();
    }

    private onItemClick(e: any) {
        let currIndex = 0;
        let newIndex = 0;

        this.delayedCall.kill();

        // Next item
        this.dataItems.forEach((el, index)=> {
            if (el === e.target) newIndex = index;

            if (el.classList.contains('active')) {
                currIndex = index;
                el.classList.remove('active');
            }
        })

        // Switch active states
        e.target.classList.add('active');
        this.showMedia(parseInt(e.target.dataset.index));

        // Direction
        if (newIndex < currIndex) {
            this.goDown(currIndex - newIndex);
        } else if (newIndex > currIndex) {
            this.goUp(newIndex - currIndex);
        }
    }

    private setTransitioning(enabled: boolean) {
        if (enabled)
            this.element.classList.add('transitioning');
        else
            this.element.classList.remove('transitioning');
    }

    private autoNext() {
        const activeItem = [...this.dataItems].filter(el => {
            if (el.classList.contains('active')) return el;
        })[0] as HTMLElement;

        this.showMedia(parseInt(activeItem.dataset.index));

        this.delayedCall = gsap.delayedCall(this.showDuration, ()=>{
            activeItem.classList.remove('active');
            activeItem.nextElementSibling.classList.add('active');
            this.goUp(1, true);
        })
    }

    private goUp(steps: number, auto: boolean = false) {
        this.setTransitioning(true);

        // Transition
        gsap.to(this.list, {
            duration: this.speed,
            top: -(this.itemHeight * steps),
            ease: 'Power4.easeOut',
            onComplete: () => {
                this.list = this.element.querySelector('.list');

                // Move list items
                for (let i = 0; i < steps; i ++) {
                    this.dataItems = this.element.querySelectorAll('.data-items li');
                    this.list.appendChild(this.dataItems[0]);
                }

                // Set list position before transition
                gsap.set(this.list, { top: 0 });

                this.autoNext();
                this.setTransitioning(false);
            }
        });
    }

    private goDown(steps: number) {
        this.setTransitioning(true);

        // Move list items
        for (let i = 0; i < steps; i ++) {
            this.list = this.element.querySelector('.list');
            this.dataItems = this.element.querySelectorAll('.data-items li');
            this.list.insertBefore(this.dataItems[this.dataItems.length-1], this.list.firstChild)
        }

        // Set list position before transition
        gsap.set(this.list, { top: -(this.itemHeight * steps) });

        // Transition
        gsap.to(this.list, {
            duration: this.speed,
            top: 0,
            ease: 'Power4.easeOut',
            onComplete: () => {
                this.list = this.element.querySelector('.list');
                this.dataItems = this.element.querySelectorAll('.data-items li');

                this.autoNext();
                this.setTransitioning(false);
            }
        });
    }

    private showMedia(index: number) {
        gsap.to(this.mediaItems, {duration: this.speed, opacity: 0});
        gsap.to(this.mediaItems[index], {duration: this.speed, opacity: 1});
    }

    private layout = () => {
        this.listHeight = this.list.getBoundingClientRect().height;
        this.itemHeight = this.dataItems[0].getBoundingClientRect().height;

        this.dataItemWrapper.style.height = `${this.listHeight / 2}px`;

        if (this.dataItems.length/2 % 2 == 0) {
            this.dataItemWrapper.style.top = `${this.itemHeight / 2}px`;
            this.icon.style.marginTop = `-${this.itemHeight / 2}px`;
        } else {
            this.icon.style.marginTop = `0px`;
        }
    }

    public destroy() {
        emitter.off(EVENT_RESIZE, this.layout);
    }
}
