/*
An extruded tube that's used to create curves over the globe or in the city.
 */

import {
    Curve,
    Mesh,
    Object3D,
    ShaderMaterial,
    TubeBufferGeometry,
    Vector3
} from "three";
import {getSplineFromCoords} from "../global/utils";
import {LinePath3} from "./LinePath3";

const TUBE_RADIUS_SEGMENTS = 5;
const DEFAULT_TUBE_RADIUS = .1;

export class Tube {
    public path: Curve<any>;
    public mesh: Mesh;
    public material: ShaderMaterial;
    public geometry: TubeBufferGeometry;
    public nulls: Array<Object3D> = [];

    attachNulls(coords: Array<any>) {
        for (let i = 0; i < coords.length; i+=3) {
            let obj = new Object3D();
            this.mesh.add(obj);
            obj.position.set(coords[i], coords[i+1], coords[i+2]);
            this.nulls.push(obj);
        }
    }
}

export class SplineTube extends Tube {
    constructor(coords: Array<any>,
                material: ShaderMaterial,
                radius: number = DEFAULT_TUBE_RADIUS,
                curveMinAltitude: number = undefined,
                curveMaxAltitude: number = undefined) {
        super();
        const { spline } = getSplineFromCoords(coords, curveMinAltitude, curveMaxAltitude);
        this.path = spline;
        this.geometry = new TubeBufferGeometry(this.path, 50, radius, TUBE_RADIUS_SEGMENTS, false);
        this.mesh = new Mesh(this.geometry, material);
        this.material = material;
        this.attachNulls(coords);
    }
}

export class LineTube extends Tube {
    constructor(coords: Array<any>, material: ShaderMaterial, radius: number = DEFAULT_TUBE_RADIUS) {
        super();
        let pathCoords = [];
        for (let i = 0; i < coords.length; i+=3) {
            pathCoords.push(new Vector3(coords[i], coords[i+1], coords[i+2]));
        }
        this.path = new LinePath3(pathCoords);
        const segments = (pathCoords.length - 1);
        //material.uniforms.uvRepeat.value.x = material.uniforms.uvRepeat.value.x * (this.path.getLength() / 100);
        this.geometry = new TubeBufferGeometry(this.path, segments, radius, TUBE_RADIUS_SEGMENTS, false);
        this.mesh = new Mesh(this.geometry, material);
        this.mesh.renderOrder = 5;
        this.material = material;
        this.attachNulls(coords);
    }
}
