/*
Entry point application for the 3d experience.
 */

import * as THREE from 'three';
import {cityScene} from './cityScene';
import {EVENT_RESIZE, Window} from "../global/window";
import {emitter} from "../global/emitter";
import {
    Object3D,
    PerspectiveCamera,
    Vector3,
    WebGLRenderer
} from "three";
import {gsap, TweenMax} from "gsap";
import {Globe, GLOBE_EASE_AMOUNT} from "./globe";
import {Timer} from "./timer";
import {cities} from "./cities";
import {overlayFactory} from "./overlayFactory";
import {state} from "../global/state";
import {ASSETS_3D, CHAPTERS_3D, DAY, LOCALE_CHINESE, TIME_OF_DAY} from "../global/constants";
import {goto_landing, hideIntroPaths, showIntroPaths} from "./animations";
import {init as initAnimations} from "./animations";
import {preload} from "../global/preload";
import {WEBGL} from "three/examples/jsm/WebGL";
import {AUDIO_CA1, AUDIO_CHA, AUDIO_CW, AUDIO_MA, AUDIO_MW, audioController} from "../global/audioController";
//import {OrbitControls} from "three/examples/jsm/controls/OrbitControls";

// 3d specific colors
export const DARK_SCENE_BG: number = 0x070B0E;
export const DARK_AMBIENT_LIGHT_CITY: number = 0x666666;
export const DARK_AMBIENT_LIGHT_GLOBE: number = 0x060606;
export const DARK_POINT_LIGHT_CITY: number = 0xffffff; //5A7897
export const DARK_POINT_LIGHT_GLOBE: number = 0xffffff;
export const DARK_POINT_LIGHT_INT_CITY: number = .8;
export const DARK_POINT_LIGHT_INT_GLOBE: number = 1;

export const LIGHT_SCENE_BG: number = 0xF9F9F9;
export const LIGHT_AMBIENT_LIGHT_CITY: number = 0xeeeeee;
export const LIGHT_AMBIENT_LIGHT_GLOBE: number = 0xeeeeee;
export const LIGHT_POINT_LIGHT: number = 0xfefefe;
export const LIGHT_POINT_LIGHT_INT_CITY: number = .4;
export const LIGHT_POINT_LIGHT_INT_GLOBE: number = .2;

// Global scene elements
export const scene = new THREE.Scene();
export const rootMesh = new THREE.Mesh(new THREE.Geometry());
export const camera = new PerspectiveCamera(35, 1, .1, 3000);
export const cameraContainer: Object3D =  new Object3D();
export const ambientLight = new THREE.AmbientLight( LIGHT_AMBIENT_LIGHT_GLOBE );
export const pointLight = new THREE.PointLight( LIGHT_POINT_LIGHT, LIGHT_POINT_LIGHT_INT_GLOBE);
export const timer = new Timer();
export const DEFAULT_HOVER_EASE = 60;

export class Hero3d {
    public element: HTMLElement;
    public initialized: boolean;
    public iconsContainer: HTMLElement;
    public contentContainer: HTMLElement;
    public hoverLookAt: Vector3 = new Vector3(0,0,0);
    public lookAtPosition: Vector3 = new Vector3(0,0,0);
    public hoverEase: number = DEFAULT_HOVER_EASE;
    public globe: Globe = new Globe();
    public loaded: boolean;
    public dpr: number;

    private webglContainer: HTMLElement;
    private fadePlane: HTMLElement;
    private globeStill: HTMLImageElement;
    private renderer: WebGLRenderer;
    private bgColor: string;
    private _myReq: number;
    private mouseX: number = 0;
    private mouseY: number = 0;
    private lastRootExport: any;
    private hoverDistance: Vector3 = new Vector3(0,0,0);
    private hoverLookAtDistance: Vector3 = new Vector3(0,0, 0);
    private imageLoaded: boolean;
    //private orbitControls: OrbitControls;

    constructor() {
        this.element = document.querySelector('.hero3d');
        this.iconsContainer = this.element.querySelector('.icons');
        this.fadePlane = this.element.querySelector('.fade-plane');
        this.webglContainer = this.element.querySelector('.webgl');
        this.contentContainer = this.element.querySelector('.content');
        this.globeStill = this.element.querySelector('.globe-still');
    }

    public init() {
        if (!this.initialized) {
            const mainEl = document.querySelector('main');
            state.setValue(CHAPTERS_3D, JSON.parse(mainEl.dataset.chapters));
            const assets3d: any = {};
            Array.from(mainEl.querySelectorAll('.data-asset3d')).forEach((assetEl) => {
                const el = assetEl as HTMLElement;
                assets3d[el.dataset.key] = el.dataset.src;
            });
            state.setValue(ASSETS_3D, assets3d);
            state.setValue(TIME_OF_DAY, DAY);
            
            if (WEBGL.isWebGLAvailable()) {
                THREE.Cache.enabled = true;
                initAnimations();
                timer.setData(state.getValue(CHAPTERS_3D));
                this.globe.init(camera, rootMesh);
                this.setupEnvironment();
                this.initialized = true;
            }
        }
        this.setupEvents();
    }

    private setupEnvironment() {
        scene.add(rootMesh);
        this.renderer = new WebGLRenderer({ antialias: true });
        this.webglContainer.appendChild(this.renderer.domElement);
        this.layout();
        this.renderer.setPixelRatio(this.dpr);
        scene.fog = new THREE.Fog(LIGHT_SCENE_BG, 100, -100);
        scene.background = new THREE.Color(this.bgColor);
        scene.add( ambientLight );
        cameraContainer.add(camera);
        scene.add( cameraContainer );
        scene.add( pointLight );
        //this.orbitControls = new OrbitControls( camera, this.renderer.domElement );
    }

    private setupEvents() {
        emitter.on(EVENT_RESIZE, this.layout);
        document.addEventListener( 'mousemove', this.onMouseMove);
    }

    private onMouseMove = (event: MouseEvent) => {
        this.mouseX = ( event.clientX - Window.halfWidth );
        this.mouseY = ( event.clientY - Window.halfHeight );
    }

    public reset() {
        overlayFactory.hideAll();
        gsap.set(this.globeStill, {scale: .95, opacity: .01});
        gsap.set(this.webglContainer, {opacity: 0});
        timer.reset();
    }

    public loadImage() {
        const assets3d = state.getValue(ASSETS_3D);
        this.globeStill.dataset.src = assets3d.globe_still_url;
        return preload.loadImage(this.globeStill);
    }

    async load() {
        if (!this.initialized) {
            return;
        }

        if (!this.loaded) {
            await cities.load();
            await this.globe.load();
            await cityScene.load();
            await overlayFactory.load();
            await audioController.load();
            this.loaded = true;
            overlayFactory.init();
        }
        this.resetHoverEase();
        this.animateInWebgl();
    }

    public animateIn(delay: number = 0, speed: number = 1) {
        gsap.to(this.globeStill, speed, {scale: 1, delay: delay, opacity: 1, ease: 'Power2.easeInOut'});
    }

    private animateInWebgl() {
        if (this.loaded) {
            if (!this._myReq) {
                this.renderLoop();
            }
            this.layout();
            goto_landing();
            gsap.to(this.globeStill, 1, {opacity: 0, ease: 'Power1.easeIn'});
            gsap.to(this.webglContainer, 1, {opacity: 1, onComplete: () => {
                gsap.to(this.globe, 1, {introIdleSpeed: -.001, easeAmount: GLOBE_EASE_AMOUNT, ease: 'Power1.easeIn'});
                showIntroPaths();
            }});
        }
    }

    public animateOut() {
        hideIntroPaths();
        return new Promise(resolve => {
            TweenMax.to(this.element, .5, {
                opacity: 0,
                onComplete: () => {
                    this.hide();
                    resolve();
                }
            });
        });
    }

    public show() {
        this.element.style.display = 'block';
        this.element.style.opacity = '1';
        this.layout();
    }

    private layout = () => {
        this.resizeGlobeStill();
        if (this.renderer) {
            this.dpr = Window.width <= 600 ? 2 : 1;
            this.renderer.setPixelRatio(this.dpr);
            camera.aspect = Window.width / Window.height;
            camera.updateProjectionMatrix();
            this.renderer.setSize(Window.width, Window.height);
            this.render();
        }
    }

    private resizeGlobeStill() {
        let h = Window.height;
        let w = h * (16/9);
        this.globeStill.style.height = h + 'px';
        this.globeStill.style.width = w + 'px';
        this.globeStill.style.left = (-(w - Window.width) / 2) + 'px';
    }

    private resetHoverEase() {
        this.hoverEase = 1;
        this.setHoverLookAtDistance(new Vector3(0, 0, 0));
        this.setHoverDistance(new Vector3(0, 0, 0));
        this.render();
        this.hoverEase = DEFAULT_HOVER_EASE;
    }

    public onChapterChange() {
        this.resetHoverEase();
        cityScene.reset();
        gsap.exportRoot({}, true).kill();
        overlayFactory.hideAll();
    }

    public hide() {
        this.element.style.display = 'none';
    }

    public render() {
        cameraContainer.position.x += ( (-(this.mouseX * this.hoverDistance.x) - cameraContainer.position.x) ) / this.hoverEase;
        cameraContainer.position.z += ( (-(this.mouseX * this.hoverDistance.z) - cameraContainer.position.z) ) / this.hoverEase;
        cameraContainer.position.y += ( ((this.mouseY * this.hoverDistance.y) - cameraContainer.position.y ) ) / this.hoverEase;
        // this.hoverLookAt.x += (((this.mouseX * this.hoverLookAtDistance.x) - this.hoverLookAt.x) ) / this.hoverEase;
        // this.hoverLookAt.y += ((-(this.mouseY * this.hoverLookAtDistance.y) - this.hoverLookAt.y) ) / this.hoverEase;
        // this.hoverLookAt.z += (((this.mouseX * this.hoverLookAtDistance.z) - this.hoverLookAt.z) ) / this.hoverEase;
        camera.lookAt((this.lookAtPosition.clone())); //.add(this.hoverLookAt)
        this.globe.render();
       // this.orbitControls.update();
        this.renderer.render(scene, camera);
        overlayFactory.render();
    }

    private renderLoop = () => {
        this.render();
        this._myReq = requestAnimationFrame(this.renderLoop);
    }

    public resume = () => {
        this.globe.normalizeRotation();
        gsap.to(this.globe, 1, {introIdleSpeed: 0, easeAmount: 1, overwrite: true});

        if (this.lastRootExport) {
            this.lastRootExport.play();
        }

        if (!this._myReq) {
           this.renderLoop();
        }
    }

    public pause() {
        this.lastRootExport = gsap.exportRoot({}, true);
        this.lastRootExport.pause();
        cancelAnimationFrame(this._myReq);
        this._myReq = null;
    }

    public toColor(speed: number, delay: number = 0, color: string = '#000000') {
        gsap.to(this.fadePlane, speed, {delay: delay, backgroundColor: color});
    }

    public fadeToColor(speed: number, delay: number = 0, color: string = '#000000') {
        gsap.set(this.fadePlane, {backgroundColor: color, delay: delay});
        gsap.to( this.fadePlane, speed, {opacity: 1, delay: delay, ease: "Power2.easeInOut"});
    }

    public fadeFromColor(speed: number, delay: number = 0) {
        gsap.to( this.fadePlane, speed, {opacity: 0, delay: delay, ease: "Power2.easeInOut"});
    }

    public setHoverDistance(distance: Vector3, delay: number = 0, speed: number = 0) {
        gsap.to(this.hoverDistance, speed, {x: distance.x, y: distance.y, z: distance.z, delay: delay, ease: 'Power2.easeInOut'});
    }

    public setHoverLookAtDistance(distance: Vector3, delay: number = 0, speed: number = 0) {
        gsap.to(this.hoverLookAtDistance, speed, {x: distance.x, y: distance.y, z: distance.z, delay: delay, ease: 'Power2.easeInOut'});
    }

    public destroy() {
        emitter.off(EVENT_RESIZE, this.layout);
        document.removeEventListener( 'mousemove', this.onMouseMove);
    }
}

export const hero3d = new Hero3d();
