import {gsap} from "gsap";
import {EVENT_RESIZE, Window} from "../global/window";
import {emitter} from "../global/emitter";
import Draggable from 'gsap/Draggable';
import {BP_S} from "../global/constants";

export class Carousel {
    public element: HTMLElement;
    private activeID: number;
    private itemW: number;
    private carousel_count: number;
    private carouselWrapper: HTMLElement;
    private carouselItems: NodeListOf<HTMLElement>;
    private navDotsWrapper: HTMLElement;
    private navDots: NodeListOf<HTMLElement>;
    private btnNext: HTMLElement;
    private btnPrev: HTMLElement;
    private swipeDir: string;
    private mobileOnly: boolean;
    private slideSpeed: number;
    private slideMeth: any;
    private opacityDisabled: number;
    private opacitySpeed: number;
    private draggable: any;
    private enabled: boolean;
    private prevX: number = 0;

    /**
     * Constructor
     *
     * @param  {HTMLElement} element - The Carousel container element.
     * @param  {string} breakpoint - The parent node in the HTML to return
     */
    constructor(element: HTMLElement, mobileOnly: boolean = false) {
        this.element = element;
        this.mobileOnly = mobileOnly;
        this.initDraggable();
    }

    public initDraggable() {
        // Draggable
        this.activeID = 0;
        this.carousel_count = this.element.querySelectorAll('.carousel-item').length;
        this.carouselWrapper = this.element.querySelector('.carousel-items');
        this.carouselItems = this.element.querySelectorAll('.carousel-item');
        this.navDotsWrapper = this.element.querySelector('.nav-dots');
        this.btnNext = this.element.querySelector('.item-prev');
        this.btnPrev = this.element.querySelector('.item-next');
        this.swipeDir;
        this.slideSpeed = .45;
        this.slideMeth = 'Power2.EaseInOut';
        this.opacityDisabled = .25;
        this.opacitySpeed = .35;

        // Hide dots if less than 2 images
        if (this.carousel_count < 2)
            this.navDotsWrapper.style.display = 'none';

        this.setupDraggable();
        this.setupDots();
        this.navigateSlide();
        this.addHandlers();

        this.enabled = true;

        // Resize handle
        emitter.on(EVENT_RESIZE, this.layout);
        this.layout();

        emitter.emit(EVENT_RESIZE, {
            width: Window.width,
            height: Window.height
        });
    }

    setupDraggable() {
        gsap.registerPlugin(Draggable);

        this.draggable = Draggable.create(this.carouselWrapper, {
            type: 'x',
            edgeResistance: 0.90,
            dragResistance: 0.0,
            bounds: '.carousel-container',
            onDrag: this.updateDirections.bind(this),
            onThrowUpdate: this.updateDirections.bind(this),
            throwProps: true,
            lockAxis: true,
            onDragStart: function (evt) {
            },
            onDragEnd: () => {
                if (this.swipeDir == 'left') {
                    this.activeID++
                }
                else if (this.swipeDir == 'right') {
                    this.activeID--
                }

                this.navigateSlide();
            }
        });
    };

    disable() {
        this.enabled = false;

        this.draggable[0].kill();
        this.removeHandlers();

        this.carouselWrapper.style.removeProperty('width');
        for (const el of this.carouselItems) {
            el.style.removeProperty('width');
            el.style.removeProperty('opacity');
        }
    }

    enable() {
        this.enabled = true;

        this.draggable[0].enable();
    }

    // set up dots
    setupDots() {
        let dotHTML: string;

        for (var i = 0; i < this.carousel_count; i++) {
            dotHTML = `<div class="nav-dot" id="dot_${i}"><span class="dot"></span></div>`;
            this.navDotsWrapper.insertAdjacentHTML('beforeend', dotHTML);
        }

        this.navDots = this.element.querySelectorAll('.nav-dot');
    }

    // navigate slide
    navigateSlide() {
        if (this.activeID >= this.carousel_count - 1) this.activeID = this.carousel_count - 1;
        if (this.activeID <= 0) this.activeID = 0;

        var xTarget = ((this.activeID * this.itemW) * -1);

        gsap.to(this.carouselWrapper, {
            duration: this.slideSpeed,
            x: xTarget,
            ease: this.slideMeth,
            overwrite: true,
            onComplete: this.slideDone.bind(this)
        });
    }

    slideDone() {
        if (!this.enabled) return false;

        gsap.to([this.navDots, this.carouselItems], {duration: this.opacitySpeed, opacity: this.opacityDisabled});

        const el = this.element.querySelector(`#dot_${this.activeID}`);
        const elActive = this.element.querySelector(`.nav-dot.active`);
        if (elActive) elActive.classList.remove('active');
        el.classList.add('active');

        gsap.to([el, this.carouselItems[this.activeID]], {
            duration: this.opacitySpeed,
            opacity: 1,
            color: 0xCC0000
        });
    }

    updateDirections() {
        this.swipeDir = this.draggable[0].getDirection("velocity");

        if (this.prevX > this.draggable[0].endX) {
            this.swipeDir = 'left';
        } else if (this.prevX < this.draggable[0].endX) {
            this.swipeDir = 'right';
        }

        this.prevX = this.draggable[0].endX;
    }

    onNavDotClick(e: any) {
        this.activeID = e.target.getAttribute('id').split('_')[1];
        this.navigateSlide();
    }

    onItemMouseDown(e: any) {
        e.target.classList.remove('grab');
        e.target.classList.add('grabbing');
    }

    onItemMouseEnter(e: any) {
        e.target.classList.remove('grabbing');
        e.target.classList.add('grab');
    }

    onItemMouseUp(e: any) {
        e.target.classList.remove('grabbing');
        e.target.classList.add('grab');
    }

    onNavDotMouseEnter(e: any) {
        gsap.to(e.target, {duration: this.opacitySpeed, opacity: 1});
    }

    onNavDotMouseLeave(e: any) {
        if (e.target.classList.contains('active')) return false;
        gsap.to(e.target, {duration: this.opacitySpeed, opacity: this.opacityDisabled});
    }

    onToggleItemClick(e: any) {
        // When two items just toggle both
        if (this.carousel_count ===2) {
            this.activeID = this.activeID === 0 ? 1 : 0;
            this.navigateSlide();
        }
    }

    private onPrevClick(e: any) {
        this.activeID = this.activeID === this.carouselItems.length - 1 ? 0 : this.activeID += 1;
        this.navigateSlide();
    }

    private onNextClick(e: any) {;
        this.activeID = this.activeID === 0 ? this.carouselItems.length - 1 : this.activeID -= 1;
        this.navigateSlide();
    }

    addHandlers() {
        this.element.addEventListener('click', this.onToggleItemClick.bind(this));

        for (const navDot of this.navDots) {
            navDot.addEventListener('click', this.onNavDotClick.bind(this));
            navDot.addEventListener('mouseenter', this.onNavDotMouseEnter.bind(this));
            navDot.addEventListener('mouseleave', this.onNavDotMouseLeave.bind(this));
        }

        for (const item of this.carouselItems) {
            item.addEventListener('mousedown', this.onItemMouseDown.bind(this));
            item.addEventListener('mouseenter', this.onItemMouseEnter.bind(this));
            item.addEventListener('mouseup', this.onItemMouseUp.bind(this));
        }

        if (this.btnPrev && this.btnNext) {
            this.btnPrev.addEventListener('click', this.onPrevClick.bind(this));
            this.btnNext.addEventListener('click', this.onNextClick.bind(this));
        }
    }

    removeHandlers() {
        this.element.removeEventListener('click', this.onToggleItemClick.bind(this));

        for (const navDot of this.navDots) {
            navDot.removeEventListener('click', this.onNavDotClick.bind(this));
            navDot.removeEventListener('mouseenter', this.onNavDotMouseEnter.bind(this));
            navDot.removeEventListener('mouseleave', this.onNavDotMouseLeave.bind(this));
        }

        for (const item of this.carouselItems) {
            item.removeEventListener('mousedown', this.onItemMouseDown.bind(this));
            item.removeEventListener('mouseenter', this.onItemMouseEnter.bind(this));
            item.removeEventListener('mouseup', this.onItemMouseUp.bind(this));
        }

        if (this.btnPrev && this.btnNext) {
            this.btnPrev.removeEventListener('click', this.onPrevClick.bind(this));
            this.btnNext.removeEventListener('click', this.onNextClick.bind(this));
        }
    }

    layout = () => {
        // Check to enable or disable functionality based on mobileOnly
        if (this.mobileOnly && Window.width < BP_S && !this.enabled) {
            this.enable();
        } else if (this.mobileOnly && Window.width >= BP_S && this.enabled) {
            this.disable();
        }

        if (this.enabled) {
            this.itemW = this.element.getBoundingClientRect().width;
            this.carouselWrapper.style.width = (this.itemW * this.carousel_count) + 'px';

            for (const item of this.carouselItems) {
                item.style.width = this.itemW + 'px';
            }
        }
    }

    public destroy() {
        this.draggable[0].kill();
        emitter.off(EVENT_RESIZE, this.layout);
        this.removeHandlers();
    }
}
