import {gsap} from "gsap";

export class AlertBar {

    public element: Element;
    readonly pushContent: NodeList;
    private closeButton: HTMLElement;

    constructor(element: Element) {
        this.element = element;
        this.closeButton = this.element.querySelector('.close');
        this.pushContent = document.body.querySelectorAll('header, .section, .hero3d');
        this.closeButton.addEventListener('click', () => {
           this.close();
        });
        this.open();
    }

    open() {
        gsap.set(this.element, {y: 0});
        gsap.set(this.pushContent, {y: this.element.clientHeight});
    }

    close() {
        gsap.to(this.element, .25,{y: -this.element.clientHeight, clearProps: 'y'});
        gsap.to(this.pushContent, .25,{y: 0, clearProps: 'y'});
    }
}