/*
The top level class for the homepage. Intializes, controls and manages the hero 3d experience.
 */

import {Page} from "../global/page";
import {CHAPTERS_3D, COLOR_MODES, DAY, SLUG_HOME, TIME_OF_DAY} from "../global/constants";
import {hero3d, timer} from "./hero3d";
import {state} from "../global/state";
import {header} from "../header";
import {gsap} from "gsap";
import {Controls, EVENT_CHAPTER_SWITCH, EVENT_PAUSE, EVENT_PLAY} from "./controls";
import * as animations from "./animations";
import {ACT_CHANGE_EVENT} from "./timer";
import {EVENT_HEADER_COLOR, EVENT_RESIZE} from "../global/window";
import {emitter} from "../global/emitter";
//@ts-ignore
import {SplitText} from "../gsap-bonus/SplitText";
import {hideIntroPaths, showIntroPaths} from "./animations";
import {WEBGL} from "three/examples/jsm/WebGL";
import {AUDIO_BE3, AUDIO_CLK, audioController} from "../global/audioController";

export class Home extends Page {
    public titleExperienceEl: HTMLElement;
    public introPlay: HTMLElement;
    public introLoader: HTMLElement;
    public dragArea: HTMLElement;
    private titleInitialEl: HTMLElement;
    private introPlayIcon: HTMLElement;
    private introControls: HTMLElement;
    private menuEl: HTMLElement;
    private statsEl: HTMLElement;
    private introLoadingLabel: HTMLElement;
    private introExploreLabel: HTMLElement;
    private disclaimer: HTMLElement;
    private statsItemsEl: NodeList;
    private controls: Controls;
    private started: boolean;
    private resumeOnFocus: boolean;

    constructor() {
        super(document.querySelector('main.home'), true);
        this.dragArea = document.querySelector('.drag-area');
        this.menuEl = this.element.querySelector('.menu');
        this.statsEl = this.element.querySelector('.stats');
        this.statsItemsEl = this.element.querySelectorAll('.stats li');
        this.titleInitialEl = this.element.querySelector('.title-initial');
        this.titleExperienceEl = this.element.querySelector('.title-experience');
        this.introControls = this.element.querySelector('.intro-controls');
        this.introPlay = this.element.querySelector('.intro-play');
        this.introPlayIcon = this.element.querySelector('.intro-play .icon');
        this.introLoader = this.element.querySelector('.intro-loader');
        this.introLoadingLabel = this.element.querySelector('.loading-label');
        this.introExploreLabel = this.element.querySelector('.explore-label');
        this.disclaimer = this.element.querySelector('.disclaimer');
        this.controls = new Controls(this.element.querySelector('.experience-controls'));
        if (!WEBGL.isWebGLAvailable()) {
            this.introControls.style.display = 'none';
        }
        hero3d.init();
    }

    setupEvents() {
        this.controls.on(EVENT_PLAY, this.playButtonHandler);
        this.controls.on(EVENT_PAUSE, this.pauseButtonHandler);
        this.controls.on(EVENT_CHAPTER_SWITCH, this.chapterSwitchHandler);
        this.enableDrag();
        document.body.addEventListener('keyup', this.keyUpHandler);
        timer.on(ACT_CHANGE_EVENT, this.onActChange);
        this.introPlay.addEventListener('click', this.startButtonHandler);
        emitter.on(EVENT_RESIZE, this.updateDOMColors);
        window.addEventListener("blur", this.onBlurHandler);
        window.addEventListener("focus", this.onFocusHandler);
    }

    async animateIn() {
        gsap.set(this.element, {opacity: 1});
        hero3d.reset();
        hero3d.show();
        await hero3d.loadImage();

        const startDelay = .25;
        gsap.to(this.statsItemsEl, 1, {opacity: 1, y: 0, stagger: .1, delay: startDelay});
        hero3d.animateIn(.5 + startDelay, 1.5);
        header.animateIn(.9 + startDelay, 1);
        gsap.to(this.menuEl, 1, {opacity: 1, delay: .9 + startDelay});

        gsap.to([this.introLoader, this.introPlay, this.disclaimer], .5, {opacity: 1, delay: 1.8 + startDelay});
        gsap.to(this.introLoader, 3, {rotation: 360, ease: 'none', repeat: -1, delay: 1.8 + startDelay});

        gsap.set(this.titleInitialEl, {perspective: 400, opacity: 1});
        const tl = gsap.timeline(),
            mySplitText = new SplitText(this.titleInitialEl, {type: "words, chars"}),
            chars = mySplitText.chars;
        tl.from(chars, {
            duration: 1,
            opacity: 0,
            y: 20,
            ease: "Power2.easeOut",
            delay: .7 + startDelay,
            stagger: 0.007,
            onComplete: this.animationInComplete.bind(this)
        });
    }

    async animationInComplete() {
        await hero3d.load();
        this.setupEvents();
        this.introExploreLabel.style.display = 'block';
        this.introLoadingLabel.style.display = 'none';
        gsap.to(this.introLoader, .5, {opacity: 0, onComplete: () => {
                gsap.killTweensOf(this.introLoader);
            }});
        gsap.to(this.introPlayIcon, 1, {opacity: 1, onComplete: () => {
                this.introPlay.classList.add('active');
            }});
    }

    animateOut(): Promise<unknown> {
        this.pause();
        gsap.exportRoot().kill();
        hero3d.animateOut();
        return super.animateOut();
    }

    startButtonHandler = () => {
        audioController.play(AUDIO_CLK);
        this.introPlay.removeEventListener('click', this.startButtonHandler);
        this.start();
    }

    playButtonHandler = () => {
        this.resume();
    }

    pauseButtonHandler = () => {
        this.pause();
    }

    onBlurHandler = () => {
        if (!timer.paused) {
            this.resumeOnFocus = true;
            this.pause();
        }
    }

    onFocusHandler = () => {
        if (this.resumeOnFocus) {
            this.resumeOnFocus = false;
            this.resume();
        }
    }

    keyUpHandler = (e: any) => {
        if (e.keyCode == 32) {
            e.preventDefault();
            if (timer.paused) {
                this.resume();
            } else {
                this.pause();
            }
        }
    }

    public start() {
        if (this.started || !hero3d.loaded) {
            return;
        }
        this.introPlay.classList.remove('active');
        this.started = true;
        this.resume();
        hideIntroPaths();
        header.hideZoom();
        this.hideMenu(.5);
        this.hideStats(.5);
        gsap.to(this.titleInitialEl, .5, {opacity: 0, onComplete: () => {
            this.element.classList.add('experience-started');
            this.disableDrag();
            }});
        gsap.to([this.introPlay, this.introPlayIcon, this.disclaimer], .5, {opacity: 0});
        this.controls.show(1);
    }

    public resume() {
        if (!timer.paused || !hero3d.loaded || !this.started) {
            return;
        }
        timer.resume();
        this.controls.resume();
        hero3d.resume();
        audioController.resumeAll();
    }

    public pause() {
        if (timer.paused || !hero3d.loaded) {
            return;
        }
        timer.pause();
        this.controls.pause();
        hero3d.pause();
        audioController.pauseAll();
    }

    chapterSwitchHandler = (event: any) => {
        if (timer.currentChapterIndex == event.chapterIndex) {
            return;
        }
        audioController.stopAll();
        gsap.set(this.titleExperienceEl, {opacity: 0});
        timer.gotoAct(event.chapterIndex, 1);
    }

    onDragAreaDownHandler = (event: any) => {
        hero3d.globe.startDrag(event.clientX, event.clientY);
        this.dragArea.addEventListener('mousemove', this.onDragAreaMoveHandler);
    }

    onDragAreaMoveHandler = (event: any) => {
        hero3d.globe.drag(event.clientX, event.clientY);
    }

    onDragAreaUpHandler = (event: any) => {
        this.dragArea.removeEventListener('mousemove', this.onDragAreaMoveHandler);
        hero3d.globe.stopDrag(event.clientX, event.clientY);
    }

    private onActChange = (event: any) => {
        const part = event.chapter + '_' + event.act;
        const chapterData = state.getValue(CHAPTERS_3D)[event.chapter - 1];
        const actData = chapterData.acts[event.act - 1];
        const from = timer.progress / timer.duration;
        const actDuration = parseInt(actData.duration);
        const to = (timer.progress + actDuration) / timer.duration;

        if (timer.currentChapterIndex != event.lastChapter) {
            hero3d.onChapterChange();
            this.controls.startTimer(timer.getChapterDuration(timer.currentChapterIndex));
            this.resume();
        }

        this.controls.updateChapterMenu(event.chapter, event.act);
        this.controls.animateProgressFromTo(from, to, actDuration);

        //@ts-ignore
        animations['play'+part](chapterData, actData, this);
    }

    showStats(speed: number = 0, delay: number = 0) {
        gsap.to(this.statsEl, speed, {opacity: 1, delay: delay});
    }

    hideStats(speed: number = 0, delay: number = 0) {
        gsap.to(this.statsEl, speed, {opacity: 0, delay: delay, onComplete: () => {
            this.statsEl.style.display = 'none';
            }});
    }

    hideMenu(speed: number = 0, delay: number = 0) {
        gsap.to(this.menuEl, speed, {opacity: 0, delay: delay, onComplete: () => {
                this.menuEl.style.display = 'none';
            }});
    }

    updateDOMColors = () => {
        if (state.getValue(TIME_OF_DAY) == DAY) {
            this.element.classList.remove(...COLOR_MODES);
            this.element.classList.add(COLOR_MODES[0]);
            header.element.classList.remove(...COLOR_MODES);
            header.element.classList.add(COLOR_MODES[0]);
            emitter.emit(EVENT_HEADER_COLOR, {mode: COLOR_MODES[0]});
        } else {
            this.element.classList.remove(...COLOR_MODES);
            this.element.classList.add(COLOR_MODES[1]);
            header.element.classList.remove(...COLOR_MODES);
            header.element.classList.add(COLOR_MODES[1]);
            emitter.emit(EVENT_HEADER_COLOR, {mode: COLOR_MODES[1]});
        }
    }

    get slug() {
        return SLUG_HOME;
    }

    private enableDrag() {
        this.dragArea.addEventListener('mousedown', this.onDragAreaDownHandler);
        window.addEventListener('mouseup', this.onDragAreaUpHandler);
    }

    private disableDrag() {
        this.dragArea.removeEventListener('mousedown', this.onDragAreaDownHandler);
        window.removeEventListener('mouseup', this.onDragAreaUpHandler);
    }

    public destroy() {
        this.controls.off(EVENT_PLAY, this.playButtonHandler);
        this.controls.off(EVENT_PAUSE, this.pauseButtonHandler);
        this.controls.off(EVENT_CHAPTER_SWITCH, this.chapterSwitchHandler);
        this.disableDrag();
        document.body.removeEventListener('keyup', this.keyUpHandler);
        timer.off(ACT_CHANGE_EVENT, this.onActChange);
        this.introPlay.removeEventListener('click', this.startButtonHandler);
        emitter.off(EVENT_RESIZE, this.updateDOMColors);
        window.removeEventListener("blur", this.onBlurHandler);
        window.removeEventListener("focus", this.onFocusHandler);
        hero3d.destroy();
    }
}
