import {Page} from "../global/page";
import {
    SLUG_BLOG,
    BP_L,
    BP_M,
    BP_S,
    BLOG_SCROLL_Y,
    BLOG_CATEGORY
} from "../global/constants";
import {gsap} from "gsap";
import {emitter} from "../global/emitter";
import {EVENT_RESIZE, EVENT_SCROLL, Window} from "../global/window";
import {state} from "../global/state";
//@ts-ignore
import lazySizes from 'lazysizes';

export class Blog extends Page {

    private navSelected: HTMLElement;
    private navigation: HTMLElement;
    private filterLinks: HTMLElement[];
    private subtitle: HTMLElement;
    private coverFeatures: HTMLElement;
    private isNavOpen = false;
    private posts: NodeListOf<HTMLElement>;
    private postsContainer: HTMLElement;
    private hiddenContainer: HTMLElement;
    private screenMode: number = 0;
    private activeDoubleClass: string = 'double-column--active';
    private selectedCategory: string = '';

    constructor() {
        super(document.querySelector('main'));
        this.element = document.querySelector('main.blog');
        this.navSelected = this.element.querySelector('.tool-bar__filter .selected span');
        this.navigation = this.element.querySelector('.tool-bar__filter .filter__nav');
        this.filterLinks = [...this.element.querySelectorAll('.tool-bar__filter li a')] as HTMLElement[];
        this.subtitle = document.querySelector('.sub-title');
        this.coverFeatures = document.querySelector('.cover-features');
        this.posts = this.element.querySelectorAll('.post');
        this.postsContainer = this.element.querySelector('.posts-container');
        this.hiddenContainer = this.element.querySelector('.hidden-container');

        this.init();
    }

    init = () => {
        this.navSelected.addEventListener(`click`, this.onNavClick);
        this.navigation.addEventListener(`mouseleave`, this.closeNav);
        this.navigation.addEventListener(`click`, this.onFilterLinkClick);

        const scrollY = parseInt(state.getValue(BLOG_SCROLL_Y));
        this.postsContainer.style.height = `${parseInt(state.getValue(BLOG_SCROLL_Y))}px`;

        emitter.on(EVENT_RESIZE, this.layout);
        emitter.on(EVENT_SCROLL, this.scroll);
        this.layout();

        this.checkLazySizes();

        window.scrollTo(0, scrollY);
        this.postsContainer.style.height = 'auto';
    }

    setFilterPosts() {
        let activeItem;

        this.selectedCategory = state.getValue(BLOG_CATEGORY);

        if (!this.selectedCategory) {
            activeItem = [...this.filterLinks].filter(el => {
                if (el.classList.contains('active')) return el;
            })[0];

            this.selectedCategory = activeItem.dataset.category;
        } else {
            [...this.filterLinks].forEach(el => {
                if (el.dataset.category === this.selectedCategory) {
                    el.classList.add('active');
                } else {
                    el.classList.remove('active');
                }
            });
        }

        this.filterPosts();
        this.checkLazySizes();
    }

    checkLazySizes() {
        window.dispatchEvent(new Event('scroll'));
    }

    scroll = () => {
        const postContainerBottom = this.postsContainer.getBoundingClientRect().bottom;

        if (postContainerBottom - Window.height < 0)
            this.loadNextSet();

        state.setValue(BLOG_SCROLL_Y, `${Window.lastScrollY}`);
    }

    loadNextSet() {
        let counter = 0;

        for (const wrapper of this.postsContainer.querySelectorAll('.wrapper')) {
            if (!wrapper.classList.contains('visible') && counter < 4) {
                counter++;
                wrapper.classList.add('visible');
            }
        }
    }

    layout = () => {
        const prevMode = this.screenMode;

        if (window.matchMedia(`(min-width: ${BP_L}px)`).matches && BP_L) {
            this.screenMode = BP_L;
        } else if (window.matchMedia(`(min-width: ${BP_M}px)`).matches && BP_M) {

            this.screenMode = BP_M;
        } else {
            this.screenMode = BP_S;
        }

        if (prevMode !== this.screenMode) {
            this.setFilterPosts();
        }
    }

    async animateIn() {
        await super.animateIn();

        gsap.to(this.subtitle, {
            duration: .3,
            opacity: 1
        });

        gsap.to(this.coverFeatures, {
            duration: .3,
            opacity: 1
        });
    }

    onNavClick = (event: Event) => {
      event.preventDefault();

      if (this.isNavOpen) {
        this.navSelected.classList.remove(`open`);
        this.navigation.classList.remove(`visible`);
      } else {
        this.navSelected.classList.add(`open`);
        this.navigation.classList.add(`visible`);
      }
      this.isNavOpen = !this.isNavOpen;
    }

    closeNav = (event?: Event) => {
      if (event) event.preventDefault();
      if (this.isNavOpen) {
        this.isNavOpen = false;
        this.navSelected.classList.remove(`open`);
        this.navigation.classList.remove(`visible`);
      }
    }

    filterPosts() {
        const category = this.selectedCategory;
        let delay = 0;

        for (const el of this.posts) {
            el.classList.remove(this.activeDoubleClass);

            // Show or hide posts
           if (el.dataset.categories.indexOf(category) > -1 || category === '') {
                el.classList.add('visible');
                el.classList.remove('hidden');
                gsap.set(el, {opacity: 0});
                gsap.to(el, {opacity: 1, duration: 0.3, delay});
                delay = delay + .15;
            } else {
                el.classList.add('hidden');
                el.classList.remove('visible');
            }
        }

        this.wrapPosts();

        state.setValue(BLOG_CATEGORY, category);
    }

    wrapPosts() {
        let totalCounter = 0;
        let counter = 0;
        let newEl: HTMLElement;

        for (const el of this.posts) {
            el.classList.remove('double-column--active');
        }

        if (this.screenMode === BP_L) {

            for (const el of this.posts) {
                // Create wrapper
                if (counter === 0) {
                    newEl = document.createElement('div');
                    newEl.classList.add('div', 'wrapper');
                }

                // Append posts
                if (el.classList.contains('visible') && el.classList.contains('double-column') && counter < 2) {
                    el.classList.add(this.activeDoubleClass);
                    newEl.appendChild(el);
                    counter = counter + 2;
                } else if (el.classList.contains('visible')) {
                    newEl.appendChild(el);
                    counter++;
                } else {
                    this.hiddenContainer.appendChild(el);
                }

                // Append wrapper
                if (counter > 2 || totalCounter === this.posts.length - 1) {
                    this.postsContainer.appendChild(newEl);
                    counter = 0;
                }

                totalCounter++;
            }

        } else if (this.screenMode === BP_M) {

            for (const el of this.posts) {
                // Create wrapper
                if (counter === 0) {
                    newEl = document.createElement('div');
                    newEl.classList.add('wrapper');
                }

                // Append posts
                if (el.classList.contains('visible') && el.classList.contains('double-column') && counter === 0) {
                    el.classList.add(this.activeDoubleClass);
                    newEl.appendChild(el);
                    counter = counter + 2;
                } else if (el.classList.contains('visible') && el.classList.contains('double-column')) {
                    newEl.appendChild(el);
                    counter++;
                } else if (el.classList.contains('visible')) {
                    newEl.appendChild(el);
                    counter++;
                } else {
                    this.hiddenContainer.appendChild(el);
                }

                // Append wrapper
                if (counter > 1 || totalCounter === this.posts.length - 1 || el.classList.contains('double-column')) {
                    this.postsContainer.appendChild(newEl);
                    counter = 0;
                }

                totalCounter++;
            }

        } else if (this.screenMode === BP_S) {

            // Create wrapper
            newEl = document.createElement('div');
            newEl.classList.add('wrapper');

            for (const el of this.posts) {
                // Append posts
                if (el.classList.contains('visible')) {
                    newEl.appendChild(el);
                } else {
                    this.hiddenContainer.appendChild(el);
                }
            }

            this.postsContainer.appendChild(newEl);
        }

        // Wrappers clean up
        counter = 0;
        for (const wrapper of this.postsContainer.querySelectorAll('.wrapper')) {
            if (!wrapper.innerHTML) {
                wrapper.remove();
            } else if (counter < 4) {
                wrapper.classList.add('visible');
            }

            counter++;
        }

        this.checkLazySizes();
    }

    onFilterLinkClick = (event: Event) => {
      event.preventDefault();

      const target = event.target as HTMLElement;
      if (target.tagName !== 'A') return;

      this.filterLinks.forEach(el => el.classList.remove('active'));
      target.classList.add('active');

      this.navSelected.innerText = target.innerText;
      this.selectedCategory = target.dataset.category;
      this.filterPosts();

      this.closeNav();
    }

    get slug() {
        return SLUG_BLOG;
    }

    destroy() {
        emitter.off(EVENT_RESIZE, this.layout);
        emitter.off(EVENT_SCROLL, this.scroll);
        this.navSelected.removeEventListener(`click`, this.onNavClick);
        this.navigation.removeEventListener(`mouseleave`, this.closeNav);
        this.navigation.removeEventListener(`click`, this.onFilterLinkClick);
        super.destroy();
    }
};
