import Emitter from "@hellomonday/emitter";

export class Preload extends Emitter{
    public loadImage(image: HTMLElement) {
        let dataSrc = image.getAttribute('data-src');
        if (!dataSrc) dataSrc = image.getAttribute('data-source');
        if (!dataSrc) { return; }
        return new Promise(resolve => {
            const imgEl = new Image();
            imgEl.onload = () => {
                image.setAttribute('src', dataSrc);
                image.setAttribute('loaded', 'true');
                resolve();
            };
            const isLoaded = image.getAttribute('loaded');
            if (isLoaded != 'true') {
                imgEl.setAttribute('src', dataSrc);
            } else {
                resolve();
            }
        });
    }
}
export const preload = new Preload();
