import {gsap} from "gsap";

export class VideoOverlay {
    public element: HTMLElement;
    private mainElement: HTMLElement;
    private modalElement: HTMLElement;
    private videoId: string = '';
    private fadeDuration: number = .3;

    constructor(element: HTMLElement, mainElement: HTMLElement) {
        this.element = element;
        this.mainElement = mainElement;
        this.init();
    }

    private init() {
        this.element.addEventListener('click', this.onAnchorClick.bind(this));
    }

    // Test for youtube
    private testURL( link: string ) {
        const regex = RegExp('^((?:https?:)?\\/\\/)?((?:www|m)\\.)?((?:youtube\\.com|youtu.be))(\\/(?:[\\w\\-]+\\?v=|embed\\/|v\\/)?)([\\w\\-]+)(\\S+)?$');
        return regex.test(link);
    }

    private onAnchorClick(e: any) {
        e.preventDefault();

        const link = e.target.href;

        if (this.testURL(link)) {
            this.videoId = link.split('v=')[1];
            const ampersandPosition = this.videoId.indexOf('&');
            if(ampersandPosition != -1) {
                this.videoId = this.videoId.substring(0, ampersandPosition);
            }

            this.openModal();
        }
    }

    private openModal() {
        this.modalElement = document.createElement('div');
        this.modalElement.classList.add('video-modal');
        this.modalElement.innerHTML = `
            <button class="close"><span class="span">close</span></button>
            <div class="container">
              <div class="ratio ratio--16x9">
              <iframe class="iframe" width="420" height="315"src="https://www.youtube.com/embed/${this.videoId}"></iframe>
              </div>
            </div>`;

        // Close handle
        this.modalElement.querySelector('.close').addEventListener('click', this.onCloseClick.bind(this));

        // Append modal
        this.modalElement.style.opacity = '0';
        this.mainElement.appendChild(this.modalElement);
        gsap.to(this.modalElement, {duration: this.fadeDuration, opacity: 1});
    }

    private onCloseClick(e: any) {
        gsap.to(this.modalElement, {duration: this.fadeDuration, opacity: 0, onComplete:()=> {
                this.modalElement.remove();
            }})
    }

    public destroy() {
        this.element.removeEventListener('click', this.onAnchorClick.bind(this));
    }
}
