import '@hellomonday/map';
// @ts-ignore
import navaid from 'navaid';
import {state} from "./global/state";
import {
    ALL_SLUGS,
    BASE_PATH,
    CURRENT_SECTION,
    LOCALE,
    LOCALE_CHINESE,
    LOCALE_ENGLISH,
    SLUG_BLOG,
    SLUG_BLOG_CATEGORY,
    SLUG_BLOG_SINGLE,
    SLUG_BUILD,
    SLUG_ECOSYSTEM,
    SLUG_HOME,
    SLUG_PROVIDE,
    SLUG_STORE
} from "./global/constants";
import {Home} from './home/home';
import {Provide} from "./provide/provide";
import {Build} from "./build/build";
import {Ecosystem} from "./ecosystem/ecosystem";
import {Store} from "./store/store";
import {EVENT_RESIZE, Window} from "./global/window";
import {header} from "./header";
import {footer} from "./footer";
import {AlertBar} from "./alertBar";
import swapDOM from "@hellomonday/swap-dom";
import {gsap} from "gsap";
import {redirects} from "./global/redirects";
import {rewriteRelLinks} from "./global/utils";
//@ts-ignore
import lazySizes from 'lazysizes';
import {Blog} from "./blog/blog";
import {BlogSingle} from "./blog/blogSingle";

class App {

    private clickBlocker: HTMLDivElement;

    constructor() {
        this.clickBlocker = document.body.querySelector('.click-blocker');
        const alertBarEl = document.body.querySelector('.alert-bar');
        if (alertBarEl) {
            new AlertBar(alertBarEl)
        }
        redirects.check();
        redirects.redirectLang();
        this.initListeners();
        lazySizes.init();
    }

    initListeners() {
        new Window();
        let router = navaid(BASE_PATH);
        router.on(SLUG_HOME, () => this.onRouteChange(SLUG_HOME));
        router.on(SLUG_PROVIDE, () => this.onRouteChange(SLUG_PROVIDE));
        router.on(SLUG_BUILD, () => this.onRouteChange(SLUG_BUILD));
        router.on(SLUG_ECOSYSTEM, () => this.onRouteChange(SLUG_ECOSYSTEM));
        router.on(SLUG_STORE, () => this.onRouteChange(SLUG_STORE));
        router.on(SLUG_BLOG, () => this.onRouteChange(SLUG_BLOG));
        router.on(SLUG_BLOG_CATEGORY, () => this.onRouteChange(SLUG_BLOG_CATEGORY));
        router.on(SLUG_BLOG_SINGLE, () => this.onRouteChange(SLUG_BLOG_SINGLE));

        state.on(CURRENT_SECTION, async (sectionState: any) => {
            header.setActiveNavItem(sectionState.current.slug);
            await sectionState.current.animateIn();
            this.clicksDisabled = false;
        });
        router.listen();
    }

    xmlCheck(url: string) {
        if (url.search('.xml') > -1) {
            window.location.href = url;
        }
        return false;
    }

    async onRouteChange(slug: string) {
        const currentSection = state.getValue(CURRENT_SECTION);

        if (currentSection && slug == currentSection.slug && slug.search('/:') < 0) {
            return;
        }

        this.clicksDisabled = true;
        if (currentSection) {
            const url = window.location.pathname;
            header.setActiveNavItem(slug);
            
            this.xmlCheck(url);
            await currentSection.animateOut();
            await swapDOM(url, 'main');
            this.trackPageView(url);
        }

        let newSection;
        switch(slug) {
            case SLUG_HOME: newSection = new Home(); break;
            case SLUG_PROVIDE: newSection = new Provide(); break;
            case SLUG_BUILD: newSection = new Build(); break;
            case SLUG_ECOSYSTEM: newSection = new Ecosystem(); break;
            case SLUG_STORE: newSection = new Store(); break;
            case SLUG_BLOG: newSection = new Blog(); break;
            case SLUG_BLOG_CATEGORY: newSection = new Blog(); break;
            case SLUG_BLOG_SINGLE: newSection = new BlogSingle(); break;
        }

        state.setValue(CURRENT_SECTION, newSection);

        header.hideMobileNav();
        this.rewriteRelLinks();
    }

    rewriteRelLinks() {
        const anchorSelector = 'a:not([href^="http"])';
        const headerLinks = header.element.querySelectorAll(anchorSelector);
        rewriteRelLinks(headerLinks);
        const footerLinks = footer.element.querySelectorAll(anchorSelector);
        rewriteRelLinks(footerLinks);
    }

    trackPageView( url: string ) {
        window.ga('send', 'pageview', url);
    }

    set clicksDisabled(value: boolean) {
        this.clickBlocker.style.display = value ? 'block' : 'none';
    }
}

(window as any).App = new App();
