import {gsap} from "gsap";
import {emitter} from "../global/emitter";
import {EVENT_RESIZE, Window} from "../global/window";

export class Accordion {
    public element: HTMLElement;
    private mainItems: NodeListOf<HTMLElement>;
    private subItems: NodeListOf<HTMLElement>;
    private subItemsLi: NodeListOf<HTMLElement>;
    private contents: NodeListOf<HTMLElement>;
    private openItems: Array<number> = [];
    private content: boolean;
    private resizeTrigger: boolean;

    constructor(element: HTMLElement, openItems: Array<number> = [], content: boolean = false, resizeTrigger: boolean = true) {
        this.element = element;
        this.openItems = openItems;
        this.content = content;
        this.resizeTrigger = resizeTrigger;
        this.mainItems = this.element.querySelectorAll('.main-item');
        this.subItems = this.element.querySelectorAll('.sub-items');
        this.subItemsLi = this.element.querySelectorAll('.sub-items .list-item');
        this.init();
    }

    private init() {
        if (this.content)
            this.contents = this.element.querySelectorAll('.content .wrapper');

        this.mainItems.forEach((el) => {
            el.addEventListener('click',
                (e) => this.onMainItemClick(e));
        }, this);

        this.subItemsLi.forEach((el) => {
            if (el.querySelector('button')) {
                el.querySelector('button').addEventListener('click',
                    (e) => this.onSubItemClick(e));
            }
        }, this);

        // Open menu items
        if ( this.openItems.length) {
            for (const i of this.openItems) {
                [...this.mainItems][i].classList.add('open');
                this.subItems[i].style.height = 'auto';
            }

            // Show first item.
            if (this.content) {
                this.subItemsLi[0].classList.add('active');
                [...this.contents][this.openItems[0]].style.display = 'block';
            }
        }

        this.emitResize();
    }

    private emitResize() {
        if (!this.resizeTrigger) return;

        emitter.emit(EVENT_RESIZE, {
            width: Window.width,
            height: Window.height
        });
    }

    private onMainItemClick(e: any) {
        const el = e.target;

        if (el.classList.contains('open')) {
            el.classList.remove('open')
            this.closeSubItems(el);
        } else {
            el.classList.add('open')
            this.openSubItems(el);
        }
    }

    private onSubItemClick(e: any) {
        const target = e.target;
        let targetIndex = 0;

        for (let i = 0; i < this.subItemsLi.length; i++) {
            this.subItemsLi[i].classList.remove('active');
            if (this.subItemsLi[i] === target.parentElement) {
                targetIndex = i;
            }
        }
        this.subItemsLi[targetIndex].classList.add('active');

        // Animate in content.
        if (this.content) {
            this.contents.forEach((el, i) => {
                if (i === targetIndex) {
                    el.style.display = 'block';
                    gsap.fromTo(el,
                        {display: 'block', opacity: 0},
                        {opacity: 1, duration: .3, delay: .1})
                } else {
                    el.style.display = 'none';
                }
            });
        }

        this.emitResize();
    }

    private openSubItems(el: HTMLElement) {
        const wrapper = el.parentElement.querySelector('.sub-items') as HTMLElement;
        const subItems = wrapper.querySelectorAll('.list-item');
        let height = 0;

        subItems.forEach((el) => {
            height +=  el.getBoundingClientRect().height;
        });

        // Animate in topics.
        gsap.to(wrapper, {
            duration: .4,
            height: height,
            ease: 'Power4.easeOut',
            onUpdate: () => {
                this.emitResize();
            },
            onComplete: function() {
                this._targets[0].style.height = 'auto';
            }
        });
    }

    private closeSubItems(el: HTMLElement) {
        const wrapper = el.parentElement.querySelector('.sub-items') as HTMLElement;
        gsap.to(wrapper, {
            duration: .4,
            height: 0,
            ease: 'Power4.easeOut',
            onUpdate: () => {
                this.emitResize();
            }
        });
    }


    public destroy() {
        this.mainItems.forEach((el) => {
            el.removeEventListener('click',
                (e) => this.onMainItemClick(e));
        }, this);

        this.subItems.forEach((el) => {
            if (el.querySelector('button')) {
                el.querySelector('button').removeEventListener('click',
                    (e) => this.onSubItemClick(e));
            }
        }, this);
    }
}
