import * as THREE from 'three';
import {
    DoubleSide, Mesh,
    Object3D,
    ShaderMaterial, Texture
} from "three";
import FlowMapShader from './shaders/FlowMapShader';
import {ASSETS_3D, COLOR_BLUE_RGBA, COLOR_WHITE_RGBA} from "../global/constants";
import {gsap} from "gsap";
import {LinePathItem, PathItem, SplinePathItem} from "./pathItem";
import {LinePath3} from "./LinePath3";
import {LineTube, SplineTube, Tube} from "./tube";
import {contains, loadTexture} from "../global/utils";
import {state} from "../global/state";
import {AUDIO_DLT1, AUDIO_SL1, AUDIO_TA, audioController} from "../global/audioController";

export const PATH_TYPE_LINE = 'pathTypeLine';
export const PATH_TYPE_SPLINE = 'pathTypeSpline';

export default class Paths {
    public container: Object3D;
    public type: string;
    public tubes: Array<Tube> = [];
    public materials: Array<ShaderMaterial> = [];
    public pathFollowers: Array<PathItem[]> = [];
    public hashSoundDelay: number = 0;
    private curveMesh: Mesh;
    private allCoords: Array<any>;
    private hashRepeat: number;
    private shareMaterial: boolean;
    private curveMinAltitude: number;
    private curveMaxAltitude: number;
    private radius: number;
    private parent: Object3D;
    private hashSound: string;
    private solidSound: string;
    private completeSound: string;
    private outSound: string;

    constructor(allCoords: Array<any>,
                parent: Object3D,
                hashRepeat: number,
                radius: number,
                type: string = PATH_TYPE_SPLINE,
                shareMaterial: boolean = true,
                curveMinAltitude: number = undefined,
                curveMaxAltitude: number = undefined) {

        this.allCoords = allCoords;
        this.parent = parent;
        this.type = type;
        this.hashRepeat = hashRepeat;
        this.shareMaterial = shareMaterial;
        this.curveMinAltitude = curveMinAltitude;
        this.curveMaxAltitude = curveMaxAltitude;
        this.radius = radius;

        this.init();
    }

    async init() {
        let uniforms = {
            color: {type: 'v', value: new THREE.Vector4(...COLOR_BLUE_RGBA)},
            uvOffset: {type: 'v', value: new THREE.Vector2(0, 0)},
            //uvRepeat: {type: 'v', value: new THREE.Vector2(this.hashRepeat, 1)},
            uTubeLength: {type: 'f', value: 0},
            texture: {type: "t", value: new Texture()},
            textureColor: {type: 'v', value: new THREE.Vector4(...COLOR_WHITE_RGBA)},
            textureVisibleAmount: {type: "f", value: 1},
            textureAlphaAmount: {type: "f", value: 1},
            globalAlpha: {type: "f", value: 1},
            featherAmount: {type: "f", value: 0},
            reverseDirection: {type: "f", value: 0},
            offset: {type: "f", value: 0}
        };

        const shaderMaterial: ShaderMaterial = new THREE.ShaderMaterial({
            uniforms: uniforms,
            side: DoubleSide,
            transparent: true,
            vertexShader: FlowMapShader.vertex,
            fragmentShader: FlowMapShader.fragment,
            depthTest: true,
            depthWrite: true
        });

        if (this.shareMaterial) {
            this.materials.push(shaderMaterial);
        }

        this.curveMesh = new THREE.Mesh();
        this.curveMesh.visible = false;
        [...this.allCoords].forEach((coords, index) => {

            let tube: Tube;
            const material: ShaderMaterial = this.shareMaterial ? this.materials[0] : shaderMaterial.clone();
            if (!this.shareMaterial) {
                material.uniforms.uvOffset.value.x = Math.random();
                this.materials.push(material);
            }
            if (this.type == PATH_TYPE_SPLINE) {
                tube = new SplineTube(coords, material, this.radius, this.curveMinAltitude, this.curveMaxAltitude);
            } else {
                tube = new LineTube(coords, material, this.radius);
            }

            //todo move scale outside - adjust for city scale
            material.uniforms.uTubeLength.value = tube.path.getLength() * (this.type == PATH_TYPE_LINE ? 10 : 1);

            this.tubes.push(tube);
            this.curveMesh.add(tube.mesh);
        });

        this.materials.forEach((material) => {
            material.uniforms.textureVisibleAmount.value = 1;
            material.uniforms.offset.value = 0;
        });

        const hashTexture = state.getValue(ASSETS_3D).hash_texture_url;
        loadTexture(hashTexture).then(value => {
            value.wrapS = THREE.RepeatWrapping;
            this.setAllUniforms('texture', value);
        });

        this.container = new Object3D();
        this.container.add(this.curveMesh);
        this.parent.add(this.container);
    }

    setSounds(hashSound: string = null, solidSound: string = null, completeSound: string = null, outSound: string = null) {
        this.hashSound = hashSound;
        this.solidSound = solidSound;
        this.completeSound = completeSound;
        this.outSound = outSound;
    }

    animateHash(delay: number = 0, speed: number = 10) {
        this.materials.forEach((material) => {
            gsap.set(material.uniforms.uvOffset.value, {x: 0, delay: delay});
            gsap.to(material.uniforms.uvOffset.value, speed, {
                x: -1,
                ease: 'none',
                delay: delay,
                overwrite: true,
                onComplete: () => {
                    material.uniforms.uvOffset.value.x = 0;
                }
            });
        });
    }

    private stopAllHashes() {
        this.materials.forEach((material) => {
            this.stopHash(material)
        });
    }

    private stopHash(material: ShaderMaterial) {
        material.uniforms.uvOffset.value.x = 0;
        gsap.killTweensOf(material.uniforms.uvOffset.value);
    }

    animateIn(speed: number = 1, delay: number = 0, staggerDelay: number = 0, onComplete: any = () => {}) {
        if (this.hashSound) {
            audioController.play(this.hashSound, delay + this.hashSoundDelay);
        }
        this.materials.forEach((material, index) => {
            material.uniforms.offset.value = 0;
            gsap.to(material.uniforms.offset, speed, {
                value: 1,
                delay: delay + (staggerDelay * index),
                ease: 'Power1.easeInOut',
                onStart: () => {
                    this.animateHash();
                    this.curveMesh.visible = true;
                },
                onComplete: onComplete,
                onCompleteParams: [material]
            });
        });
    }

    animateInSolid(speed: number = 1, delay: number = 0, staggerDelay: number = 0, onComplete: any = () => {}) {
        if (this.solidSound && speed > 0) {
            audioController.play(this.solidSound, delay);
        }
        this.materials.forEach((material, index) => {
            material.uniforms.textureVisibleAmount.value = 1;
            gsap.to(material.uniforms.textureVisibleAmount, speed, {
                value: 0,
                delay: delay + (staggerDelay * index),
                ease: 'Power1.easeInOut',
                onStart: () => {
                    this.curveMesh.visible = true;
                },
                onComplete: () => {
                    if (index == 0) {
                        if (this.hashSound) {
                            audioController.fadeOut(this.hashSound);
                        }
                        if (this.completeSound) {
                            audioController.play(this.completeSound);
                        }
                    }
                    this.stopHash(material);
                    if (onComplete) {
                        onComplete(material, index);
                    }
                }
            });
        });
    }

    animateOut(speed: number = 1, delay: number = 0, staggerDelay: number = 0, onComplete: any = () => {}, except: Array<number> = []) {
        if (this.solidSound && speed > 0) {
            audioController.fadeOut(this.solidSound, delay);
        }
        if (this.outSound && speed > 0) {
            audioController.play(this.outSound, delay);
        }
        this.materials.forEach((material, index) => {
            if (!contains(except, index)) {
                gsap.to(material.uniforms.offset, speed, {
                    value: 0,
                    delay: delay + (staggerDelay * index),
                    ease: 'Power1.easeInOut',
                    onComplete: (material, index, onComplete) => {
                        if (this.outSound && index == 0) {
                            audioController.fadeOut(this.outSound);
                        }
                        onComplete(material, index);
                    },
                    onCompleteParams: [material, index, onComplete]
                });
            }
        });
    }

    reset() {
        this.stopAllHashes();
        this.curveMesh.visible = false;
        this.setAllUniforms('reverseDirection', 0);
        for (let i = 0; i < this.pathFollowers.length; i++) {
            const tube = this.pathFollowers[i];
            if (tube) {
                for (let j = 0; j < tube.length; j++) {
                    const follower = tube[j];
                    follower.reset();
                }
            }
        }
    }

    addPathFollower(object3d: Object3D, tubeIndex: number) {
        let pathItem: PathItem;
        const tube = this.tubes[tubeIndex];
        if (this.type == PATH_TYPE_SPLINE) {
            pathItem = new SplinePathItem(object3d, tube.path);
        }
        else {
            pathItem = new LinePathItem(object3d, tube.path as LinePath3);
        }
        tube.mesh.add(pathItem.mesh);

        if (!this.pathFollowers[tubeIndex]) {
            this.pathFollowers[tubeIndex] = [];
        }
        this.pathFollowers[tubeIndex].push(pathItem);
    }

    get material() {
        return this.materials[0];
    }

    setAllUniforms(prop: string, value: any) {
        this.materials.forEach((material, index) => {
            (material as any).uniforms[prop].value = value;
        });
    }

    getPathFollowers(tubeIndex: number) {
        return this.pathFollowers[tubeIndex];
    }

    getNulls(tubeIndex: number) {
        return this.tubes[tubeIndex].nulls;
    }
}