/*
The main timer for the 3d application.
 */

import {gsap} from "gsap";
import Emitter from "@hellomonday/emitter";

export const ACT_CHANGE_EVENT = 'actChangeEvent';

export class Timer extends Emitter {

    public currentChapterIndex: number;
    public currentActIndex: number;
    public duration: number = 0;
    public paused: boolean = true;
    private data: any;
    private gsapTimer: any;

    public setData(data: any) {
        this.data = data;
        this.duration = this.getActStartEndTime().end;
    }

    public start() {
        this.emit(ACT_CHANGE_EVENT, {chapter: this.currentChapterIndex, act: this.currentActIndex});
        this.startTimer();
    }

    public pause() {
        this.paused = true;
        if (this.gsapTimer) {
            this.gsapTimer.pause();
        }
    }

    public resume() {
        this.paused = false;
        if (this.gsapTimer) {
            this.gsapTimer.resume();
        } else {
            this.start();
        }
    }

    public reset() {
        if (this.gsapTimer) {
            this.gsapTimer.kill();
            this.gsapTimer = null;
        }
        this.currentChapterIndex = 1;
        this.currentActIndex = 1;
    }

    public get progress() {
        return this.getActStartEndTime(this.currentChapterIndex, this.currentActIndex).start;
    }

    public nextAct() {
        let act = this.currentActIndex + 1;
        let chapter = this.currentChapterIndex;
        if (act > this.data[chapter - 1].acts.length) {
            act = 1;
            chapter = chapter > this.data.length - 1 ? 1 : chapter + 1;
        }
        this.gotoAct(chapter, act);
    }

    public gotoAct(chapterIndex: number, actIndex: number = 0) {
        const lastChapterIndex = this.currentChapterIndex;
        this.currentChapterIndex = chapterIndex;
        this.currentActIndex = actIndex;
        this.emit(ACT_CHANGE_EVENT, {chapter: chapterIndex, act: actIndex, lastChapter: lastChapterIndex});
        this.startTimer();
    }

    public getChapterDuration(chapterIndex: number) {
        const chapterData = this.data[chapterIndex - 1];
        let duration = 0;
        for (let i = 0; i < chapterData.acts.length; i++) {
            duration += parseInt(chapterData.acts[i].duration);
        }
        return duration;
    }

    private getActStartEndTime(chapterIndex: number = this.data.length, chapterActIndex: number = null) {
        let duration = 0;
        let act;
        let actDuration;

        for (let i = 0; i < chapterIndex; i++) {
            let actIndex = chapterActIndex && i == chapterIndex - 1 ? chapterActIndex : this.data[i].acts.length;
            for (let j = 0; j < actIndex; j++) {
                act = this.data[i].acts[j];
                actDuration = parseInt(act.duration);
                duration += actDuration;
            }
        }
        return {start: duration - actDuration, end: duration};
    }

    private startTimer() {
        if (this.gsapTimer) {
            this.gsapTimer.kill();
        }
        const delay = this.data[this.currentChapterIndex - 1].acts[this.currentActIndex - 1].duration;
        this.gsapTimer = gsap.delayedCall(delay, this.nextAct.bind(this));
    }
}


