/*
    Creates and textures the globe and exposes globe-specific animations
 */

import * as THREE from 'three';
import {gsap} from "gsap";
import {
    AdditiveBlending, BackSide, DoubleSide, Euler, FrontSide,
    Mesh, MeshBasicMaterial, MeshPhongMaterial, MeshStandardMaterial,
    Object3D, PerspectiveCamera, Sprite, SpriteMaterial, Texture, TextureLoader, Vector2, Vector3
} from "three";
import {ASSETS_3D, GLOBE_RADIUS} from "../global/constants";
import {clamp, loadTexture} from "../global/utils";
import {GLTFLoader} from "three/examples/jsm/loaders/GLTFLoader";
import {hero3d} from "./hero3d";
import {state} from "../global/state";

export const DARK_SPHERE_COLOR: number = 0xaaaaaa;
export const LIGHT_SPHERE_COLOR: number = 0xffffff;
export const DARK_ATMOSPHERE_OPACITY: number = .8;
export const LIGHT_ATMOSPHERE_OPACITY: number = .3;
export const BACK_GLOW_SCALE:number = 420;
export const GLOBE_EASE_AMOUNT:number = 15;

export class Globe {

    public sphereMaterial: MeshStandardMaterial;
    public backGlowDarkSprite: Sprite;
    public loaded: Boolean;
    public container: Object3D;
    public globeContainer: Object3D;
    public atmosphere: Mesh;
    public easeAmount: number = 1;

    private sphereMesh: Mesh;
    private globeTexture: Texture;
    private bumpMapTexture: Texture;
    private backGlowDarkTexture: Texture;
    private camera: PerspectiveCamera;
    private rootMesh: Object3D;
    private dragStartPoint: Vector2;
    private currentGlobeRotation: Euler = new Euler(0,0, 0);
    private targetGlobeRotation: Euler = new Euler(0,0,0);
    private lastDragDistance: Vector2 = new Vector2(0,0);
    private introIdleSpeed: number = 0;

    init(camera: PerspectiveCamera, rootMesh: Object3D) {
        this.camera = camera;
        this.rootMesh = rootMesh;
        this.globeContainer = new Object3D();
        this.container = new Object3D();
        this.container.add(this.globeContainer);
    }

    async load() {
        if (this.loaded) {
            return;
        }
        const assets3d = state.getValue(ASSETS_3D);
        this.globeTexture = await loadTexture(assets3d.globe_texture_url) as Texture;
        this.bumpMapTexture = await loadTexture(assets3d.globe_texture_bump_url) as Texture;
        this.backGlowDarkTexture = await loadTexture(assets3d.globe_texture_back_glow_url) as Texture;
        await this.loadAtmosphere();
        this.setupGlobeLayers();
        this.loaded = true;
    }

    private loadAtmosphere() {
        const assets3d = state.getValue(ASSETS_3D);
        return new Promise<Texture>((resolve, reject) => {
            const gltf_loader = new GLTFLoader();
            gltf_loader.load(
                assets3d.globe_model_atmosphere_url,
                (gltf: any) => {
                    const scale = 122.5;

                    this.atmosphere = gltf.scene.getObjectByName('atmosphere').clone(true);
                    this.atmosphere.renderOrder = 2;
                    this.atmosphere.scale.set(scale, scale, scale);
                    const srcMaterialAtmos: MeshStandardMaterial = this.atmosphere.material as MeshStandardMaterial;
                    const material = new MeshBasicMaterial(
                        {
                            transparent: true,
                            opacity: .5,
                            depthTest: false,
                            side: FrontSide,
                            blending: AdditiveBlending,
                            map: srcMaterialAtmos.map
                        }
                    );
                    this.atmosphere.material = material;
                    this.container.add(this.atmosphere);

                    resolve();
                }
            );
        });
    }

    private setupGlobeLayers() {
        // Back Glow Texture Dark
        const backGlowDarkTexture = new SpriteMaterial( {
            map: this.backGlowDarkTexture,
            depthWrite: false,
            opacity: 0
        });
        this.backGlowDarkSprite = new THREE.Sprite( backGlowDarkTexture );
        this.backGlowDarkSprite.scale.set(BACK_GLOW_SCALE, BACK_GLOW_SCALE, 1 );
        this.container.add(this.backGlowDarkSprite);

        // Sphere
        const geometry = new THREE.SphereGeometry(GLOBE_RADIUS, 80, 80);
        this.sphereMaterial = new MeshStandardMaterial({
            map: this.globeTexture,
            color: LIGHT_SPHERE_COLOR,
            bumpMap: this.bumpMapTexture,
            bumpScale: .65
        });
        this.sphereMesh = new THREE.Mesh(geometry, this.sphereMaterial);
        this.globeContainer.add(this.sphereMesh);
    }

    public render() {
        if (this.loaded) {
            const delta = 1150 - this.camera.position.z;
            const back_glow_scale = BACK_GLOW_SCALE + (delta * (delta / 500)) / 25;
            this.backGlowDarkSprite.scale.x = this.backGlowDarkSprite.scale.y = back_glow_scale;
            this.atmosphere.lookAt(this.camera.position);
            this.targetGlobeRotation.y -= this.introIdleSpeed;
            this.currentGlobeRotation.x += (this.targetGlobeRotation.x - this.currentGlobeRotation.x) / this.easeAmount;
            this.currentGlobeRotation.y += (this.targetGlobeRotation.y - this.currentGlobeRotation.y) / this.easeAmount;
            this.currentGlobeRotation.z += (this.targetGlobeRotation.z - this.currentGlobeRotation.z) / this.easeAmount;
            this.globeContainer.rotation.copy(this.currentGlobeRotation);
        }
    }

    public get rotation() : Euler {
        return this.targetGlobeRotation;
    }

    public animateInBackGlowDark(speed: number = 1, delay: number = 0, ease: string = 'Power1.easeInOut') {
        gsap.to(this.backGlowDarkSprite.material, speed, {opacity: 1, delay: delay, ease: ease, onStart: () => {}});
    }

    public animateOutBackGlowDark(speed: number = 1, delay: number = 0, ease: string = 'Power1.easeInOut') {
        gsap.to(this.backGlowDarkSprite.material, speed, {opacity: 0, delay: delay, ease: ease, onComplete: () => {}});
    }

    public add() {
        if (!this.container.parent) {
            this.rootMesh.add(this.container);
        }
    }

    public remove() {
        if (this.container.parent) {
            this.rootMesh.remove(this.container);
        }
    }

    public startDrag(x: number, y: number) {
        this.dragStartPoint = new Vector2(x, y);
    }

    public drag(x: number, y: number) {
        const dragDistance = new Vector2(this.dragStartPoint.x - x, this.dragStartPoint.y - y);
        const dragDelta = this.lastDragDistance.sub(dragDistance);
        this.targetGlobeRotation.y += dragDelta.x / 1000;
        this.targetGlobeRotation.x = clamp(this.targetGlobeRotation.x, -.25, .4);
        this.targetGlobeRotation.x += dragDelta.y / 1000;
        this.lastDragDistance.copy(dragDistance);
    }

    public stopDrag(x: number, y: number) {
        this.lastDragDistance = new Vector2(0,0);
    }

    public normalizeRotation() {
        this.targetGlobeRotation.y = this.targetGlobeRotation.y % (Math.PI * 2);
        this.currentGlobeRotation.y = this.currentGlobeRotation.y % (Math.PI * 2);
    }

    public resetRotation(rotation: Vector3) {
        hero3d.globe.rotation.set(rotation.x, rotation.y, rotation.z);
        this.currentGlobeRotation.set(rotation.x, rotation.y, rotation.z);
    }
}
